import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

const ServerAlert = (props) => {

    // this message will be displayed based on server response for the whole app 
    if(props.alert.path) {
        // in case the item created, and give the user show item button
        return (
            <div className={`alert alert__${props.alert.code} alert`}>
                { props.alert.message 
                ? <p className={`alert__${props.alert.code}-message`} > <Link className="display-item-btn" to={props.alert.path}></Link> {props.alert.message} </p> 
                : null }
            </div>
        )
    } else {
        return (
            <div className={`alert alert__${props.alert.code} alert`}>
                { props.alert.message 
                ? <p className={`alert__${props.alert.code}-message`} > {props.alert.message} </p> 
                : null }
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        alert: state.alert
    }
}

export default connect(mapStateToProps)(ServerAlert);
