// @ts-nocheck
import { fetchCurrentLabItemsAction } from 'actions/pagesActions/SchoolMember/LaboratoryManager/LaboratoryItemsAction';
import alertActions from 'actions/utilsActions/alertActions';
import { clearTableListAction } from 'actions/utilsActions/clearTableListAction';
import CheckBox from 'components/Form/CheckBox';
import CustomDatePicker from 'components/Form/CustomDatePicker';
import CustomDropdown from 'components/Form/CustomDropdown';
import DropMenu from 'components/Form/DropMenu';
import InputField from 'components/Form/InputField';
import ListLayout from 'components/List/ListLayout';
import PaginatedList from 'components/List/PaginatedList';
import SearchFilter from 'components/List/SearchFilter';
import Alert from 'components/Utils/Alert';
import parseDateToString, { onMinDateChange, parseDateForMoment } from 'helpers/parseDate';
import React, { Component } from 'react'
import { connect } from 'react-redux';
import moment from 'moment';
import ExtraInfo from 'components/Utils/ExtraInfo';

const ITEMS_COLUMNS_HEADERS = [
    {
        label: '',
        key: 'id'
    },
    {
        label: ' ',
        key: 'name'
    },
    {
        label: ' ',
        key: 'addedDate'
    },
    {
        label: '  ',
        key: 'expireDate'
    },
    {
        label: '',
        key: 'path'
    }
];

class CurrentItemsList extends Component {

    constructor(props) {
        super(props);
        this.itemsRef = React.createRef();

        this.state = {
            page: 1,
            id: '',
            itemId: 'all',
            fromAddedDate: '',
            toAddedDate: '',
            fromExpireDate: '',
            toExpireDate: '',
            valid: false,
            willExpire: false,
            expired: false
        }

        this.baseState = { ...this.state };
    }

    componentDidMount() {
        if(this.itemsRef.current) {
            // set the labs dropmenu with ALL option at mount
            this.itemsRef.current.selectItem({label: ' ', value: 'all'})
        }
    }

    onFiltersChange = (value, fieldName, toDateFieldName='') => {
        switch (fieldName) {
            case 'fromAddedDate':
            case 'fromExpireDate':
                if(this.state[toDateFieldName] && value.getTime() >= this.state[toDateFieldName].getTime()) {
                    // reset the 'to' date becasue it maybe become before the 'from' date
                    this.setState({ [fieldName]: value, [toDateFieldName]: '' });
                    return;
                }
                this.setState({ [fieldName]: value });
                break;
            default:
                this.setState({ [fieldName]: value })
                break;
        }
    }

    onPageChange = (currentPage) => {
        // clear alerts, becasue when item deleted, it will be send here
        this.props.clearAlert();

        // set the currentPage, and make API call after that
        this.setState({ page: currentPage.selected + 1 }, () => {
            // This will make the call when the component mount first
            this.props.fetchCurrentLabItemsAction(
                this.props.match.params.labId, 
                this.convertParamsDateToString()
            );
        });
    }

    onFilterSubmit = (e) => {
        // after submit, set the filter submit params, and after that make API request
        e.preventDefault();
        this.setState({ page: 1 }, () => {
            this.props.fetchCurrentLabItemsAction(
                this.props.match.params.labId, 
                this.convertParamsDateToString()
            );
        })
    }

    onFilterReset = () => {
        this.setState({ ...this.baseState }, () => {
            if(this.itemsRef.current) {
                // set the labs dropmenu with ALL option at mount
                this.itemsRef.current.selectItem({label: ' ', value: 'all'})
            }
        });
    }

    convertParamsDateToString = () => {
        const newParams = { ...this.state };
        // convert the date format for the API server
        newParams.fromAddedDate = parseDateToString(newParams.fromAddedDate);
        newParams.toAddedDate = parseDateToString(newParams.toAddedDate);
        newParams.fromExpireDate = parseDateToString(newParams.fromExpireDate);
        newParams.toExpireDate = parseDateToString(newParams.toExpireDate);
        return newParams;
    }

    convertStringToDate = (items=[]) => {
        // Convert string to date, to display it in dd/MM/yyyy format without time
        return items.map(item => {
            // clone the item, we don't want it to change in the reducer 
            const newItem = {...item}
            newItem.addedDate = parseDateToString(new Date(item.addedDate)).replace(/-/g,"/");
            newItem.expireDate = item.expireDate ? parseDateToString(new Date(item.expireDate)).replace(/-/g,"/") : ' ';
            newItem.highlightClassName = this.getItemExpireStatus(item.expireDate);
            return newItem;
        });
    }

    getItemExpireStatus = (itemExpireDate) => {
        // for warning or error highlighted rows, based on the expire date
        const m = moment(parseDateForMoment(moment()._d), 'YYYY-MM-DD');
        m.startOf('day');
        if(itemExpireDate) {
            itemExpireDate = new Date(itemExpireDate)
            const diffrenceDays = moment(parseDateForMoment(itemExpireDate), 'YYYY-MM-DD').diff(m, 'day');
            if(diffrenceDays > 0 && diffrenceDays < 8) {
                return 'highlight-warning';
            } else if(diffrenceDays <= 0) {
                return 'highlight-error';
            }
        }
        return '';
    }

    render() {
        return (
            <div className={`margin-top-huge no-padding-top ${this.props.expireMessage && 'table-with-warning-margin-top'}`}>
                <Alert messages={[this.props.expireMessage]} type={300} />
                <ListLayout>
                    <PaginatedList
                        totalPages={this.props.totalPages}
                        data={ this.convertStringToDate(this.props.items) }
                        onPageChange={this.onPageChange}
                        colHeaders = {ITEMS_COLUMNS_HEADERS}
                        initialPage={this.state.page - 1}
                    >
                        <ExtraInfo 
                            boolCondition={this.props.expireMessage}
                            errorMessage={'   ,     '}
                            warningMessage={'   ,      '}
                        />
                    </PaginatedList>
                    <SearchFilter onSubmit={this.onFilterSubmit}>
                        <div className="from-group">
                            <InputField
                                id="id"
                                name="id"
                                initial={true}
                                initialValue={this.state.id}
                                onChangeHandler={(e) => this.onFiltersChange(e.target.value, 'id')}
                                colSize="1"
                                placeholder="  "
                                icon="fas fa-search"
                            />
                        </div>
                        <div className='from-group'>
                            <CustomDropdown 
                                title="  "
                                colSize="1"
                                setRef={(ref) => this.itemsRef = ref}
                                list={[{label: ' ', value: 'all'}, ...this.props.ministryItems]}
                                onChange={(selectedItem) => this.onFiltersChange(selectedItem, 'itemId')}
                            />
                        </div>
                        <div className="form-group">
                            <DropMenu 
                                colSize="1"
                                title=" "
                            >
                                <CustomDatePicker 
                                    colSize="1"
                                    placeholder=" "
                                    onChangeHandler={(newDate) => this.onFiltersChange(newDate, 'fromAddedDate', 'toAddedDate') }
                                    initial={true}
                                    initialDate={this.state.fromAddedDate}
                                />
                                <CustomDatePicker 
                                    colSize="1"
                                    placeholder=" "
                                    onChangeHandler={(newDate) => this.onFiltersChange(newDate, 'toAddedDate') }
                                    initial={true}
                                    initialDate={this.state.toAddedDate}
                                />
                            </DropMenu>
                        </div>
                        <div className="form-group">
                            <DropMenu 
                                colSize="1"
                                title="  "
                            >
                                <CustomDatePicker 
                                    colSize="1"
                                    placeholder=" "
                                    onChangeHandler={(newDate) => this.onFiltersChange(newDate, 'fromExpireDate', 'toExpireDate') }
                                    initial={true}
                                    initialDate={this.state.fromExpireDate}
                                />
                                <CustomDatePicker 
                                    colSize="1"
                                    placeholder=" "
                                    onChangeHandler={(newDate) => this.onFiltersChange(newDate, 'toExpireDate') }
                                    initial={true}
                                    initialDate={this.state.toExpireDate}
                                    minDate={onMinDateChange(this.state.fromExpireDate)}
                                />
                            </DropMenu>
                        </div>
                        <div className="form-group">
                            <DropMenu 
                                colSize="1"
                                title=" "
                            >   
                                <div className="margin-top">
                                    <CheckBox 
                                        id="valid"
                                        name="valid"
                                        label=""
                                        initial={true}
                                        initialValue={this.state.valid}
                                        onChange={(isChecked) => {this.onFiltersChange(isChecked, 'valid')}}
                                    />
                                </div>
                                <div className="margin-top">
                                    <CheckBox 
                                        id="willExpire"
                                        name="willExpire"
                                        label=" "
                                        initial={true}
                                        initialValue={this.state.willExpire}
                                        onChange={(isChecked) => {this.onFiltersChange(isChecked, 'willExpire')}}
                                    />
                                </div>
                                <div className="margin-top">
                                    <CheckBox 
                                        id="expired"
                                        name="expired"
                                        label=" "
                                        initial={true}
                                        initialValue={this.state.expired}
                                        onChange={(isChecked) => {this.onFiltersChange(isChecked, 'expired')}}
                                    />
                                </div>
                            </DropMenu>
                        </div>
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value="" />
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onFilterReset} />
                        </div>
                    </SearchFilter>
                </ListLayout>
            </div>
        )
    }
    componentWillUnmount() {
        // clear the table list in redux store when unmount
        this.props.clearTableListAction();
    }
}

const mapStateToProps = state => {
    return {
        items: state.tableList.laboratoryItems,
        totalPages: state.tableList.totalPages,
        ministryItems: Object.values(state.items),
        expireMessage: state.tableList.expireMessage
    }
}

export default connect(mapStateToProps, { 
    fetchCurrentLabItemsAction,
    clearTableListAction, 
    clearAlert: alertActions.clear 
})(CurrentItemsList);