import React from 'react';
import { connect } from 'react-redux';
import { Router, Route, Switch } from 'react-router-dom';

import Login from '../pages/Public/Login';
import Register from '../pages/Public/Register';
import history from '../history';
import alertActions from '../actions/utilsActions/alertActions';
import PrivateRoute from '../routes/PrivateRoute';
import IndexPage from '../pages/IndexPage';
import NotFound from './Utils/NotFound';
import loadingActions from '../actions/utilsActions/loadingActions';
import { clearItemViewAction } from '../actions/utilsActions/clearItemViewAction';
import ForgetPass from 'pages/Public/ForgetPass';
import ResetPass from 'pages/Public/ResetPass';


class App extends React.Component {


    constructor(props) {
        super(props);

        history.listen((location, action) => {
            // Listen for address bar change, and execute below functions
            this.props.clearAlert();
            this.props.removeLoading();
            this.props.clearItemViewAction();
        });

        // users console WARNING message
        console.log(
            "%c!", 
            "color: red; font-family: sans-serif; font-size: 4.5em; font-weight: bolder; text-shadow: #000 1px 1px;");
        console.log(
            '%c     .                 ""                 .', 
            'font-size: 1rem; font-weight: 700;');
    }

    render() {

        return (
            <div>
                <Router history={history}>
                    <Switch>
                        <Route path="/login" exact component={Login}/>
                        <Route path="/register" exact component={Register}/>
                        <Route path="/forget-pass" exact component={ForgetPass}/>
                        <Route path="/reset-pass/:token" exact component={ResetPass}/>

                        <PrivateRoute path="/" exact component={IndexPage} />
                        <PrivateRoute path="/profile" exact component={IndexPage} />
                        <PrivateRoute path="/defect/create" exact component={IndexPage} />

                        { /* Technical Support Routes */ }
                        <PrivateRoute path="/defect" exact component={IndexPage} />
                        <PrivateRoute path="/defect/:id" exact component={IndexPage} />

                        { /* System Administrator Routes */ }

                        <PrivateRoute path="/semester" exact component={IndexPage} />
                        <PrivateRoute path="/semester/create" exact component={IndexPage} />
                        <PrivateRoute path="/semester/:id" exact component={IndexPage} />
                        <PrivateRoute path="/item" exact component={IndexPage} />
                        <PrivateRoute path="/item/create" exact component={IndexPage} />
                        <PrivateRoute path="/item/:id" exact component={IndexPage} />
                        <PrivateRoute path="/course" exact component={IndexPage} />
                        <PrivateRoute path="/course/create" exact component={IndexPage} />
                        <PrivateRoute path="/course/:id" exact component={IndexPage} />
                        <PrivateRoute path="/experiment" exact component={IndexPage} />
                        <PrivateRoute path="/experiment/create" exact component={IndexPage} />
                        <PrivateRoute path="/experiment/:id" exact component={IndexPage} />
                        <PrivateRoute path="/city" exact component={IndexPage} />
                        <PrivateRoute path="/city/create" exact component={IndexPage} />
                        <PrivateRoute path="/city/:id" exact component={IndexPage} />
                        <PrivateRoute path="/educational-supervisor/create" exact component={IndexPage} />
                        <PrivateRoute path="/educational-supervisor" exact component={IndexPage} />
                        <PrivateRoute path="/educational-supervisor/:id" exact component={IndexPage} />

                        <PrivateRoute path="/question" exact component={IndexPage} />
                        <PrivateRoute path="/question/add" exact component={IndexPage} />
                        <PrivateRoute path="/question/:id" exact component={IndexPage} />

                        { /* School Manager Routes */ }
                        
                        <PrivateRoute path="/school" exact component={IndexPage} />
                        <PrivateRoute path="/school/create" exact component={IndexPage} />

                        <PrivateRoute path="/dashboard/laboratory" exact component={IndexPage} />
                        <PrivateRoute path="/dashboard/laboratory-manager" exact component={IndexPage} />


                        <PrivateRoute path="/members/create" exact component={IndexPage} />
                        <PrivateRoute path="/science-teacher/:id" exact component={IndexPage} />
                        <PrivateRoute path="/laboratory-manager/:id" exact component={IndexPage} />

                        <PrivateRoute path="/laboratory/create" exact component={IndexPage} />
                        <PrivateRoute path="/laboratory/:id" exact component={IndexPage} />

                        { /* School Member Routes */ }
                        <PrivateRoute path="/invites" exact component={IndexPage} />

                        { /* Laboratory Manager Routes */ }
                        <PrivateRoute path="/school-laboratory" exact component={IndexPage} />

                        <PrivateRoute path="/school-laboratory/:labId/item/:itemId" exact component={IndexPage} />

                        <PrivateRoute path="/practical-request" exact component={IndexPage} />
                        <PrivateRoute path="/practical-request/:id" exact component={IndexPage} />

                                    { /* Laboratory Manager Lab Tabs View */ }
                        <PrivateRoute path="/school-laboratory/:labId" exact component={IndexPage} />
                        <PrivateRoute path="/school-laboratory/:labId/current-items" exact component={IndexPage} />
                        <PrivateRoute path="/school-laboratory/:labId/removed-items" exact component={IndexPage} />


                        {/* Science Teacher Routes */}
                        <PrivateRoute path="/practical-experiment/create" exact component={IndexPage} />
                        <PrivateRoute path="/practical-experiment/:id" exact component={IndexPage} />
                        <PrivateRoute path="/practical-experiment/:id/evaluate" exact component={IndexPage} />
                        <PrivateRoute path="/practical-experiment" exact component={IndexPage} />


                        { /* Educational Supervisor Routes */ }
                        <PrivateRoute path="/visit/create" exact component={IndexPage} />
                        <PrivateRoute path="/visit/current" exact component={IndexPage} />

                        <PrivateRoute path="/visit/current/laboratory/:id" exact component={IndexPage} />
                        <PrivateRoute path="/visit/current/laboratory/:id/dashboard" exact component={IndexPage} />
                        <PrivateRoute path="/visit/current/laboratory/:id/evaluate" exact component={IndexPage} />

                        <PrivateRoute path="/visit/current/laboratory-manager/:id" exact component={IndexPage} />
                        <PrivateRoute path="/visit/current/laboratory-manager/:id/dashboard" exact component={IndexPage} />
                        <PrivateRoute path="/visit/current/laboratory-manager/:id/evaluate" exact component={IndexPage} />

                        <Route path="*" render={ (props) => (
                            <NotFound
                                {...props}
                                className='resource-not-found-full'
                                statusCode='404'
                                message=',    '
                                btnLabel="  "
                                referer="/"
                            />
                            )}
                        />
                    </Switch>
                </Router>
            </div>
        )
    }
}


export default connect(null, 
    { 
        clearAlert: alertActions.clear, 
        removeLoading: loadingActions.remove,
        clearItemViewAction,
    })(App);
