import mylabs from '../../../apis/mylabs';
import catchHandler from '../../../helpers/catchHandler';
import loadingActions from '../../utilsActions/loadingActions';
import alertActions from '../../utilsActions/alertActions';
import history from '../../../history';
import { renewJwtAction } from '../../utilsActions/jwtAction';

export const fetchCreateSchoolRequirementsAction = () => {
    return async (dispatch) => {
        try {

            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());

            // fetch the requirments of creating school from the API server 
            const response = await mylabs.get('/city/create-requirements');
            
            // Send to Reducers
            dispatch( { type: 'FETCH_FORM_CITIES', payload: response.data.cities });
            
            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch(err) {
            catchHandler(dispatch, err);
        }
    }
}

export const createSchoolAction = (school) => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to patch
            const response = await mylabs.post('/school', { ...school })

            // send the created school object to the reducers 
            dispatch({ type: 'CREATE_SCHOOL', payload: response.data.school });

            // remove the loading indicator
            dispatch(loadingActions.remove());

            // Renew the Token here first
            dispatch(renewJwtAction(response.data.token));
            
            // navigate the user to the schoolView page, 
            history.push('/school');

            // message to the user that the school is created
            dispatch(alertActions.success({
                message: "   ",
                code: 200,
                success: true
            }));

        } catch (err) {
            catchHandler(dispatch, err);
        }

    }
}

export const fetchSchoolAction = () => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to get school info
            const response = await mylabs.get('/school')

            // send the response to the reducers
            dispatch({ type: 'FETCH_SCHOOL', payload: response.data.school });

            // send to form city reducer
            dispatch({ type: 'FETCH_FORM_CITIES', payload: response.data.cities });

            // send all laboratory managers to the reducer 
            dispatch({ type: 'FETCH_LABORATORY_MANAGERS', payload: response.data.laboratoryManagers })

            // send all science teacher to the reducer
            dispatch({ type: 'FETCH_SCIENCE_TEACHERS', payload: response.data.scienceTeachers })

            // send all laboratories to reducer
            dispatch({ type: 'FETCH_LABORATORIES', payload: response.data.laboratories });

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}

export const updateSchoolAction = (updatedSchool) => {
    // PATCH school
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make PATCH request to the API server
            const response = await mylabs.patch(`/school`, { ...updatedSchool });

            // send updated school to school reducer 
            dispatch({ type: 'PATCH_SCHOOL', payload: response.data.school });

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true
            }));

            // the response of PATCH school should return the new updated
            // DISPATCH HERE PLEASE!!

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}

