import { userConstants } from '../actions/types';

const itemViewReducer = (state={}, action) => {
    switch (action.type) {
        case 'CLEAR_ITEM_VIEW':
            return {};
        case 'FETCH_ITEM':
            return {...action.payload};
        case 'FETCH_COURSE':
            return {...action.payload};
        case 'FETCH_SEMESTER':
            // converting the start and end date into dates first
            return {
                ...action.payload,
                startDate: new Date(action.payload['startDate']),
                endDate: new Date(action.payload['endDate'])
            };
        case 'FETCH_EXPERIMENT':
            return {...action.payload};
        case 'FETCH_CITY':
            return {...action.payload};
        case userConstants.LOGOUT:
            return {};
        default: 
            return state;
    }
}

export default itemViewReducer;