import _ from 'lodash';
import { userConstants } from '../../actions/types';


const scienceTeachersReducer = (state={}, action) => {
    
    switch (action.type) {
        case 'FETCH_SCIENCE_TEACHERS':
            return _.mapKeys(action.payload, 'id');
        case 'UPDATE_SCIENCE_TEACHER':
            return { ...state, [action.payload.id]: action.payload }
        case 'FETCH_SCIENCE_TEACHER':
            return { ...state, [action.payload.id]: action.payload }
        case userConstants.LOGOUT:
            return {};
        default:
            return state;
    }

}

export default scienceTeachersReducer;