// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import Form from '../../../components/Form/Form'
import FormHeader from '../../../components/Form/FormHeader'
import InputField from '../../../components/Form/InputField'
import NotFoundItem from '../../../components/Utils/NotFoundItem';
import ContentPadding from '../../../components/Utils/ContentPadding'
import { validateEqualPropsObjects } from '../../../helpers/validate'
import alertActions from '../../../actions/utilsActions/alertActions';
import ContentDivider from '../../../components/Utils/ContentDivider';
import CustomMultipleDropdown from '../../../components/Form/CustomMultipleDropdown';
import { getItemsDifferenceStatus } from '../../../helpers/objectsHelpers';
import NotAllowed from '../../../components/Utils/NotAllowed';
import { fetchLabAction, updateLabManagersOnLab } from '../../../actions/pagesActions/SchoolManager/laboratoryAction';

class LabView extends Component {

    state = {
        id: '',
        labManagers: []
    }

    async componentDidMount () {
        // connecting to the dropdown menu to reach clearSelection functionality
        this.dropdownRef = React.createRef();
        try {
            // check if lab exist in local redux store
            if(this.props.lab && this.props.hasSchool) {
                this.settingState();
            } else if(this.props.hasSchool){
                // if this particular lab not exist in local redux store, call API to fetch it
                
                await this.props.fetchLabAction(this.props.match.params.id);
                this.settingState();
            }
        } catch (e){ }
    }

    settingState = () => {
        const { lab } = this.props;
        if(lab) {
            // setting the state of science teacher from redux store
            this.setState({
                id: lab.id,
                labManagers: lab.labManagers,
            }, () => {
                if(this.props.labManagers.length !== 0) {
                    // avoid selecting default labs when the list actually empty
                    this.dropdownRef.current.deselectAll();
                    this.dropdownRef.current.selectMultipleItems(this.state.labManagers);
                }
            });
        }
    }


    onLabManagersChangeHandler = (selectedLabManagers) => {
        this.setState({ labManagers: [ ...selectedLabManagers ] });
    }

    onFormSubmit = async (e) => {
        e.preventDefault();
        const { lab } = this.props;

        if(lab) {
            const { labManagers } = this.state;
            const originalLabManagers = lab.labManagers;
            // check if the user update the data, else don't overfetch
            if(!validateEqualPropsObjects(labManagers, originalLabManagers)) {
                // update action
                await this.props.updateLabManagersOnLab({
                    labManagers: getItemsDifferenceStatus(originalLabManagers, labManagers),
                    labId: this.state.id
                })
                // set last update after success submit
                this.settingState();
            }
        }
    }

    onResetHandler = () => {
        // remove alerts
        this.props.clearAlert();
        // reset form values
        this.settingState();
    }

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.hasSchool}
                referer="/"
                message="    "
                statusCode="403"
                btnLabel="  "
            >
                <NotFoundItem 
                    referer="/school"
                >
                    <Form onSubmit={this.onFormSubmit}>
                        <FormHeader 
                            label=" " 
                            referer="/school"
                            refererLabel=""
                            icon="fas fa-chevron-left"
                        />
                        <ContentPadding>
                            <div className="form-group">
                                <InputField
                                    id="id"
                                    name="id"
                                    type="text"
                                    initial={true}
                                    initialValue={this.state.id }
                                    label=" "
                                    locked={true}
                                    icon="fas fa-lock"
                                    colSize="2"
                                />
                                <CustomMultipleDropdown 
                                    colSize="2"
                                    label="   "
                                    setRef={(ref) => this.dropdownRef = ref}
                                    onChange={this.onLabManagersChangeHandler}
                                    list={this.props.labManagers}
                                    title='  '
                                    selectedTitle=" "
                                />
                            </div>
                        </ContentPadding>
                        <ContentDivider />
                        <ContentPadding>
                            <div className="form-group confirm-group margin-top-big">
                                <input className="btn submit-btn" type="submit" value=""/>
                                <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                            </div>
                        </ContentPadding>
                    </Form>
                </NotFoundItem>
            </NotAllowed>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
        labManagers: Object.values(state.laboratoryManagers)
                        .map(labManager => { 
                            return { label: labManager.name, value: labManager.id }
                        }),
        lab: Object.values(state.laboratories)
                .filter(lab => lab.id.toString() === ownProps.match.params.id)[0],
    }
}

export default connect(mapStateToProps, { 
    fetchLabAction,
    updateLabManagersOnLab,
    clearAlert: alertActions.clear
})(LabView);
