// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';
import _ from 'lodash';

import { Form } from '../../../components/Form/Form';
import FormHeader from '../../../components/Form/FormHeader';
import InputField from '../../../components/Form/InputField';
import Alert from '../../../components/Utils/Alert';
import ContentPadding from '../../../components/Utils/ContentPadding';
import { validateArrayOfEmptyObjects, validateEmpty, validateEqualPropsObjects } from '../../../helpers/validate';
import alertActions from '../../../actions/utilsActions/alertActions';
import ContentDivider from '../../../components/Utils/ContentDivider';
import { deleteCityAction, fetchCityAction, updateCityAction } from '../../../actions/pagesActions/SystemAdministrator/cityAction';
import { getItemsDifferenceStatusWithUpdate } from '../../../helpers/objectsHelpers';
import NotFoundItem from '../../../components/Utils/NotFoundItem';


class CityView extends Component {

    async componentDidMount () {
        // API action call, First check if the tableList contain the item, or bring it from server
        try {
            await this.props.fetchCityAction(this.props.match.params.id);
            // in case the item exist in the API server
            this.settingState();
        } catch (e){ 
            // in case the item does not exist
        }
    }

    state = {
        id: '',
        name: '',
        areas: [],
        errorMessages: []
    }

    settingState = () => {
        if(this.props.city) {
            this.setState({ 
                ...this.props.city,
                // Clone deep, because the array contain objects
                areas: _.cloneDeep(this.props.city.areas),
                errorMessages: []
            });
        }
    }

    validateUpdatedCity = () => {
        return [
            validateEmpty(this.state.name, '      '),
            validateArrayOfEmptyObjects(this.state.areas, {label: '', value: ''}, '     ')
        ].filter(message => message)
    }

    updateCity = async () => {
        const updatedCity = _.omit({...this.state, areas: _.cloneDeep(this.state.areas)}, 'errorMessages');
        const originalCity = this.props.city;

        if(this.state.errorMessages.length === 0 && 
            !validateEqualPropsObjects(updatedCity, originalCity)) {
            
            const areasList = getItemsDifferenceStatusWithUpdate(
                originalCity.areas, 
                updatedCity.areas.filter(item => item.label),
                'value', 'label');
            // createAction call here
            await this.props.updateCityAction({
                ...updatedCity,
                areas: [...areasList]
            })
            // reset after successful PATCH
            // RESET HERE
            this.settingState();
        }
    }

    onNameChangeHandler = (e) => {
        this.setState( { name: e.target.value });   
    }

    onAddFieldClick = (e) => {
        e.preventDefault();
        this.setState({  areas: [ ...this.state.areas, { label: '', value: '' } ] })
    }

    onRemoveFieldClick = (index) => {
        const list = [...this.state.areas];
        list.splice(index, 1);
        this.setState({ areas: [...list] });
    }

    onDynamicFieldsChange = (e, index) => {
        const { name, value } = e.target;
        const list = [...this.state.areas];
        list[index][name] = value;
        this.setState({ areas: [...list] });
    }

    onFormSubmit = (e) => {
        e.preventDefault();

        // clear server alert at submit
        this.props.clearAlert();
        
        this.setState({ 
            errorMessages: this.validateUpdatedCity()
         }, () => {
            this.updateCity();
         });
    }

    onResetHandler = () => {
        // reset values
        this.settingState();
    }

    onDeleteHandler = () => {
        this.props.deleteCityAction(this.state.id);
    }

    dynamicFieldsRender = () => {
        return this.state.areas.map((field, index) => {
            return (
            <div className="form-group-dynamic" key={index}>
                <InputField
                    id={`areaName${index}`}
                    name="label"
                    type="text"
                    onChangeHandler={(e) => this.onDynamicFieldsChange(e, index)}
                    initial={true}
                    initialValue={field.label}
                    label={`${index+1} `}
                    placeholder=":  "
                    icon="fas fa-signature"
                    colSize="dynamic"
                />
                <div className="form-group__dynamic-col--btn">
                    <button className="delete-field-btn" type="reset" onClick={() => this.onRemoveFieldClick(index)}> 
                        <i className="fas fa-times"/>
                    </button>
                </div>
            </div>
            );
        })
    }
    
    render() {
        return (
            <NotFoundItem
                referer="/city"
            >
                <Form onSubmit={this.onFormSubmit}>
                    <FormHeader 
                        label=" " 
                        icon="fas fa-chevron-left"
                        refererLabel=" "
                        referer="/city"
                    />
                    <ContentPadding>
                        <Alert messages={this.state.errorMessages} type="error" />
                        <div className="form-group">
                            <InputField
                                id="id"
                                name="id"
                                type="text"
                                initial={true}
                                initialValue={this.state.id}
                                locked={true}
                                label=" "
                                placeholder=": 10"
                                icon="fas fa-lock"
                                colSize="2"
                            />
                            <InputField
                                id="name"
                                name="name"
                                type="text"
                                onChangeHandler={this.onNameChangeHandler}
                                initial={true}
                                initialValue={this.state.name}
                                label=" "
                                placeholder=": "
                                icon="fas fa-signature"
                                colSize="2"
                            />
                        </div>
                        {this.dynamicFieldsRender()}
                        <div className="form-group confirm-group margin-top-big">
                            <button className="btn submit-btn" onClick={this.onAddFieldClick}>  <i className=" btn-icon fas fa-plus"></i></button>
                        </div>
                        <ContentDivider />
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value="" />
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                            <input className="btn delete-btn" type="reset" value="" onClick={this.onDeleteHandler}/>
                        </div>
                    </ContentPadding>
                </Form>
            </NotFoundItem>

        )
    }
}

const mapStateToProps = state => {
    return {
        city: state.itemView.city
    }
}

export default connect(mapStateToProps, { 
    fetchCityAction,
    updateCityAction,
    deleteCityAction,
    clearAlert: alertActions.clear,
})(CityView);
