import _ from 'lodash';
import { userConstants } from '../../actions/types';


const invitationReducer = (state={}, action) => {
    
    switch (action.type) {
        case 'FETCH_INVITES':
            return _.mapKeys(action.payload, 'memberId');
        case 'CREATE_INVITE':
            return { ...state, [action.payload.memberId]: action.payload };
        case 'DELETE_INVITE':
            return _.omit(state, action.payload);
        case 'FETCH_MEMBER_INVITES':
            return _.mapKeys(action.payload, 'schoolId');
        case 'REJECT_MEMBER_INVITE':
            return _.omit(state, action.payload);
        case 'ACCEPT_MEMBER_INVITE':
            return {};
        case userConstants.LOGOUT:
            return {};
        default:
            return state;
    }
}

export default invitationReducer;