import React from 'react';
import FormHeader from '../../Form/FormHeader';
import { Link } from 'react-router-dom';

const PreviewList = ({ 
    list, 
    listHeader, 
    referer, 
    icon, 
    refererLabel, 
    emptyMessage, 
    className, 
    tableClassName,
    previewTitleMessage="",
    title,
    subTitle
}) => {
    const renderRows = () => {
        if(list.length !== 0) {
            return list.map((row, index) => {
                return (
                    <div className="preview-list-table__row-item" key={index} >
                        <p className="preview-list-table__username">
                            {row[title] && `${previewTitleMessage} ${row[title]}`}
                            { row[subTitle] && <span className="preview-list-table__role">{row[subTitle]}</span>}
                        </p>
                        <div className="preview-list-table__more-container">
                            <Link to={row.path} className="preview-list-table__more-btn"></Link>
                        </div>
                    </div>
                );
            })
        } else {
            return (
                <div className="preview-list__empty-message">
                    { emptyMessage }
                </div>
            );
        }
    }

    return (
        <div className={`preview-list ${className ? 'preview-list-' + className : '' }`}>
            <FormHeader 
                label={listHeader} 
                referer={referer}
                refererLabel={refererLabel}
                icon={icon}
            />
            <div className={`preview-list-table ${tableClassName}`}>
                {renderRows()}
            </div>
        </div>
    )
}

export default PreviewList;
