import mylabs from '../../../apis/mylabs';
import catchHandler from '../../../helpers/catchHandler';
import loadingActions from '../../utilsActions/loadingActions';
import alertActions from '../../utilsActions/alertActions';
import history from '../../../history';

export const createVisitAction = (visit) => {
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make PATCH request to the API server
            const response = await mylabs.post(`/visit`, visit);

            // get courses to show it in the dropdown
            dispatch({ type: 'CREATE_CURRENT_VISIT', payload: response.data.visit });

            // send school info to laboratories reducer 
            dispatch({ type: 'FETCH_LABORATORIES', payload: response.data.laboratories });

            // send school info to laboratory managers reducer 
            dispatch({ type: 'FETCH_LABORATORY_MANAGERS', payload: response.data.laboratoryManager });

            history.push('/visit/current');

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}

export const fetchCurrentVisitAction = () => {
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make PATCH request to the API server
            const response = await mylabs.get(`/visit/current`);

            // get courses to show it in the dropdown
            dispatch({ type: 'FETCH_CURRENT_VISIT', payload: response.data.visit });

            // send school info to laboratories reducer 
            dispatch({ type: 'FETCH_LABORATORIES', payload: response.data.laboratories });

            // send school info to laboratory managers reducer 
            dispatch({ type: 'FETCH_LABORATORY_MANAGERS', payload: response.data.laboratoryManager });

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}


export const closeCurrentVisitAction = () => {
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make PATCH request to the API server
            await mylabs.post(`/visit/current/close`);

            // get courses to show it in the dropdown
            dispatch({ type: 'CLOSE_CURRENT_VISIT' });

            // send school info to laboratories reducer 
            dispatch({ type: 'CLEAR_LABORATORIES_BY_CLOSE_VISIT' });

            // send school info to laboratory managers reducer 
            dispatch({ type: 'CLEAR_LABORATORY_MANAGERS_BY_CLOSE_VISIT' });

            history.push('/visit/create');

            // message to the user that the school is created
            dispatch(alertActions.success({
                message: "   ,     ",
                code: 200,
                success: true
            }));

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
} 

