import React, { useState, useEffect } from 'react'

const InputField = ({ 
    name, 
    label, 
    requiredInput, 
    onChangeHandler, 
    type, 
    id, 
    placeholder, 
    icon, 
    colSize, 
    initialValue,
    locked,
    initial ,
    className=""
}) => {

    const [text, setText] = useState('');

    useEffect( () => {
        // When the initialValue change, re-render the component
        if(initial) {
            // This useEffect will make change only if the initial props set to true
            setText(initialValue);
        } 
    }, [initialValue, initial]);

    const onChangeText = (e) => {
        setText(e.target.value);
        onChangeHandler(e);
    }

    const renderLabel = () => {
            return (
                label ? <label htmlFor={id} className="form__label"> {label} </label> : null 
            );
    }

    return (
        <div className={`form-group__${colSize}-col`}>
            { renderLabel() }
            <div className="form-control">
                <input 
                    name={name}
                    id={id}
                    type={type} 
                    className={`form__input ${className}`}
                    value={text}
                    onChange={onChangeText}
                    disabled={locked ? locked : false}
                    required={requiredInput}
                    placeholder={placeholder}
                />
                <i className={`form__input--icon ${icon}`}></i>
            </div>
        </div>
    )
}

export default InputField;
