import history from '../history';
import _ from 'lodash';

import loadingActions from '../actions/utilsActions/loadingActions';
import alertActions from '../actions/utilsActions/alertActions';

const catchHandler = (dispatch, err) => {
    // Remove the loading spinner
    dispatch(loadingActions.remove());
    if(_.isUndefined(err.response)) {
        // if undefined, mean the server is NOT WORKING
        dispatch(alertActions.error({
            message: ' :  ,   ',
            code: '500',
            success: false
        }));
    } else if(err.response.data.error === 403){
        // That's mean the JWT in the user localStorage is expired or invalid
        history.push('/login');

    } else if(err.response.data.message === "404 Not Found:    ") {
        // if we dont message from the server
        dispatch(alertActions.error({
            message: '',
            code: err.response.data.error,
            success: false
        }));
    } else {
        // return the error response from the server to display to the user
        dispatch(alertActions.error({
            message: err.response.data.message,
            code: err.response.data.error,
            success: false
        }));
    }
}

export default catchHandler;