// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux'
import _ from 'lodash'

import { Form } from 'components/Form/Form'
import FormHeader from 'components/Form/FormHeader'
import RadioQuestion from 'components/Form/RadioQuestion'
import Alert from 'components/Utils/Alert'
import ContentDivider from 'components/Utils/ContentDivider'
import ContentPadding from 'components/Utils/ContentPadding'
import NotAllowed from 'components/Utils/NotAllowed'
import NotFoundItem from 'components/Utils/NotFoundItem'
import InputField from 'components/Form/InputField'
import parseDateToString from 'helpers/parseDate'
import { validateEqualPropsObjects } from 'helpers/validate'
import { evaluatePracticalExperimentAction, fetchPracticalExperimentRequestAction } from 'actions/pagesActions/SchoolMember/ScienceTeacher/experimentsAction'

class PracticalExperimentEvaluate extends Component {

    state = {
        answers: [],
        errorMessages: [],
        evaluated: false,
        executed: true
    }

    async componentDidMount() {
        if(this.props.hasSchool && 
          (this.props.questions.length < 1 || validateEqualPropsObjects(this.props.experiment, {}))) {
            // API call HERE to get questions
            await this.props.fetchPracticalExperimentRequestAction(this.props.match.params.id)
        }
        this.settingState();
    }

    settingState = () => {
        this.setState({ 
            answers: this.props.questions.map(question => { return { questionId: question.id, answerId: '' }}),
            evaluated: this.props.experiment.evaluated,
            executed: this.props.experiment.executed,
            errorMessages: [] 
        });

    }

    checkEmptyQuestion = () => {
        for(let answer of this.state.answers) {
            if(answer.answerId === '') {
                return '       '
            }
        }
        return [];
    }

    submitEvaluate = () => {
        if(!this.state.errorMessages.length) {
            // API action call to submit questions answers
            this.props.evaluatePracticalExperimentAction(
                this.props.match.params.id, 
                { answers: this.state.answers }
            )
        }
    }

    onQuestionChange = (value, index) => {
        // cloneDeep to avoid changing the state directly
        const currentAnswers = _.cloneDeep(this.state.answers);
        currentAnswers[index].answerId = value;
        this.setState({answers: currentAnswers});
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        this.setState({ 
            errorMessages: this.checkEmptyQuestion()
        }, () => {
            this.submitEvaluate();
        })
    }

    onResetHandler = () => {
        // reset the form
        this.settingState()
    }

    renderQuestions = () => {
        return this.props.questions.map((question, index) => {
            return (
                <RadioQuestion 
                    key={question.id}
                    question={question}
                    onChange={(newAnswer) => this.onQuestionChange(newAnswer, index)}
                    initialValue={this.state.answers[index] ? this.state.answers[index].answerId : ''}
                />
            );
        });
    }

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.hasSchool}
                referer="/invites"
                statusCode="403"
                message="       "
                btnLabel="   "
            >
                <NotAllowed
                    hasSchool={!(this.state.evaluated || this.state.executed !== true)}
                    referer={`/practical-experiment/${this.props.match.params.id}`}
                    statusCode="403"
                    message={this.state.evaluated ? "      " : "        "}
                    btnLabel="  "
                >
                    <NotFoundItem
                        referer="/"
                    >
                        <Form onSubmit={this.onFormSubmit} >
                            <FormHeader 
                                label=" "
                                referer={`/practical-experiment/${this.props.match.params.id}`}
                                refererLabel=" "
                                icon="fas fa-chevron-left"
                            />
                            <ContentPadding>
                                <div className="form-group">
                                    <InputField 
                                        label=" "
                                        initial={true}
                                        initialValue={ this.props.experiment.labManager ? this.props.experiment.labManager : ''}
                                        locked={true}
                                        icon="fas fa-lock"
                                        colSize="2"
                                    />
                                    <InputField 
                                        label=""
                                        initial={true}
                                        initialValue={this.props.experiment.labId ? this.props.experiment.labId : ''}
                                        locked={true}
                                        icon="fas fa-lock"
                                        colSize="2"
                                    />
                                </div>
                                <div className="form-group">
                                    <InputField 
                                        label=" "
                                        initial={true}
                                        initialValue={ this.props.experiment.courseName ? this.props.experiment.courseName : ''}
                                        locked={true}
                                        icon="fas fa-lock"
                                        colSize="2"
                                    />
                                    <InputField 
                                        label=" "
                                        initial={true}
                                        initialValue={this.props.experiment.experimentName ? this.props.experiment.experimentName : ''}
                                        locked={true}
                                        icon="fas fa-lock"
                                        colSize="2"
                                    />
                                </div>
                                <div className="form-group">
                                    <InputField 
                                        label="  "
                                        initial={true}
                                        initialValue={ this.props.experiment.executeDate ? parseDateToString(new Date(this.props.experiment.executeDate)).replace(/-/g,"/") : ''}
                                        locked={true}
                                        icon="fas fa-lock"
                                        colSize="2"
                                    />
                                    <InputField 
                                        label="  "
                                        initial={true}
                                        initialValue={this.props.experiment.quantity ? this.props.experiment.quantity: ''}
                                        locked={true}
                                        icon="fas fa-lock"
                                        colSize="2"
                                    />
                                </div>
                            </ContentPadding>
                            <FormHeader 
                                label=" "
                            />
                            <ContentPadding>
                                <Alert type="404" messages={this.state.errorMessages} />
                                { this.renderQuestions() }
                            </ContentPadding>
                            <ContentPadding>
                                <ContentDivider />
                                <div className="form-group confirm-group margin-top-big">
                                    <input className="btn submit-btn" type="submit" value=" "/>
                                    <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                                </div>
                            </ContentPadding>
                        </Form>
                    </NotFoundItem>

                </NotAllowed>
            </NotAllowed>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
        experiment: state.experiments[ownProps.match.params.id] ? state.experiments[ownProps.match.params.id] : {},
        questions: Object.values(state.scienceTeacherQuestions)
    }
}

export default connect(mapStateToProps, {
    fetchPracticalExperimentRequestAction,
    evaluatePracticalExperimentAction
})(PracticalExperimentEvaluate);