import React from 'react';
import FormHeader from '../../Form/FormHeader';

const ConfirmPreviewList = ({ 
    list, 
    listHeader, 
    referer, 
    icon, 
    refererLabel, 
    emptyMessage, 
    className,
    acceptAction,
    rejectAction,
    acceptLabel="",
    rejectLabel="",
    tableClassName
}) => {

    const renderRows = () => {
        if(list.length !== 0) {
            return list.map((row) => {
                if(row.name) {
                    // to render invites in school manager page
                    return (
                        <div className="preview-list-table__row-item" key={row.memberId} >
                            <p className="preview-list-table__username">
                                {row.name}
                                { row.role ? <span className="preview-list-table__role">{row.role}</span> : null }
                                { row.message ? <span className="preview-list-table__role">{row.message}</span> : null }
                            </p>
                            <div className="preview-list-table__confirm-container">
                                { acceptAction ? <div onClick={() => acceptAction(row.memberId)} className="preview-list-table__accept-btn">{acceptLabel}</div> : null }
                                { rejectAction ? <div onClick={() => rejectAction(row.memberId)} className="preview-list-table__reject-btn">{rejectLabel}</div> : null }
                            </div>
                        </div>
                    );
                } else {
                    // to render invites in members page
                    return (
                        <div className="preview-list-table__row-item" key={row.schoolId} >
                            <p className="preview-list-table__username">
                                {row.nameSchool}
                                { row.role ? <span className="preview-list-table__role">{row.role}</span> : null }
                                { row.message ? <span className="preview-list-table__role">{row.message}</span> : null }
                            </p>
                            <div className="preview-list-table__confirm-container">
                                { acceptAction ? <div onClick={() => acceptAction(row.schoolId)} className="preview-list-table__accept-btn">{acceptLabel}</div> : null }
                                { rejectAction ? <div onClick={() => rejectAction(row.schoolId)} className="preview-list-table__reject-btn">{rejectLabel}</div> : null }
                            </div>
                        </div>
                    );
                }
            })
        } else {
            return (
                <div className="preview-list__empty-message">
                    { emptyMessage }
                </div>
            );
        }
    }

    return (
        <div className={`preview-list ${className ? 'preview-list-' + className : '' }`}>
            <FormHeader 
                label={listHeader} 
                referer={referer}
                refererLabel={refererLabel}
                icon={icon}
            />
            <div className={`preview-list-table ${tableClassName}`}>
                {renderRows()}
            </div>
        </div>
    )
}

export default ConfirmPreviewList;
