import _ from 'lodash';
import { userConstants } from '../../actions/types';


const laboratoryManagersReducer = (state={}, action) => {
    
    switch (action.type) {
        case 'FETCH_LABORATORY_MANAGERS':
            return _.mapKeys(action.payload, 'id');
        case 'UPDATE_LABORATORY_MANAGERS':
            return { ...state, ..._.mapKeys(action.payload, 'id') };
        case 'FETCH_LABORATORY_MANAGER':
            return { ...state, [action.payload.id]: action.payload };
        case 'UPDATE_LABORATORY_MANAGER':
            return { ...state, [action.payload.id]: action.payload };
        case 'CLEAR_LABORATORY_MANAGERS_BY_CLOSE_VISIT':
            return {};
        case userConstants.LOGOUT:
            return {};
        default:
            return state;
    }

}

export default laboratoryManagersReducer;