// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import Form from 'components/Form/Form';
import FormHeader from 'components/Form/FormHeader';
import InputField from 'components/Form/InputField';
import Alert from 'components/Utils/Alert';
import ContentPadding from 'components/Utils/ContentPadding';
import { validateEmpty } from 'helpers/validate';
import ContentDivider from 'components/Utils/ContentDivider';
import TextArea from 'components/Form/TextArea';
import alertActions from 'actions/utilsActions/alertActions';
import { createDefectReportAction } from 'actions/userActions';

class ReportDefect extends Component {

    constructor(props) {
        super(props);
        this.state = {
            title: '',
            text: '',
            errorMessages: []
        }

        this.baseState = { ...this.state };
    }

    validateDefectReport = () => {
        return [
            validateEmpty(this.state.title, '     '),
            validateEmpty(this.state.text, '       ')
        ].filter(message => message)
    }

    createDefectReport = async () => {
        if(this.state.errorMessages.length === 0) {
            // MAKE API CALL HERE 
            await this.props.createDefectReportAction({
                title: this.state.title,
                text: this.state.text
            })
            if(this.props.status) {
                // after success api call, reset fields
                this.onResetHandler();
            }
        }
    }

    onFieldChangeHandler = (value, fieldName) => {
        this.setState({ [fieldName]: value });
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        this.props.clearAlert();

        // performing validation and setting messages
        this.setState({
            errorMessages: this.validateDefectReport()
        }, () => {
            this.createDefectReport()
        });
    }

    onResetHandler = () => {
        // reset form values
        this.setState({ ...this.baseState });
    }

    render() {
        return (
            <Form onSubmit={this.onFormSubmit}>
                <FormHeader label="  " />
                <ContentPadding>
                    <Alert messages={this.state.errorMessages} type="error" />
                    <div className="form-group">
                        <InputField
                            id="title"
                            name="title"
                            onChangeHandler={(e) => this.onFieldChangeHandler(e.target.value, 'title')}
                            type="text"
                            initial={true}
                            initialValue={this.state.title}
                            label=" "
                            placeholder=":    "
                            icon="fas fa-signature"
                            colSize="1"
                        />
                    </div>
                    <div className="form-group">
                        <TextArea 
                            id="text"
                            name="text"
                            onChangeHandler={(e) => this.onFieldChangeHandler(e.target.value, 'text')}
                            type="text"
                            initial={true}
                            initialValue={this.state.text}
                            label=" "
                            colSize="1"
                            placeholder=":     ,       "
                        />
                    </div>
                    <ContentDivider />
                    <div className="form-group confirm-group margin-top-big">
                        <input className="btn submit-btn" type="submit" value=""/>
                        <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                    </div>
                </ContentPadding>
            </Form>
        );
    }
}

const mapStateToProps = state => {
    return {
        status: state.alert.success
    }
}

export default connect(mapStateToProps, { 
    createDefectReportAction, 
    clearAlert: alertActions.clear
})(ReportDefect);