import React from 'react'
import { Link } from 'react-router-dom';
import _ from 'lodash';


import logoText from '../images/icon-logo.png';
import { connect } from 'react-redux';
import getNavList from '../helpers/navList';
import navToggleAction from '../actions/utilsActions/navToggleAction';

const Sidebar = (props) => {



    const renderNavList = () => {

        // Give the ability to implement dynamic navigation based on the user role.
        // also pass the state of hasSchool, to show pages to access school or add School only
        const navList = getNavList(props.role, props.hasSchool);
        const navArray = [];
        for(let itemList of navList) {
            if(_.has(itemList, 'children') && !itemList.breadcrumbOnly) {
                // mean, this is a header in the side nav bar
                navArray.push(<li className="right-side__nav-list--header" key={itemList.label}>{itemList.label}</li>);
                for (let nestedItemList of itemList.children) {
                    // this is a Link route in the nav bar
                    navArray.push(
                    <Link 
                        to={nestedItemList.path} 
                        className="right-side__nav-list--item" 
                        key={nestedItemList.path}
                        onClick={() => props.navToggleAction(props.navToggle)}
                    >
                        {nestedItemList.label}
                    </Link>)
                }
            }
        }

        return navArray;
    }

    return (
        <div className={`right-side ${props.toggledClass}`}>
            <div className="right-side__logo">
                <img className="right-side__logo--logo" src={logoText} alt=" "/>
            </div>
            <div className="right-side__profile">
                <div className="right-side__user-profile">
                    <div className="right-side__profile-avatar">{`${ props.currentUser.fname[0] +'.'+ props.currentUser.lname[0]}`}</div>
                    <div className="right-side__profile-identity">
                        <p className="right-side__profile-identity--name">{`${ props.currentUser.fname +' '+ props.currentUser.lname}`}</p>
                        <p className="right-side__profile-identity--role">{`${props.currentUser.role}`}</p>
                    </div>
                </div>
                <Link 
                    to="/profile" 
                    className="right-side__profile-anchor"
                    onClick={() => props.navToggleAction(props.navToggle)}
                >
                    <i className="fas fa-chevron-left" />
                </Link>
            </div>
            <nav className="right-side__nav">
                <ul className="right-side__nav-list">
                    {renderNavList()}
                </ul>
            </nav>
        </div>
    );
}

const mapStateToProps = state => {
    return {
        toggledClass: state.navToggle,
        currentUser: state.auth.user.data,
        role: state.auth.user.permissions,
        navToggle: state.navToggle,
        hasSchool: state.auth.user.data.hasSchool
    }
}

export default connect(mapStateToProps, { navToggleAction })(Sidebar)
