// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import { 
    validateSaudiPhoneNumber, 
    validateEmail, 
    validateName, 
    validateUpdatePassword,
    validateEqualPropsObjects
} from '../helpers/validate';
import Form from '../components/Form/Form';
import InputField from '../components/Form/InputField';
import Alert from '../components/Utils/Alert';
import { initiateProfileAction, patchProfileAction } from '../actions/pagesActions/profileAction';
import alertActions from '../actions/utilsActions/alertActions';
import ContentPadding from 'components/Utils/ContentPadding';
import FormHeader from 'components/Form/FormHeader';

class Profile extends Component {

    state = {
        id: '',
        fname: '',
        mname: '',
        lname: '',
        phone: '',
        email: '',
        role: '',
        oldPassword: '',
        newPassword: '',
        confirmedNewPassword: '',
        personalErrorMessages: [],
        privacyErrorMessages: []
    }

    async componentDidMount() {
        // Call action to execute the API and initiate the component with initial data
        if(!this.props.profile.id) {
            await this.props.initiateProfileAction();
        }
        this.settingState();
    }

    settingState = () => {
        const { profile } = this.props;
        if(profile.id) {
            this.baseState = { ...this.state, ...profile };
            this.setState({ ...this.baseState });
        }
    }

    validatePersonalData = () => {
        const data = this.state;

        return [
            //validate personal info update
            validateName([data.fname, data.mname, data.lname]),
            validateSaudiPhoneNumber(data.phone),
            validateEmail(data.email)
        ].filter(message => message)
    }

    validatePrivateData = () => {
        const data = this.state;

        return [
            //validate passwords
            ...validateUpdatePassword(
                data.oldPassword, 
                data.newPassword, 
                data.confirmedNewPassword
            )
        ].filter(message => message)
    }

    updateProfile = async () => {
        const data = this.state;
        if (this.state.personalErrorMessages.length === 0 && 
            this.state.privacyErrorMessages.length === 0 && 
            !validateEqualPropsObjects(this.state, this.baseState)) {

            await this.props.patchProfileAction({ ...data })
            // If the patch call success, set the new state of the profile in case of reset changes.
            //REPLCAE this.state with profile from API
            if(this.props.status ) {
                this.settingState();
            }
        }
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        this.props.clearAlert();

        this.setState({
            personalErrorMessages: this.validatePersonalData(),
            privacyErrorMessages: this.validatePrivateData()
        }, () => {
            this.updateProfile();
        })
    }

    onInputChangeHandler = (e) => {
        // Handling the Personal Info of the state
        const { name, value } = e.target;
        this.setState({ [name]: value });
    }

    onResetHandler = (e) => {
        // Reseting the values of the personal data.        
        e.preventDefault();
        this.settingState();
    }

    render() {
        return (
            <div>
                <Form onSubmit={this.onFormSubmit}>
                    <FormHeader label=" " />
                    <ContentPadding>
                        <Alert 
                            messages={this.state.personalErrorMessages}
                            type="error"
                        />
                        <div className="form-group">
                            <InputField
                                id="id"
                                name="id"
                                onChangeHandler={this.onInputChangeHandler}
                                type="text"
                                initial={true} 
                                initialValue={this.state.id}
                                locked={true}
                                label=" "
                                icon="fas fa-lock"
                                colSize="1"
                            />
                        </div>
                        <div className="form-group">
                            <InputField 
                                id="role"
                                name="role"
                                onChangeHandler={this.onInputChangeHandler}
                                type="text"
                                initial={true}
                                initialValue={this.state.role}
                                locked={true}
                                label=" "
                                colSize="1"
                                icon="fas fa-lock"
                            />
                        </div>

                        <div className="form-group">
                            <InputField 
                                id="fname"
                                name="fname"
                                onChangeHandler={this.onInputChangeHandler}
                                type="text"
                                initial={true}
                                initialValue={this.state.fname}
                                label=" "
                                colSize="1"
                                icon="fas fa-user"
                            />
                        </div>
                        
                        <div className="form-group">
                            <InputField
                                id="mname"
                                name="mname"
                                onChangeHandler={this.onInputChangeHandler}
                                type="text"
                                initial={true}
                                initialValue={this.state.mname}
                                label=" "
                                colSize="1"
                                icon="fas fa-user"
                            />
                        </div>
                        <div className="form-group">
                            <InputField 
                                id="lname"
                                name="lname"
                                onChangeHandler={this.onInputChangeHandler}
                                type="text"
                                initial={true}
                                initialValue={this.state.lname}
                                label=" "
                                colSize="1"
                                icon="fas fa-user"
                            />
                        </div>
                        <div className="form-group">
                            <InputField 
                                id="email"
                                name="email"
                                onChangeHandler={this.onInputChangeHandler}
                                type="text"
                                initial={true}
                                initialValue={this.state.email}
                                label=" "
                                colSize="1"
                                icon="fas fa-at"
                            />
                        </div>
                        <div className="form-group">
                            <InputField 
                                id="phone"
                                name="phone"
                                onChangeHandler={this.onInputChangeHandler}
                                type="tel"
                                initial={true}
                                initialValue={this.state.phone}
                                label=" "
                                colSize="1"
                                icon="fas fa-phone"
                            />
                        </div>
                    </ContentPadding>
                    <FormHeader label=" " />                        
                    <ContentPadding>
                        
                        <Alert 
                            messages={this.state.privacyErrorMessages}
                            type="error"
                        />
                        <div className="form-group">
                            <InputField 
                                id="old-pass"
                                name="oldPassword"
                                onChangeHandler={this.onInputChangeHandler}
                                initial={true}
                                initialValue={this.state.oldPassword}
                                type="password"
                                label="  "
                                colSize="1"
                                icon="fas fa-key"
                            />
                        </div>
                        <div className="form-group">
                            <InputField
                                id="new-pass"
                                name="newPassword"
                                onChangeHandler={this.onInputChangeHandler}
                                type="password"
                                initial={true}
                                initialValue={this.state.newPassword}
                                label="  "
                                colSize="1"
                                icon="fas fa-key"
                            />
                        </div>
                        <div className="form-group">
                            <InputField 
                                id="confirm-new-pass"
                                name="confirmedNewPassword"
                                onChangeHandler={this.onInputChangeHandler}
                                initial={true}
                                initialValue={this.state.confirmedNewPassword}
                                type="password"
                                label="   "
                                colSize="1"
                                icon="fas fa-key"
                            />
                        </div>
                    </ContentPadding>
                    <FormHeader label=" " /> 
                        <ContentPadding>
                            <div className="form-group confirm-group">
                                <input className="btn submit-btn" type="submit" value=""/>
                                <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                            </div>
                        </ContentPadding>
                </Form>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        currentUser: state.auth.user,
        status: state.alert.success,
        profile: state.profile
    }
}

export default connect(mapStateToProps, { 
    initiateProfileAction, 
    patchProfileAction, 
    clearAlert: alertActions.clear 
})(Profile);
