import { userConstants } from '../../actions/types';
import _ from 'lodash';

const evaluateQuestionsReducer = (state={}, action) => {
    switch(action.type) {

        case 'FETCH_SCIENCE_TEACHER_EVALUATE_QUESTIONS':
            return _.mapKeys(action.payload, 'id');
        case userConstants.LOGOUT:
            // Remove the JWT token
            localStorage.removeItem('token');
            return {};
            
        default:
            return state;
    }
}

export default evaluateQuestionsReducer;