import React, { Fragment } from 'react'
import { Route } from 'react-router'

import Tab, { TabElement } from 'components/Utils/Tab'
import QuestionAdd from './QuestionAdd'
import QuestionList from './QuestionList'

const QuestionTab = ({ match }) => {
    return (
        <Fragment>
            <Tab>
                <TabElement 
                    to="/question/add"
                    match={match}
                    title=" "
                />
                <TabElement 
                    to="/question"
                    match={match}
                    title=" "
                />
            </Tab>
            <Route 
                path="/question/add"
                exact
                component={QuestionAdd}
            />
            <Route 
                path="/question"
                exact
                component={QuestionList}
            />
        </Fragment>
    )
}

export default QuestionTab
