import React from 'react'
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

const NotAllowed = ({ 
    children, 
    referer, 
    hasSchool=true,
    message='     ',
    statusCode='404',
    btnLabel='  '

}) => {
    // in case the code is not 404 or is undefined or hasSchool == true, return the components
    // else just display not found message
    return ( 
        hasSchool
        ? children 
        : <div className="resource-not-found">
            <p className="resource-not-found__code">{statusCode}</p>
            <p className="resource-not-found__message">{message}</p>
            <Link to={referer} className="resource-not-found__referer btn">{btnLabel}</Link>
        </div> );
}

export default connect()(NotAllowed);
