// @ts-nocheck
import React, { useEffect } from 'react'

import NotFoundItem from 'components/Utils/NotFoundItem'
import { Form } from 'components/Form/Form'
import CustomDatePicker from 'components/Form/CustomDatePicker'
import FormHeader from 'components/Form/FormHeader'
import InputField from 'components/Form/InputField'
import TextArea from 'components/Form/TextArea'
import PreviewList from 'components/List/PreviewList/PreviewList'
import PreviewListContainer from 'components/List/PreviewList/PreviewListContainer'
import ContentPadding from 'components/Utils/ContentPadding'
import { connect } from 'react-redux'
import { validateEqualPropsObjects } from 'helpers/validate'
import { closeCurrentVisitAction, fetchCurrentVisitAction } from 'actions/pagesActions/EducationalSupervisor/visitAction'
import ContentDivider from 'components/Utils/ContentDivider'

const VisitCurrent = ({ 
    visit, 
    laboratoryManagers, 
    laboratories, 
    fetchCurrentVisitAction, 
    closeCurrentVisitAction 
}) => {


    useEffect(() => {
        // make API call, if the visit ID empty
        if(!visit.visitId) {
            fetchCurrentVisitAction();
        }
    })

    const onCloseCurrentVisit = () => {
        closeCurrentVisitAction();
    }

    const convertRole = (list) => {
        return list.map(member => {
            return { ...member, role: member.role.ar };
        })
    }

    return (
        <NotFoundItem
            referer="/visit/create"
            message="    "
            btnLabel="   "
        >
            <Form>
                <FormHeader label=" " />
                <ContentPadding>
                    <div className="form-group">
                        <InputField
                            id="visitId"
                            name="visitId"
                            type="text"
                            initial={true}
                            initialValue={visit.visitId}
                            label=" "
                            icon="fas fa-lock"
                            locked={true}
                            colSize="2"
                        />
                        <CustomDatePicker
                            id="visitDate"
                            name="visitDate"
                            type="text"
                            initial={true}
                            initialDate={visit.visitDate ? new Date(visit.visitDate) : ''}
                            label=" "
                            icon="fas fa-lock"
                            locked={true}
                            colSize="2"
                        />
                    </div>
                    <div className="form-group margin-bottom-text-area">
                        <TextArea
                            id="note"
                            name="note"
                            type="text"
                            initial={true}
                            initialValue={visit.note ? visit.note : ' '}
                            label=""
                            icon="fas fa-lock"
                            locked={true}
                            colSize="1"
                        />
                    </div>
                    <ContentDivider />
                    <div className="form-group confirm-group margin-top-big">
                        <input 
                            className="btn cancel-btn-red no-margin-right" 
                            type="reset" 
                            value=" "
                            onClick={onCloseCurrentVisit}
                        />
                    </div>
                </ContentPadding>
            </Form>
            <Form >
                <FormHeader label=" " />
                <ContentPadding>
                    <div className="form-group">
                        <InputField
                            id="schoolId"
                            name="schoolId"
                            type="text"
                            initial={true}
                            initialValue={visit.schoolId}
                            label=" "
                            icon="fas fa-lock"
                            locked={true}
                            colSize="2"
                        />
                        <InputField
                            id="schoolName"
                            name="schoolName"
                            type="text"
                            initial={true}
                            initialValue={visit.schoolName}
                            label=" "
                            icon="fas fa-lock"
                            locked={true}
                            colSize="2"
                        />
                    </div>
                    <div className="form-group">
                        <InputField
                            id="cityName"
                            name="cityName"
                            type="text"
                            initial={true}
                            initialValue={visit.cityName}
                            label=""
                            icon="fas fa-lock"
                            locked={true}
                            colSize="2"
                        />
                        <InputField
                            id="areaName"
                            name="areaName"
                            type="text"
                            initial={true}
                            initialValue={visit.areaName}
                            label=" "
                            icon="fas fa-lock"
                            locked={true}
                            colSize="2"
                        />
                    </div>
                </ContentPadding>
            </Form>
            <PreviewListContainer>
                <PreviewList
                    listHeader=" "
                    list={convertRole(laboratoryManagers)}
                    emptyMessage="   "
                    title="name"
                    subTitle="role"
                />
                <PreviewList
                    listHeader=""
                    list={laboratories} 
                    emptyMessage="   "
                    title="id"
                    previewTitleMessage="  : "
                />
            </PreviewListContainer>
        </NotFoundItem>
    )
    
}

const mapStateToProps = state => {
    let visit = state.educationalSupervisorCurrentVisit;
    if(validateEqualPropsObjects(visit, {})) {
        visit = undefined;
    }
    
    return {
        visit,
        laboratories: Object.values(state.laboratories),
        laboratoryManagers: Object.values(state.laboratoryManagers)
    }
}

VisitCurrent.defaultProps = {
    visit: {
        visitId: '',
        visitDate: '',
        note: '',
        schoolId: '',
        schoolName: '',
        cityName: '',
        areaName: ''
    }
}

export default connect(mapStateToProps, {
    fetchCurrentVisitAction,
    closeCurrentVisitAction
})(VisitCurrent);