// @ts-nocheck
import ContentPadding from 'components/Utils/ContentPadding'
import TextContainer from 'components/Text/TextContainer'
import React, { Fragment } from 'react'
import { Link } from 'react-router-dom'
import BulletContainer from 'components/Text/BulletContainer'
import BulletPoint from 'components/Text/BulletPoint'
import TitleHeader from 'components/Text/TitleHeader'
import ParagraphBlock from 'components/Text/ParagraphBlock'
import { connect } from 'react-redux'

const SchoolManagerHomePage = ({ hasSchool }) => {


    const renderInstructions = () => {

        if(hasSchool) {
            return (
                <Fragment>
                    <BulletContainer
                        title=":      "
                    >
                        <BulletPoint>
                            &nbsp;      
                            <Link to="/members/create" className="color-primary">    </Link>           
                        </BulletPoint>
                        <BulletPoint>
                            . ,    
                        </BulletPoint>
                        <BulletPoint>
                                       
                        </BulletPoint>
                    </BulletContainer>
                    <BulletContainer
                        title=":     "
                    >
                        <BulletPoint>
                            &nbsp;      
                            <Link to="/laboratory/create" className="color-primary">  </Link>
                        </BulletPoint>
                        <BulletPoint>
                            .    
                        </BulletPoint>
                        <BulletPoint>
                                  
                        </BulletPoint>
                        <BulletPoint>
                                
                        </BulletPoint>
                    </BulletContainer>
                    <BulletContainer
                        title=":      , "
                    >
                        <BulletPoint>
                            &nbsp;      
                            <Link to="/school" className="color-primary">  </Link>
                        </BulletPoint>
                        <BulletPoint>
                            &nbsp;      
                            <Link to="/dashboard/laboratory-manager" className="color-primary">   </Link>
                        </BulletPoint>
                        <BulletPoint>
                            &nbsp;      
                            <Link to="/dashboard/laboratory" className="color-primary">   </Link>
                        </BulletPoint>
                    </BulletContainer>
                </Fragment>
            );
        }

        return (
            <Fragment>
                <BulletContainer
                    title=":      "
                >
                    <BulletPoint>
                        &nbsp;      
                        <Link to="/school/create" className="color-primary">   </Link>           
                    </BulletPoint>
                    <BulletPoint>
                        .    ,      
                    </BulletPoint>
                    <BulletPoint>
                          ,      
                    </BulletPoint>
                </BulletContainer>
            </Fragment>
        );
    }

    return (
        <TextContainer>
            <TitleHeader title=" " />
            <ParagraphBlock>
                               
                 ,           
                ,            
                     .       
                   .         
                        .       
                  ,        
                  .
            </ParagraphBlock>

            <TitleHeader title="" />
            <ContentPadding>
                <ContentPadding>
                    { renderInstructions() }
                </ContentPadding> 
            </ContentPadding> 
        </TextContainer>
    )
}

const mapStateToProps = (state) => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
    }
}

export default connect(mapStateToProps)(SchoolManagerHomePage)
