import { userConstants } from '../../actions/types';


const dashboardReducer = (state={}, action) => {
    switch (action.type) {

        case 'FETCH_DASHBOARD':
            return { ...action.payload };
        case 'CLEAR_DASHBOARD':
            return {};
        case userConstants.LOGOUT:
            return {};
        default:
            return state;
    }
}

export default dashboardReducer;