// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import { fetchPracticalRequestsAction } from 'actions/pagesActions/SchoolMember/LaboratoryManager/PracticalRequestAction';
import alertActions from 'actions/utilsActions/alertActions';
import CustomDatePicker from 'components/Form/CustomDatePicker';
import CustomDropdown from 'components/Form/CustomDropdown';
import DropMenu from 'components/Form/DropMenu';
import InputField from 'components/Form/InputField';
import ListLayout from 'components/List/ListLayout';
import PaginatedList from 'components/List/PaginatedList';
import SearchFilter from 'components/List/SearchFilter';
import NotAllowed from 'components/Utils/NotAllowed';
import NotFoundItem from 'components/Utils/NotFoundItem';
import parseDateToString, { onMinDateChange } from 'helpers/parseDate';
import CheckBox from 'components/Form/CheckBox';

const COLUMN_REQUESTS_HEADERS = [
    {
        label: '',
        key: 'id'
    },
    {
        label: '',
        key: 'experimentName'
    },
    {
        label: ' ',
        key: 'executeDate'
    },
    {
        label: '  ',
        key: 'createDate'
    },
    {
        label: ' ',
        key: 'statusLabel'
    },
    {
        label: '',
        key: 'labId'
    },
    {
        label: '',
        key: 'path'
    }
];

class PracticalExperimentList extends Component {

    constructor(props) {
        super(props);
        this.labsRef = React.createRef();

        this.state = {
            page: 1,
            id: '',
            labId: 'all',
            fromExecuteDate: '',
            toExecuteDate: '',
            fromCreateDate: '',
            toCreateDate: '',
            accepted: false,
            rejected: false,
            pending: false
        }

        this.baseState = { ...this.state };
    }

    componentDidMount() {
        if(this.labsRef.current) {
            // set the labs dropmenu with ALL option at mount
            this.labsRef.current.selectItem({label: ' ', value: 'all'})
        }
    }

    convertStringToDate = (items=[]) => {
        // Convert string to date, to display it in dd/MM/yyyy format without time
        return items.map(item => {
            // clone the item, we don't want it to change in the reducer 
            const newItem = {...item}
            newItem.createDate = parseDateToString(new Date(item.createDate)).replace(/-/g,"/");
            newItem.executeDate = parseDateToString(new Date(item.executeDate)).replace(/-/g,"/");
            newItem.statusLabel = newItem.status === null ? ' ' : newItem.status === true ? '' : ''
            return newItem
        });
    }

    convertParamsDateToString = () => {
        const newParams = { ...this.state };
        newParams.fromExecuteDate = parseDateToString(newParams.fromExecuteDate);
        newParams.toExecuteDate = parseDateToString(newParams.toExecuteDate);
        newParams.fromCreateDate = parseDateToString(newParams.fromCreateDate);
        newParams.toCreateDate = parseDateToString(newParams.toCreateDate);
        return newParams;
    }

    onFiltersChange = (value, fieldName, toDateFieldName='') => {
        switch (fieldName) {
            case 'fromExecuteDate':
            case 'fromCreateDate':
                if(this.state[toDateFieldName] && value.getTime() >= this.state[toDateFieldName].getTime()) {
                    // reset the 'to' date becasue it maybe become before the 'from' date
                    this.setState({ [fieldName]: value, [toDateFieldName]: '' });
                    return;
                }
                this.setState({ [fieldName]: value });
                break;
            default:
                this.setState({ [fieldName]: value });
                break;
        }
    }

    onPageChange = (currentPage) => {
        // clear alerts, becasue when item deleted, it will be send here
        this.props.clearAlert();

        // set the currentPage, and make API call after that
        this.setState({ page: currentPage.selected + 1 }, () => {
            // This will make the call when the component mount first
            this.props.fetchPracticalRequestsAction(this.convertParamsDateToString())
            
        });
    }

    onFilterSubmit = (e) => {
        e.preventDefault();
        this.setState({ page: 1 }, () => {
            this.props.fetchPracticalRequestsAction(this.convertParamsDateToString());
        })
    }

    onFilterReset = () => {
        this.setState( { ...this.baseState }, () => {
            if(this.labsRef.current) {
                // set the labs dropmenu with ALL option
                this.labsRef.current.selectItem({label: ' ', value: 'all'})
            }
        });
    }

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.hasSchool}
                referer="/invites"
                statusCode="403"
                message="       "
                btnLabel="   "
            >
                <NotFoundItem
                    referer="/"
                >
                    <ListLayout>
                        <PaginatedList 
                            totalPages={this.props.totalPages}
                            data={this.convertStringToDate(this.props.requests)}
                            colHeaders = {COLUMN_REQUESTS_HEADERS}
                            onPageChange={this.onPageChange}
                            initialPage={this.state.page - 1}
                        />
                        <SearchFilter onSubmit={this.onFilterSubmit} >
                            <div className="from-group">
                                <InputField
                                    id="id"
                                    name="id"
                                    initial={true}
                                    initialValue={this.state.id}
                                    onChangeHandler={(e) => this.onFiltersChange(e.target.value, 'id')}
                                    colSize="1"
                                    placeholder="  "
                                    icon="fas fa-search"
                                />
                            </div>
                            <div className='from-group'>
                                <CustomDropdown 
                                    title=" "
                                    colSize="1"
                                    setRef={(ref) => this.labsRef = ref}
                                    list={[{label: ' ', value: 'all'}, ...this.props.labs]}
                                    onChange={(selectedLab) => this.onFiltersChange(selectedLab, 'labId')}
                                />
                            </div>
                            <div className="form-group">
                                <DropMenu 
                                    colSize="1"
                                    title=" "
                                >
                                    <CustomDatePicker 
                                        colSize="1"
                                        placeholder=" "
                                        onChangeHandler={(newDate) => this.onFiltersChange(newDate, 'fromExecuteDate', 'toExecuteDate') }
                                        initial={true}
                                        initialDate={this.state.fromExecuteDate}
                                    />
                                    <CustomDatePicker 
                                        colSize="1"
                                        placeholder=" "
                                        onChangeHandler={(newDate) => this.onFiltersChange(newDate, 'toExecuteDate') }
                                        initial={true}
                                        initialDate={this.state.toExecuteDate}
                                        minDate={onMinDateChange(this.state.fromExecuteDate)}
                                    />
                                </DropMenu>
                            </div>
                            <div className="form-group">
                                <DropMenu 
                                    colSize="1"
                                    title="  "
                                >
                                    <CustomDatePicker 
                                        colSize="1"
                                        placeholder=" "
                                        onChangeHandler={(newDate) => this.onFiltersChange(newDate, 'fromCreateDate', 'toCreateDate') }
                                        initial={true}
                                        initialDate={this.state.fromCreateDate}
                                    />
                                    <CustomDatePicker 
                                        colSize="1"
                                        placeholder=" "
                                        onChangeHandler={(newDate) => this.onFiltersChange(newDate, 'toCreateDate') }
                                        initial={true}
                                        initialDate={this.state.toCreateDate}
                                        minDate={onMinDateChange(this.state.fromCreateDate)}
                                    />
                                </DropMenu>
                            </div>
                            <div className="form-group">
                                <DropMenu 
                                        colSize="1"
                                        title=" "
                                >
                                    <div className="margin-top">
                                        <CheckBox 
                                            id="accepted"
                                            name="accepted"
                                            label=""
                                            initial={true}
                                            initialValue={this.state.accepted}
                                            onChange={(isChecked) => {this.onFiltersChange(isChecked, 'accepted')}}
                                        />
                                    </div>
                                    <div className="margin-top">
                                        <CheckBox 
                                            id="rejected"
                                            name="rejected"
                                            label=""
                                            initial={true}
                                            initialValue={this.state.rejected}
                                            onChange={(isChecked) => {this.onFiltersChange(isChecked, 'rejected')}}
                                        />
                                    </div>
                                    <div className="margin-top">
                                        <CheckBox 
                                            id="pending"
                                            name="pending"
                                            label=" "
                                            initial={true}
                                            initialValue={this.state.pending}
                                            onChange={(isChecked) => {this.onFiltersChange(isChecked, 'pending')}}
                                        />
                                    </div>
                                </DropMenu>
                            </div>
                            <div className="form-group confirm-group margin-top-big">
                                <input className="btn submit-btn" type="submit" value="" />
                                <input className="btn cancel-btn" type="reset" value=" " onClick={this.onFilterReset} />
                            </div>
                        </SearchFilter>
                    </ListLayout>
                </NotFoundItem>
            </NotAllowed>
        )
    }
}

const mapStateToProps = state => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
        requests: state.tableList.requests,
        totalPages: state.tableList.totalPages,
        labs: Object.values(state.laboratories)
    }
}

export default connect(mapStateToProps, {
    fetchPracticalRequestsAction,
    clearAlert: alertActions.clear
})(PracticalExperimentList);