import ContentPadding from 'components/Utils/ContentPadding'
import React from 'react'

function ParagraphBlock({ children }) {
    return (
        <div className="text-container__paragraph-container"> 
        <ContentPadding>
            <p className="text-container__paragraph-element">
                { children }
            </p>
        </ContentPadding> 
    </div>
    )
}

export default ParagraphBlock
