import _ from 'lodash';
import { userConstants } from '../../actions/types';


const laboratoriesReducer = (state={}, action) => {

    switch (action.type) {
        case 'FETCH_LABORATORIES':
            return _.mapKeys(action.payload, 'id');
        case 'UPDATE_LABORATORIES':
            return { ...state, ..._.mapKeys(action.payload, 'id') };
        case 'FETCH_LABORATORY':
            return { ...state, [action.payload.id]: action.payload };
        case 'CREATE_LABORATORY':
            return { ...state, [action.payload.id]: action.payload };
        case 'UPDATE_LABORATORY':
            return { ...state, [action.payload.id]: action.payload };
        case 'CLEAR_LABORATORIES_BY_CLOSE_VISIT':
            return {};
        case userConstants.LOGOUT:
            return {};
        default:
            return state;
    }

}

export default laboratoriesReducer;