import loadingActions from '../utilsActions/loadingActions';
import alertActions from '../utilsActions/alertActions';
import mylabs from '../../apis/mylabs';
import catchHandler from '../../helpers/catchHandler';


export const initiateProfileAction = () => {
    return async (dispatch) => {
        try {
            // create action for isLoading
            dispatch(loadingActions.show());

            // Calling the API
            const response = await mylabs.get('/profile');

            
            // send to reducer
            dispatch({ type: 'FETCH_PROFILE', payload: { ...response.data.user }});

            // STOP the isLoading spinner
            dispatch(loadingActions.remove());

        } catch(err) {
            catchHandler(dispatch, err);
        }
    }

}

const renewJwtAction = (token) => {
    // execute this action when updating user data, it will renew the token and info of the auth state
    return {
        type: 'RENEW_JWT',
        token
    }
}

export const patchProfileAction = (formValues) => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to patch
            const response = await mylabs.patch('/profile', { ...formValues })

            // send to reducer
            dispatch({ type: 'FETCH_PROFILE', payload: { ...response.data.user }});

            // remove the loading indicator
            dispatch(loadingActions.remove());

            dispatch(alertActions.success({
                message: "   ",
                code: 200,
                success: true
            }));
            // Renew the Token here first
            dispatch(renewJwtAction(response.data.token));

        } catch (err) {
            catchHandler(dispatch, err);
        }

    }
}
