import React from 'react'
import { Route, Redirect } from 'react-router-dom';
import NotFound from '../components/Utils/NotFound';
import { connect } from 'react-redux';

const PrivateRoute = ({ component: Component, roles, currentUserRole, location, ...rest }) => {
    if(!roles) {
        // Redirecting to Login page if JWT token not exist
        return (
            <Route {...rest} render={ props=> (
                
                localStorage.getItem('token')
                ? < Component {...props} /> 
                : <Redirect to={{
                    pathname: '/login',
                    state: { from: props.location } 
                }} />
                )
            } />
        );
    } else {
        // if the user does not have the permissions to display the page, return 401 error message
        return (
            <Route {...rest} render={ props=> (
                roles.includes(currentUserRole)
                ? < Component {...props} /> 
                : <NotFound 
                    statusCode="401" 
                    message=',      ' 
                    referer="/"
                    btnLabel='  '
                />
                )
            } />
        );

    }


}

const mapStateToProps = state => {
    return {
        location: state.referer
    }
}

export default connect(mapStateToProps)(PrivateRoute);
