// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';
import ReactToPrint from 'react-to-print';

import CustomDatePicker from '../../../components/Form/CustomDatePicker';
import { Form } from '../../../components/Form/Form';
import FormHeader from '../../../components/Form/FormHeader';
import InputField from '../../../components/Form/InputField';
import Alert from '../../../components/Utils/Alert';
import ContentDivider from '../../../components/Utils/ContentDivider';
import ContentPadding from '../../../components/Utils/ContentPadding';
import CustomeQRCode from '../../../components/Utils/CustomeQRCode';
import { isWholeNumber, validateEmpty } from '../../../helpers/validate';
import NotAllowed from '../../../components/Utils/NotAllowed';
import NotFoundItem from '../../../components/Utils/NotFoundItem';
import { deleteLabItemAction, fetchLabItemAction, updateLabItemAction } from '../../../actions/pagesActions/SchoolMember/LaboratoryManager/LaboratoryItemsAction';
import CustomDropdown from 'components/Form/CustomDropdown';
import ConfirmDialog from 'components/Utils/ConfirmDialog';
import TextArea from 'components/Form/TextArea';
import logo from '../../../images/icon-logo.png';
import { detectBrowser } from 'helpers/browserHelpers';

const DELETE_TYPES_LIST = [
    {
        label: '',
        value: 'lost'
    },
    {
        label: '',
        value: 'broke'
    },
    {
        label: ' ',
        value: 'expired'
    },
    {
        label: '',
        value: 'tainted'
    },
    {
        label: '',
        value: 'used'
    }
]

class LabItemView extends Component {

    state = {
        id: '',
        itemType: '',
        quantity: '',
        expireDate: null,
        hasExpireDate: false,
        errorMessages: [],
        isDialogOpen: false,
        deleteType: '',
        note: '',
        deleteTypeErrorMessage: []
    }

    async componentDidMount() {
        // specific item GET ACTION call here
        if(this.props.hasSchool && !this.props.item) {
            await this.props.fetchLabItemAction(this.props.match.params.labId, this.props.match.params.itemId);
        } 
        this.settingState();
    }

    settingState = () => {
        const { item } = this.props;
        if(item) {
            this.setState({
                id: item.id,
                itemType: item.name,
                expireDate: item.expireDate ? new Date(item.expireDate) : null,
                hasExpireDate: item.expireDate ? true : false,
                quantity: item.quantity,
                errorMessages: []
            });
        }
    }

    validateUpdatedItem = () => {
        return [
            validateEmpty(this.state.itemType, '    '),
            isWholeNumber(this.state.quantity, 
                '      ',
                 '     ,      ',
                 '     '
            ),
            this.state.hasExpiredDate ? validateEmpty(this.state.newExpireDate, '    ') : ''
        ].filter(message => message)
    }

    validateDeleteOption = () => {
        return [
            validateEmpty(this.state.deleteType, '      '),
        ].filter(message => message)
    }

    updateItem = async () => {
        const { quantity, expireDate } = this.props.item;
        if(!this.state.errorMessages.length && 
            (( quantity !== this.state.quantity) || (this.state.hasExpireDate &&
            new Date(expireDate).getTime() !== this.state.expireDate.getTime()))) {
            // call the create action call here
            await this.props.updateLabItemAction(this.props.match.params.labId, {
                itemId: this.state.id,
                quantity: Number(this.state.quantity) - Number(quantity),
                expireDate: this.state.expireDate,
                hasExpireDate: this.state.hasExpireDate
            })
            if(this.props.status) {
                this.settingState();        
            }

        }
    }

    deleteItem = async () => {
        if(!this.state.deleteTypeErrorMessage.length) {
            // call the DELETE action call here
            this.props.deleteLabItemAction(
                this.props.match.params.labId, 
                this.props.match.params.itemId, 
                {
                    reason: this.state.deleteType,
                    note: this.state.note
                }
            );
        }
    }

    onFieldChange = (value, fieldName) => {
        this.setState({ [fieldName]: value });
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        this.setState({ 
            errorMessages: this.validateUpdatedItem()
        }, () => {
            this.updateItem();
        })

    }

    onResetHandler = () => {
        this.settingState();
    }

    onConfirmDeleteBtnClick = (e) => {
        e.preventDefault();
        this.setState({ 
            deleteTypeErrorMessage: this.validateDeleteOption()
        }, () => {
            this.deleteItem();
        })

    }

    onConfrimCancelBtnClick = () => {
        this.setState({
            isDialogOpen: false, 
            deleteType: '', 
            note: '', 
            deleteTypeErrorMessage: []
        })
    }

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.hasSchool}
                referer="/invites"
                statusCode="403"
                message="       "
                btnLabel="   "
            >
                <NotFoundItem
                    referer="/school-laboratory"
                >
                    <Form onSubmit={ this.onFormSubmit } >
                        <FormHeader 
                            label=" " 
                            icon="fas fa-chevron-left"
                            refererLabel=" "
                            referer={`/school-laboratory/${this.props.match.params.labId}`}    
                        />
                        <ContentPadding>
                            <Alert 
                                messages={this.state.errorMessages}
                                type="error"
                            />
                            <div 
                                className={`qr-sticker-constainer ${detectBrowser() === 'chrome' ? 'qr-sticker-constainer-chrome' : 'qr-sticker-constainer-not-chrome'}`}
                                ref={(el) => this.componentRef = el}
                            >
                                <div className="display-on-print right-side-logo">
                                    <img src={logo} alt="logo"/>
                                </div>
                                <div className="qr-sticker">
                                    <CustomeQRCode 
                                        url={`http://localhost:3000${this.props.match.url}`}
                                    />
                                    <ReactToPrint 
                                        trigger={() => <div className="print-btn no-display-on-print" >  <i className="fas fa-print"/></div>}
                                        content={() => this.componentRef}
                                    />
                                </div>
                                <div className="form-group-qr" >
                                    <div className="form-group">
                                        <InputField 
                                            id="id"
                                            name="id"
                                            type="text"
                                            locked={true}
                                            label=" "
                                            initial={true}
                                            initialValue={this.state.id}
                                            colSize="1"
                                            icon="fas fa-lock"
                                        />
                                    </div>
                                    <div className="form-group">
                                        <InputField 
                                            id="type"
                                            name="type"
                                            type="text"
                                            label=" "
                                            locked={true}
                                            initial={true}
                                            initialValue={this.state.itemType}
                                            colSize="1"
                                            icon="fas fa-lock"
                                        />
                                    </div>
                                    <div className="form-group no-display-on-print">
                                        <InputField 
                                            id="quantity"
                                            name="quantity"
                                            type="text"
                                            onChangeHandler={(e) => this.onFieldChange(e.target.value, 'quantity')}
                                            label=""
                                            initial={true}
                                            initialValue={this.state.quantity}
                                            colSize="1"
                                            icon="fas fa-sort-numeric-up-alt"
                                        />
                                    </div>
                                    <div className="form-group">
                                        <CustomDatePicker
                                            weekend={false}
                                            label="  "
                                            colSize="1"
                                            locked={!this.state.expireDate}
                                            placeholder={!this.state.expireDate ? ' ' : ''}
                                            initial={true}
                                            initialDate={this.state.expireDate}
                                            onChangeHandler={(expireDate) => this.onFieldChange(expireDate, 'expireDate')}
                                            minDate=
                                            { new Date().setDate(new Date().getDate() + 1) }
                                        />
                                    </div>
                                </div>
                            </div>
                            <ContentDivider />
                            <div className="form-group confirm-group margin-top-big">
                                <input className="btn submit-btn" type="submit" value=""/>
                                <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                                <input className="btn delete-btn" type="reset" value="" onClick={() => this.setState({isDialogOpen: true})}/>
                            </div>
                        </ContentPadding>
                    </Form>
                        {
                            this.state.isDialogOpen 
                            ? <ConfirmDialog
                                title=" "
                                onOutSideClick={this.onConfrimCancelBtnClick}
                                errorMessages={this.state.deleteTypeErrorMessage}
                            >
                                <div className="form-group">
                                    <CustomDropdown 
                                        label="  "
                                        colSize="1"
                                        title="  "
                                        list={DELETE_TYPES_LIST}
                                        onChange={(deleteType) => this.onFieldChange(deleteType, 'deleteType')}
                                    />
                                </div>
                                <div className="form-group">
                                    <TextArea 
                                        id="note"
                                        name="note"
                                        onChange={(e) => this.onFieldChange(e.target.value, 'note')}
                                        label=" "
                                        placeholder=":       "
                                        colSize="1"
                                    />
                                </div>
                                <ContentDivider />
                                <div className="form-group confirm-group margin-top-big">
                                    <input className="btn submit-btn" type="submit" value=" " onClick={this.onConfirmDeleteBtnClick}/>
                                    <input className="btn cancel-btn" type="reset" value="" onClick={this.onConfrimCancelBtnClick}/>
                                </div>

                            </ConfirmDialog>
                            : null    
                        }
                </NotFoundItem>
            </NotAllowed>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    // get the item from the reducer, if not exist, return undefined 
    let itemToView = state.labItems[ownProps.match.params.itemId];
    const { labId } = ownProps.match.params;

    // if the item lab Id not equal to the path, return undefined;
    if(itemToView && itemToView.labId.toString() !== labId) {
        itemToView = undefined;
    }

    return {
        hasSchool: state.auth.user.data.hasSchool,
        item: itemToView,
        status: state.alert.success
    }
}

export default connect(mapStateToProps, { 
    fetchLabItemAction,
    updateLabItemAction,
    deleteLabItemAction
})(LabItemView);
