import { 
    validateArrayOfEmptyObjects, 
    validateDuplicateItemsInArray, 
    validateEmpty, 
    validateEmptyPropObjectInArray, 
    validateIsPositiveNumberPropObjectInArray, 
    validateIsNumberPropObjectsInArray 
} from '../../../helpers/validate';

export const validateExperiment = (experiment, itemsFieldName) => {
    return [
        validateEmpty(experiment.title, '      '),
        validateEmpty(experiment.courseId, '     '),
        validateArrayOfEmptyObjects(experiment[itemsFieldName], { value: '', quantity: '' }, '       '),
        validateEmptyPropObjectInArray(experiment[itemsFieldName], 'quantity', '        '),
        validateIsNumberPropObjectsInArray(experiment[itemsFieldName], 'quantity', '        '),
        validateIsPositiveNumberPropObjectInArray(experiment[itemsFieldName], 'quantity', '            '),
        validateDuplicateItemsInArray(experiment[itemsFieldName], 'value', '        ')
    ].filter(message => message)
}
