import mylabs from '../../../../apis/mylabs';
import catchHandler from '../../../../helpers/catchHandler';
import loadingActions from '../../../utilsActions/loadingActions';
import alertActions from '../../../utilsActions/alertActions';
import history from '../../../../history';
import parseDateToString from '../../../../helpers/parseDate';


export const createLabItemAction = (item, labId) => {
    return async (dispatch) => {

        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make POST request to the API server
            const response = await mylabs.post(`/laboratory/${labId}/item`, {
                ...item,
                expireDate: item.hasExpireDate ? parseDateToString(item.expireDate) : ''
            });

            // send new created item to lab manager reducer
            dispatch({ type: 'CREATE_LAB_ITEM', payload: response.data.laboratoryItem });

            // removing loading spinner after success
            dispatch(loadingActions.remove());

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true,
                path: `/school-laboratory/${response.data.laboratoryItem.labId}/item/${response.data.laboratoryItem.id}`
            }));
            
        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}


export const fetchCurrentLabItemsAction = (labId, filterParams) => {
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());
    
            // make GET request to the API server
            const response = await mylabs.get(`/laboratory/${labId}/item`, {
                params: filterParams
            });

            // send the response to the reducers
            dispatch({ type: 'FETCH_LAB_ITEMS_TABLE', payload: response.data });

            // send fetched items to lab manager reducer
            dispatch({ type: 'FETCH_PAGED_LAB_ITEMS', payload: response.data.laboratoryItems });
    
            dispatch({ type: 'FETCH_FORM_ITEMS', payload: response.data.ministryItems })

            // removing loading spinner after success
            dispatch(loadingActions.remove());
            
        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}

