import { userConstants } from '../actions/types';

const semesterReducer = (state={}, action) => {
    switch (action.type) {
        case 'CLEAR_TABLE_LIST':
            return {};
        case 'FETCH_SEMESTERS':
            return { ...action.payload };
        case 'FETCH_ITEMS':
            return { ...action.payload };
        case 'FETCH_COURSES':
            return { ...action.payload }
        case 'FETCH_EXPERIMENTS':
            return { ...action.payload }
        case 'FETCH_CITIES':
            return { ...action.payload }
        case 'FETCH_SUPERVISORS_ACCOUNTS_TABLE':
            return { ...action.payload }
        case 'FETCH_PRACTICAL_REQUESTS_TABLE':
            return { ...action.payload };
        case 'FETCH_LAB_ITEMS_TABLE':
            return { ...action.payload };
        case 'FETCH_TABLE_QUESTION':
            return { ...action.payload };
        case userConstants.LOGOUT:
            return {};
        default: 
            return state;
    }
}

export default semesterReducer;