import React from 'react'

const ExtraInfo = ({ 
    boolCondition, 
    warningMessage, 
    errorMessage 
}) => {
    return (
        boolCondition ?
        <div className="extra-info">
            <p className="extra-info__highlight-error">
                { errorMessage }
                <i className="fas fa-asterisk" />
            </p>
            <p className="extra-info__highlight-warning">
                { warningMessage}
                <i className="fas fa-asterisk" />
            </p>
        </div>
        : null
    )
}

export default ExtraInfo
