// @ts-nocheck
import { connect } from 'react-redux';
import React, { Component, Fragment } from 'react'
import { Link } from 'react-router-dom';
import _ from 'lodash';
import moment from 'moment';

import CustomDropdown from 'components/Form/CustomDropdown';
import { Form } from 'components/Form/Form';
import FormHeader from 'components/Form/FormHeader';
import InputField from 'components/Form/InputField';
import ContentDivider from 'components/Utils/ContentDivider';
import ContentPadding from 'components/Utils/ContentPadding';
import NotAllowed from 'components/Utils/NotAllowed';
import NotFoundItem from 'components/Utils/NotFoundItem';
import { getLabelObject } from 'helpers/objectsHelpers';
import CustomDatePicker from 'components/Form/CustomDatePicker';
import { cancelPracticalExperimentRequestAction, fetchPracticalExperimentRequestAction, updatePracticalExperimentRequestAction } from 'actions/pagesActions/SchoolMember/ScienceTeacher/experimentsAction';
import { classesList } from 'helpers/fixedLists';
import { isNumberGreatThanZero, validateEmpty, validateEqualPropsObjects } from 'helpers/validate';
import Alert from 'components/Utils/Alert';
import parseDateToString, { parseDateForMoment } from 'helpers/parseDate';
import alertActions from 'actions/utilsActions/alertActions';
import TextArea from 'components/Form/TextArea';

class PracticalExperimentView extends Component {

    constructor(props) {
        super(props);
        this.labDropdownRef = React.createRef();
        this.classNoRef = React.createRef();
    }

    state = {
        id: '',
        courseName: '',
        experimentName: '',
        quantity: '',
        executeDate: '',
        createDate: '',
        confirmDate: '',
        labId: '',
        labManager: '',
        scienceTeacher: '',
        classNo: '',
        items_used: [],
        status: null,
        executed: null,
        evaluated: null,
        errorMessages: [],
    }

    async componentDidMount() {
        if(this.props.hasSchool && (!this.props.experiment || !this.props.labs.length)) {
            await this.props.fetchPracticalExperimentRequestAction(this.props.match.params.id);
        }
        this.settingState();
    }

    componentDidUpdate(nextProps) {
        if(!validateEqualPropsObjects(nextProps.experiment, this.props.experiment)) {
            this.settingState();
        }
    }

    settingState = () => {
        if(this.props.experiment && !validateEqualPropsObjects(this.props.experiment, {})) {
            this.setState({
                ...this.props.experiment,
                executeDate: new Date(this.props.experiment.executeDate),
                createDate: new Date(this.props.experiment.createDate),
                confirmDate: this.props.experiment.confirmDate ? new Date(this.props.experiment.confirmDate) : '',
                errorMessages: []
            }, () => {
                // setting the dropdown with current state values
                if(this.props.labs.length && this.props.experiment.status === null) {
                    this.labDropdownRef.current.selectItem(getLabelObject(this.state.labId, this.props.labs))
                    this.classNoRef.current.selectItem(getLabelObject(this.state.classNo, classesList))
                }
            })
        }
    }
    
    convertDateUTCString = (date) => {
        date = new Date(date);
        // removing GMT to compare date with the date coming from the server
        return new Date(Date.UTC(date.getFullYear(), date.getMonth(), date.getDate(), 0, 0, 0)).toUTCString();
    }

    validateExperiment = () => {
        return [
            isNumberGreatThanZero(this.state.quantity, 
                '       ',
                '      ,      ',
                '           '
            ),
           validateEmpty(this.state.executeDate, '      '),
           validateEmpty(this.state.labId, '      '),
           validateEmpty(this.state.classNo, '    ')
        ].filter(message => message)
    }

    updatingExperiment = () => {
        // upcoming 5 line, is only for comparison before making API call
        let currentProps = _.cloneDeep(this.state);
        currentProps = _.omit(currentProps, 'errorMessages');
        currentProps.executeDate = this.convertDateUTCString(currentProps.executeDate);
        currentProps.createDate = this.convertDateUTCString(currentProps.createDate);
        currentProps.confirmDate = currentProps.confirmDate ? this.convertDateUTCString(currentProps.confirmDate) : '';

        if(!this.state.errorMessages.length && !validateEqualPropsObjects(currentProps, this.props.experiment)) {
            this.props.updatePracticalExperimentRequestAction({
                ...this.state,
                requestId: this.state.id,
                executeDate: parseDateToString(this.state.executeDate),
                quantity: Number(this.state.quantity)
            })
        }
    }

    onFieldChange = (value, fieldName) => {
        this.setState({ [fieldName]: value });
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        this.props.clearAlert();
        
        this.setState({ 
            errorMessages: this.validateExperiment()
        }, () => {
            this.updatingExperiment();
        })
    }

    onResetHandler = (e) => {
        e.preventDefault();
        this.props.clearAlert();
        this.settingState();
    }

    onDeleteHandler = () => {
        this.props.cancelPracticalExperimentRequestAction(this.props.experiment.id);
    }

    renderUsedItems = () => {
        return this.state.items_used.map((item) => {
            return (
                <div className="form-group" key={item.value}>
                    <InputField
                        id="id"
                        name="id"
                        label={` `}
                        initial={true}
                        initialValue={item.label}
                        locked={true}
                        icon="fas fa-lock"
                        colSize="2"
                    />
                    <InputField
                        id="id"
                        name="id"
                        label=" "
                        initial={true}
                        initialValue={item.quantity}
                        locked={true}
                        icon="fas fa-lock"
                        colSize="2"
                    />
                </div>
            );
        })
    }

    renderLabAndClassFields = () => {
        if(this.state.status === null) {
            return (
                <div className="form-group">
                    <CustomDropdown 
                        label=""
                        title=" "
                        list={this.props.labs}
                        onChange={(labId) => this.onFieldChange(labId, 'labId')}
                        setRef={(ref) => this.labDropdownRef = ref}
                        colSize="2"
                    />
                    <CustomDropdown 
                        label=" "
                        title=" "
                        list={classesList}
                        setRef={(ref) => this.classNoRef = ref}
                        onChange={(classNo) => this.onFieldChange(classNo, 'classNo')}
                        colSize="2"
                    />
                </div>
            );
        } else {
            return (
                <div className="form-group">
                    <InputField 
                        label=""
                        initial={true}
                        initialValue={this.state.labId ? getLabelObject(this.state.labId, this.props.labs).label : ''}
                        locked={true}
                        icon="fas fa-lock"
                        colSize="2"
                    />
                    <InputField 
                        label=" "
                        initial={true}
                        initialValue={this.state.classNo ? getLabelObject(this.state.classNo, classesList).label : ''}
                        locked={true}
                        icon="fas fa-lock"
                        colSize="2"
                    />
                </div>
            );
        }
    }

    renderConfirmedFields = () => {
        if(this.state.status !== null) {        
            return (
                <Fragment>
                    <div className="form-group">
                        <InputField 
                            label=" "
                            initial={true}
                            initialValue={this.state.scienceTeacher}
                            locked={true}
                            icon="fas fa-lock"
                            colSize="2"
                        />
                        <InputField 
                            label=" "
                            initial={true}
                            initialValue={this.state.labManager}
                            locked={true}
                            icon="fas fa-lock"
                            colSize="2"
                        />
                    </div>
                    <div className="form-group">
                        <InputField
                            label="  "
                            initial={true}
                            initialValue={this.state.status ? '  ' : '  '}
                            locked={true}
                            icon={this.state.status === true ? 'fas fa-check' : 'fas fa-times'}
                            colSize="2"
                            className={this.state.status ? "color-primary" : 'color-danger' }
                        />
                        <CustomDatePicker 
                            label="  "
                            initial={true}
                            initialDate={this.state.confirmDate}
                            locked={true}
                            icon="fas fa-lock"
                            colSize="2"
                        />
                    </div> 
                </Fragment>
            );
        }
    }

    renderBtnsActions = () => {
        const momentCurrent = moment(parseDateForMoment(moment()._d), 'YYYY-MM-DD');
        const momentExecuteDate = moment(parseDateForMoment(this.state.executeDate), 'YYYY-MM-DD');

        if(this.state.status === null && momentCurrent.isBefore(momentExecuteDate)) {
            // show save changes button
            return (
                <ContentPadding>
                    <ContentDivider />
                    <div className="form-group confirm-group margin-top-big">
                        <input className="btn submit-btn" type="submit" value=""/>
                        <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                        <input className="btn delete-btn" type="reset" value=" " onClick={this.onDeleteHandler}/>
                    </div>
                </ContentPadding>
            );
        } else if(this.state.executed === true && momentCurrent.isSameOrAfter(momentExecuteDate) && !this.state.evaluated) {
            return (
                <ContentPadding>
                    <ContentDivider />
                    <div className="form-group confirm-group margin-top-big">
                        <Link to={`/practical-experiment/${this.props.match.params.id}/evaluate`} className="btn submit-btn no-margin-right link-btn" >  </Link>
                    </div>
                </ContentPadding>
            );
        }
        return null;
    }

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.hasSchool}
                referer="/invites"
                statusCode="403"
                message="       "
                btnLabel="   "
            >
                <NotFoundItem
                    referer="/"
                >
                    <Form onSubmit={this.onFormSubmit}>
                        <FormHeader 
                            label={`  `}
                            referer="/practical-experiment"
                            refererLabel=" "
                            icon="fas fa-chevron-left"
                        />
                        <ContentPadding>
                            <Alert messages={this.state.errorMessages} type="404" />
                            <div className="form-group">
                                <InputField
                                    id="id"
                                    name="id"
                                    label=" "
                                    initial={true}
                                    initialValue={this.state.id}
                                    locked={true}
                                    icon="fas fa-lock"
                                    colSize="2"
                                />
                                <CustomDatePicker 
                                    id="execute-date"
                                    name="execute-date"
                                    label="  "
                                    initial={true}
                                    initialDate={this.state.createDate}
                                    onChangeHandler={(createDate) => this.onFieldChange(createDate, 'createDate')}
                                    locked={true}
                                    colSize="2"
                                    minDate={new Date().setDate(new Date().getDate() + 2)}
                                />
                            </div>
                            { this.renderConfirmedFields() }
                            </ContentPadding>
                        <FormHeader 
                            label={` `}
                        />
                        <ContentPadding>
                            <div className="form-group">
                                <InputField 
                                    label=" "
                                    initial={true}
                                    initialValue={this.state.courseName}
                                    locked={true}
                                    icon="fas fa-lock"
                                    colSize="2"
                                />
                                <InputField 
                                    label=" "
                                    initial={true}
                                    initialValue={this.state.experimentName}
                                    locked={true}
                                    icon="fas fa-lock"
                                    colSize="2"
                                />
                            </div>
                            { this.renderLabAndClassFields() }
                            <div className="form-group">
                                <InputField
                                    id="quantity"
                                    name="quantity"
                                    label="  "
                                    initial={true}
                                    initialValue={this.state.quantity}
                                    locked={this.state.status !== null}
                                    onChangeHandler={(e) => this.onFieldChange(e.target.value, 'quantity')}
                                    icon="fas fa-sort-numeric-up-alt"
                                    colSize="2"
                                />
                                <CustomDatePicker 
                                    id="execute-date"
                                    name="execute-date"
                                    label="  "
                                    initial={true}
                                    initialDate={this.state.executeDate}
                                    locked={this.state.status !== null}
                                    onChangeHandler={(executeDate) => this.onFieldChange(executeDate, 'executeDate')}
                                    colSize="2"
                                    minDate={new Date().setDate(new Date().getDate() + 2)}
                                />
                            </div>
                            { this.state.status !== null ?
                            <div className="form-group margin-bottom">
                                <TextArea 
                                    label={this.state.reqStatus === null ? " " : "  "}
                                    locked={this.state.reqStatus !== null}
                                    initial={true}
                                    initialValue={!this.state.confirmNote && (this.state.reqStatus || this.state.reqStatus === false) ? ' ' : this.state.confirmNote}
                                    onChangeHandler={this.onNoteChange}
                                    placeholder=":       "
                                    colSize="1"
                                />
                            </div>
                            : null 
                            }
                        </ContentPadding>
                        { this.state.status === true ? 
                            <Fragment>
                                <FormHeader 
                                    label="   "
                                />
                                <ContentPadding>
                                    { this.renderUsedItems() }
                                </ContentPadding>
                            </Fragment>
                            : null
                        }
                    { this.state.executeDate ? this.renderBtnsActions() : null }
                    </Form>
                </NotFoundItem>
            </NotAllowed>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
        experiment: state.experiments[ownProps.match.params.id],
        labs: Object.values(state.laboratories)
    }
}

export default connect(mapStateToProps, { 
    fetchPracticalExperimentRequestAction,
    updatePracticalExperimentRequestAction,
    cancelPracticalExperimentRequestAction,
    clearAlert: alertActions.clear
})(PracticalExperimentView);