// @ts-nocheck
import { fetchCurrentVisitLaboraotryManagerDashboardAction } from 'actions/pagesActions/EducationalSupervisor/dashboardAction';
import Card from 'components/Dashboard/Card';
import CardContainer from 'components/Dashboard/CardContainer';
import ChartContainer from 'components/Dashboard/ChartContainer';
import List from 'components/Dashboard/List';
import LoadingIndicator from 'components/Dashboard/LoadingIndicator';
import OverviewMetric from 'components/Dashboard/OverviewMetric';
import ContentPadding from 'components/Utils/ContentPadding';
import { convertActivityDashboardStringToDate, countingActivityDashboardYears, rateColors } from 'helpers/dashboard';
import { isSameProps } from 'helpers/objectsHelpers';
import { validateEqualPropsObjects } from 'helpers/validate';
import React, { Component } from 'react'
import Chart from "react-google-charts";
import { connect } from 'react-redux';

const EXPERIMENTS_COLUMNS_HEADERS = [
    {
        label: ' ',
        key: 'qunatity'
    },
    {
        label: ' ',
        key: 'experimentExecuted'
    }
];

class LaboratoryManagerDashboard extends Component {

    state = {
        fromDate: '',
        toDate: ''
    }

    componentDidMount () {
        this.props.fetchCurrentVisitLaboraotryManagerDashboardAction(this.props.match.params.id);    
    }

    render() {
        return (
            <ContentPadding>
                <div className="dashboard">
                    { /* <DateFilter /> */ }
                    <div className="dashboard-flex">
                        <div className="dashboard-flex__50">
                            { /* Figure-out some chart */ }
                            <CardContainer>
                                <Card
                                    title=""
                                >
                                    <span>
                                        <span className={`${rateColors(this.props.dashboard.rate)}`} > {this.props.dashboard.rate}</span> 
                                        <span className="rate-separator">/ 5 </span> 
                                    </span>
                                </Card>
                                <Card
                                    title=""
                                >
                                    <span > { this.props.dashboard.numberLabs } </span>
                                </Card>
                            </CardContainer>
                            <ChartContainer 
                                title=" "
                                className="dashboard-chart-area dashboard-chart-area-row-span-list"
                            >
                                <Chart
                                    width={'720px'}
                                    height={'100%'}
                                    chartType="AreaChart"
                                    loader={<LoadingIndicator />}
                                    data={[
                                        ['', '', ''],
                                        ...this.props.dashboard.itemActivity
                                    ]}
                                    options={{
                                        // For the legend to fit, we make the chart area smaller
                                        chartArea: { width: '70%', height: '70%' },
                                        // lineWidth: 25
                                        colors: ['#0FA591' ,'#C0392B']
                                    }}
                                    // For tests
                                    rootProps={{ 'data-testid': '1' }}
                                />
                            </ChartContainer>
                        </div>
                        <div className="dashboard-flex__50">
                            <OverviewMetric 
                                title=""
                                metricsData={[
                                    { label: '', value: this.props.dashboard.totalExperiments },
                                    { label: '', value: this.props.dashboard.approvedExperiments, color: 'color-primary' },
                                    { label: '', value: this.props.dashboard.notApprovedExperiments, color: 'color-danger' },
                                ]}
                            />
                            <ChartContainer
                                title=" "
                                className="dashboard-chart-pie"
                            >
                                <Chart
                                    height={'260px'}
                                    chartType="PieChart"
                                    loader={<LoadingIndicator />}
                                    data={[
                                        [' ', ''],
                                        ['', this.props.dashboard.approvedExperiments],
                                        ['', this.props.dashboard.notApprovedExperiments],
                                    ]}
                                    options={{
                                        // Just add this option
                                        pieHole: 0.3,
                                        colors: ['#0FA591' ,'#C0392B']
                                    }}
                                    rootProps={{ 'data-testid': '3' }}
                                />
                            </ChartContainer>
                            <List 
                                title=' '
                                colHeaders={EXPERIMENTS_COLUMNS_HEADERS}
                                data={this.props.dashboard.numberExecuteExperiment}
                            />
                        </div>

                    </div>
                    <ChartContainer 
                        title=" "
                        className="dashboard-chart-calender"
                    >
                        <Chart
                            width={950}
                            height={170* countingActivityDashboardYears(this.props.dashboard.numberExecutedDate)}
                            chartType="Calendar"
                            loader={<LoadingIndicator />}
                            data={[
                                [{ type: 'date', id: 'Date' }, { type: 'number', id: 'Won/Loss' }],
                                ...convertActivityDashboardStringToDate(this.props.dashboard.numberExecutedDate),
                            ]}
                            options={{
                                noDataPattern: {
                                    backgroundColor: '#eee',
                                    color: '#ddd'
                                },
                                colorAxis: {
                                    minValue: 1,
                                    maxValue: 7,
                                    colors: ['#87d2c8', '#096357']
                                } 
                            }}
                            rootProps={{ 'data-testid': '1' }}
                        />
                        
                    </ChartContainer>
                </div>
            </ContentPadding>
        )
    }
}

const mapStateToProps = state => {

    let dashboard = validateEqualPropsObjects(state.dashboard, {}) ? undefined : state.dashboard;

    if(dashboard && !isSameProps(dashboard, LaboratoryManagerDashboard.defaultProps.dashboard)) {
        dashboard = undefined;
    }

    return {
        dashboard: dashboard
    }
}

LaboratoryManagerDashboard.defaultProps = {
    dashboard: {
        approvedExperiments: 0,
        endSemester: "",
        itemActivity: [["", 0, 0]],
        notApprovedExperiments: 0,
        numberExecuteExperiment: [],
        numberExecutedDate: [],
        numberLabs: 0,
        rate: 0.00,
        startSemester: "",
        totalExperiments: 0
    }
}

export default connect(mapStateToProps, {
    fetchCurrentVisitLaboraotryManagerDashboardAction
})(LaboratoryManagerDashboard);