// @ts-nocheck
import React, { Component } from 'react'

import CustomDropdown from 'components/Form/CustomDropdown';
import { Form } from 'components/Form/Form';
import FormHeader from 'components/Form/FormHeader';
import InputField from 'components/Form/InputField';
import Alert from 'components/Utils/Alert';
import ContentDivider from 'components/Utils/ContentDivider';
import ContentPadding from 'components/Utils/ContentPadding';
import { evaluateTypes } from 'helpers/fixedLists';
import { validateEqualPropsObjects } from 'helpers/validate';
import { deleteQuestionAction, fetchQuestionAction, updateQuestionAction } from 'actions/pagesActions/SystemAdministrator/questionAction';
import { connect } from 'react-redux';
import NotFoundItem from 'components/Utils/NotFoundItem';
import { validateQuestion } from './validateQuestion';
import _ from 'lodash';

class QuestionAdd extends Component {

    constructor(props) {
        super(props);
        this.evaluateDropdownRef = React.createRef();
    }

    state = {
        text: '',
        evaluateType: 0,
        errorMessages: []
    }

    async componentDidMount() {
        if(!this.props.question) {
            await this.props.fetchQuestionAction(this.props.match.params.id);
        }
        this.settingState();
    }

    settingState = () => {
        if(this.props.question) {
            this.setState({ ...this.props.question }, () => {
                const evaluateType = evaluateTypes.find(evaluate => evaluate.value === this.props.question.evaluateType );
                this.evaluateDropdownRef.current.selectSingleItem(evaluateType);
            });
        }
    }

    updateQuestion = async () => {
        const originalQuestion = this.props.question;
        const updatedQuestion = _.omit(this.state, 'errorMessages');
        if(!this.state.errorMessages.length && 
            !validateEqualPropsObjects(originalQuestion, updatedQuestion)) {
            // API call here
            await this.props.updateQuestionAction(this.state);
            this.settingState();
        }
    }

    onFieldChangeHandler = (value, fieldName) => {
        this.setState({ [fieldName]: value });
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        this.setState({ errorMessages: validateQuestion(this.state)
        }, () => {
            this.updateQuestion();
        })
    }

    onResetHandler = () => {
        this.settingState();
    }

    onDeleteHandler = () => {
        this.props.deleteQuestionAction(this.props.match.params.id);
    }

    render() {
        return (
            <NotFoundItem
                referer="/"
            >
                <Form onSubmit={this.onFormSubmit}>
                    <FormHeader 
                        label=" " 
                        icon="fas fa-chevron-left"
                        referer="/question"
                        refererLabel=" "
                    />
                    <ContentPadding>
                        <Alert messages={this.state.errorMessages} type="error" />
                        <div className="form-group">
                            <InputField
                                id="text"
                                name="text"
                                type="text"
                                initial={true}
                                initialValue={this.state.text}
                                locked={true}
                                label=" "
                                placeholder=":        "
                                icon="fas fa-signature"
                                colSize="2"
                            />
                            <CustomDropdown 
                                colSize="2"
                                onChange={(selectedEvaluate) => this.onFieldChangeHandler(selectedEvaluate, 'evaluateType')}
                                list={evaluateTypes}
                                title='  '
                                label=" "
                                setRef={(ref) => this.evaluateDropdownRef = ref}
                            />
                        </div>
                        <ContentDivider />
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value=""/>
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                            <input className="btn delete-btn" type="reset" value="" onClick={this.onDeleteHandler}/>
                        </div>
                    </ContentPadding>
                </Form>
            </NotFoundItem>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        question: state.adminQuestions[ownProps.match.params.id]
    }
}

export default connect(mapStateToProps, {
    fetchQuestionAction,
    updateQuestionAction,
    deleteQuestionAction
})(QuestionAdd);