import alertActions from './utilsActions/alertActions';
import { userConstants } from './types';
import history from '../history';
import mylabs from '../apis/mylabs';
import catchHandler from '../helpers/catchHandler';
import loadingActions from './utilsActions/loadingActions';

export const loginAction = (formValues) => {
    return async (dispatch, getState) => {
        // create LOGIN_REQUEST, to have login indicator spinner
        dispatch(request());
        try {

            // create action for isLoading
            dispatch(loadingActions.show());

            // make API call
            const response = await mylabs.post('/login', {...formValues});
            
            // remove action for isLoading
            dispatch(loadingActions.remove());

            // create LOGIN_SUCCES action to stop spinner
            dispatch(success(response.data.token));

            // remove alerts from the state
            dispatch(alertActions.clear());

            // go to where the user intended to navigate to, such as bookmark or history
            // if he have no specific, route him to '/' 
            history.push(getState().referer);

        } catch(err) {
            dispatch(failure());
            catchHandler(dispatch, err);
        }
    }
    function request() { return { type: userConstants.LOGIN_REQUEST }};
    function success(token) { return { type: userConstants.LOGIN_SUCCESS, token }};
    function failure() { return { type: userConstants.LOGIN_FAILURE }}
}


export const registerAction = (formValues) => {
    return async (dispatch, getState) => {
        
        // create REGISTER_REQUEST, to have register indicator spinner
        dispatch(request());

        try {

            // create action for isLoading
            dispatch(loadingActions.show());

            // make API call
            const response = await mylabs.post('/register', { ...formValues });

            // remove action for isLoading
            dispatch(loadingActions.show());


            if(response.data.hasToken) {
                // if hasToken === true, navigate him to the '/'
                dispatch(success(response.data.token));
                dispatch(alertActions.clear());
                dispatch({ type: userConstants.LOGIN_SUCCESS, token: response.data.token });
                history.push('/');
            } else {
                // no token, go to Login after register
                dispatch(success());
                history.push('/login');
            }

        } catch(err) {
            dispatch(failure());
            catchHandler(dispatch, err);
        }
    }

    function request() {return { type: userConstants.REGISTER_REQUEST }}
    function success(token='') {return { type: userConstants.REGISTER_SUCCESS, token }}
    function failure() { return { type: userConstants.REGISTER_FAILURE }}
}

export const forgetPasswordAction = (email) => {
    return async (dispatch) => {
        try {
            // make API call
            await mylabs.post('/forget-pass', { email });

            // remove alerts from the state
            dispatch(alertActions.clear());

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "          ",
                code: 200,
                success: true
            }));

        } catch(err) {
            catchHandler(dispatch, err);
        }
    }
}

export const resetPasswordAction = (password, confirmedPassword, token) => {
    return async (dispatch) => {
        try {
            // make API call
            const response = await mylabs.post(`/reset-pass`, { password, confirmedPassword, token });

            // remove alerts from the state
            dispatch(alertActions.clear());

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "    ,    ",
                code: 200,
                success: true
            }));

            return response.data;

        } catch(err) {
            catchHandler(dispatch, err);
        }
    }
}

export const createDefectReportAction = (report) => {
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make PATCH request to the API server
            await mylabs.post(`/defect-report`, { ...report });

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "   ,              ",
                code: 200,
                success: true
            }));

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}

export const logoutAction = () => {
    // create action to remove JWT token on logout
    return {
        type: userConstants.LOGOUT
    }
}
