import React, { Fragment } from 'react'
import { Route } from 'react-router'

import Tab, { TabElement } from 'components/Utils/Tab'
import CityCreate from './CityCreate'
import CityList from './CityList'

function CityTab({ match }) {
    return (
        <Fragment>
            <Tab>
                <TabElement 
                    to="/city/create"
                    match={match}
                    title=" "
                />
                <TabElement 
                    to="/city"
                    match={match}
                    title=" "
                />
            </Tab>
            <Route 
                path="/city/create"
                exact
                component={CityCreate}
            />
            <Route 
                path="/city"
                exact
                component={CityList}
            />
        </Fragment>
    )
}

export default CityTab
