// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import Form from '../../../components/Form/Form';
import FormHeader from '../../../components/Form/FormHeader';
import InputField from '../../../components/Form/InputField';
import Alert from '../../../components/Utils/Alert';
import ContentPadding from '../../../components/Utils/ContentPadding';
import { validateEmpty } from '../../../helpers/validate';
import ContentDivider from '../../../components/Utils/ContentDivider';
import TextArea from '../../../components/Form/TextArea';
import { createInviteAction, fetchInvitesAction, cancelInviteAction } from '../../../actions/pagesActions/SchoolManager/invitesAction';
import NotAllowed from '../../../components/Utils/NotAllowed';
import PreviewListContainer from '../../../components/List/PreviewList/PreviewListContainer';
import ConfirmPreviewList from '../../../components/List/PreviewList/ConfirmPreviewList';
import alertActions from '../../../actions/utilsActions/alertActions';

class InviteMember extends Component {

    constructor(props) {
        super(props);
        this.state = {
            memberId: '',
            message: '',
            errorMessages: []
        }

        this.baseState = { ...this.state };
    }

    async componentDidMount() {
        if(this.props.invites.length === 0){
            // if invites empty fetch them from the API server
            await this.props.fetchInvitesAction();
            // in case the invites are fetched empty, remove the message from the server, user doesnt whant to see it
        }
    }

    validateInvite = () => {
        return [
            validateEmpty(this.state.memberId, '      ')
        ].filter(message => message)
    }

    sendInvite = async () => {
        if(this.state.errorMessages.length === 0) {
            // MAKE API CALL HERE 
            await this.props.createInviteAction({
                memberId: this.state.memberId,
                message: this.state.message
            })
            
            if(this.props.status) {
                // after success api call, reset fields
                this.onResetHandler();
            }
        }
    }

    onFieldChangeHandler = (e) => {
        const { name, value } = e.target;
        this.setState({ [name]: value });
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        this.props.clearAlert();

        // performing validation and setting messages
        this.setState({
            errorMessages: this.validateInvite()
        }, () => {
            this.sendInvite()
        });
    }

    onResetHandler = () => {
        // reset form values
        this.setState({ ...this.baseState });
    }

    onRejectHandler = (inviteId) => {
        // call cancel invite action
        this.props.cancelInviteAction(inviteId);
    } 

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.hasSchool}
                referer="/"
                message="     "
                statusCode="403"
                btnLabel="  "
            >
                <Form onSubmit={this.onFormSubmit}>
                    <FormHeader label=" " />
                    <ContentPadding>
                        <Alert messages={this.state.errorMessages} type="error" />
                        <div className="form-group">
                            <InputField
                                id="memberId"
                                name="memberId"
                                type="text"
                                onChangeHandler={this.onFieldChangeHandler}
                                initial={true}
                                initialValue={this.state.memberId}
                                label="   "
                                placeholder=": 1014"
                                icon="fas fa-signature"
                                colSize="1"
                            />
                        </div>
                        <div className="form-group">
                            <TextArea 
                                id="message"
                                name="message"
                                type="text"
                                initial={true}
                                initialValue={this.state.message}
                                onChangeHandler={this.onFieldChangeHandler}
                                label=" "
                                colSize="1"
                                placeholder=":        "
                            />
                            
                        </div>
                        <ContentDivider />
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value=""/>
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                        </div>
                    </ContentPadding>
                </Form>
                <PreviewListContainer>
                    <ConfirmPreviewList 
                        listHeader=" "
                        list={this.props.invites}
                        className="full"
                        rejectAction={this.onRejectHandler}
                        rejectLabel=" "
                        emptyMessage="   "
                    />
                </PreviewListContainer>
            </NotAllowed>
        )
    }
}

const mapStateToProps = state => {
    
    return {
        hasSchool: state.auth.user.data.hasSchool,
        invites: Object.values(state.invites),
        status: state.alert.success
    }
}

export default connect(mapStateToProps, { 
    createInviteAction, 
    fetchInvitesAction, 
    cancelInviteAction,
    clearAlert: alertActions.clear
})(InviteMember);
