// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

import InputField from 'components/Form/InputField';
import PublicForm from 'components/Form/PublicForm';
import Alert from 'components/Utils/Alert';
import { validateEmail } from 'helpers/validate';
import { forgetPasswordAction } from 'actions/userActions';

class ForgetPass extends Component {

    constructor(props) {
        super(props);
        this.baseState = { ...this.state };
    }

    state = {
        email: '',
        errorMessages: []
    }

    validateEmail = () => {
        return [
            validateEmail(this.state.email)
        ].filter(message => message);
    }

    createForgetPassToken = async () => {
        if(!this.state.errorMessages.length) {
            // API action call here
            await this.props.forgetPasswordAction(this.state.email)
            this.setState({ ...this.baseState });
        }
    }

    onFieldChange = (value, fieldName) => {
        this.setState({ [fieldName]: value })
    }

    onForgetPassSubmit = (e) => {
        e.preventDefault();
        this.setState({
            errorMessages: this.validateEmail()
        }, () => {
            this.createForgetPassToken();
        })
    }

    render() {
        return (
            <PublicForm 
                onSubmit={this.onForgetPassSubmit} 
                type="login"
            >
                <Alert messages={[
                    '.     ',
                    '               '
                ]} type="info" />
                <Alert messages={[...this.state.errorMessages]} type="error" />
                <Alert messages={[this.props.alert.message]} type={this.props.alert.code} />
                <div className="form-group">
                    <InputField
                        name="email"
                        onChangeHandler={(e) => this.onFieldChange(e.target.value, 'email')}
                        id="email"
                        initial={true}
                        initialValue={this.state.email}
                        label=" "
                        placeholder="example@gmail.com"
                        icon="fas fa-at"
                        colSize="1"
                    />
                </div>
                <div className="form-group">
                    <div className="form-group__1-col form-group__1-col--center">
                        <input type="submit" className="form__submit" value=" "/>
                    </div>
                </div>

                <div className="form-group margin-top">
                    <div className="form-group__1-col form-group__1-col--center">
                        <Link className="btn btn--white" to="/login">  </Link>
                    </div>
                </div>
            </PublicForm>
        );
    }
}

const mapStateToProps = state => {
    return {
        alert: state.alert
    }
}

export default connect(mapStateToProps, {
    forgetPasswordAction,

})(ForgetPass);