// @ts-nocheck
import React, { Component, Fragment } from 'react'

import InputField from 'components/Form/InputField';
import PublicForm from 'components/Form/PublicForm';
import Alert from 'components/Utils/Alert';
import { validateMatchedPasswords, validatePassword } from 'helpers/validate';
import { resetPasswordAction } from 'actions/userActions';
import { connect } from 'react-redux';
import history from '../../history';

class ResetPass extends Component {

    state = {
        password: '',
        confirmedPassword: '',
        reseted: false,
        navigatingTimer: 5,
        errorMessages: []
    }

    validatePassword = () => {
        return [
            validatePassword(this.state.password),
            validateMatchedPasswords(this.state.password, this.state.confirmedPassword)
        ].filter(message => message);
    }

    resetPassword = async () => {
        if(!this.state.errorMessages.length) {
            // reset call
            const { password, confirmedPassword } = this.state;
            const { token } = this.props.match.params;
            const response = await this.props.resetPasswordAction(password, confirmedPassword, token);
            if(response) {
                this.setState({ reseted: true }, () => {
                    setInterval(() => this.setState({ navigatingTimer: this.state.navigatingTimer - 1 }), 1000);
                    setTimeout(() => history.push('/login'), 5000);
                });
            }
        }
    }

    onFieldChange = (value, fieldName) => {
        this.setState({ [fieldName]: value });
    }

    onResetPassSubmit = (e) => {
        e.preventDefault();
        this.setState({
            errorMessages: this.validatePassword()
        }, () => {
            this.resetPassword();
        })
    }

    renderReset = () => {
        if(!this.state.reseted) {
            return (
                <Fragment>
                    <Alert messages={[...this.state.errorMessages, this.props.alert.message]} type="error" />
                    <div className="form-group">
                        <InputField
                            name="password"
                            type="password"
                            onChangeHandler={(e) => this.onFieldChange(e.target.value, 'password')}
                            id="password"
                            label="  "
                            icon="fas fa-key"
                            colSize="1"
                        />
                    </div>
                    <div className="form-group">
                        <InputField
                            name="confirmedPassword"
                            type="password"
                            onChangeHandler={(e) => this.onFieldChange(e.target.value, 'confirmedPassword')}
                            id="confirmedPassword"
                            label="  "
                            icon="fas fa-key"
                            colSize="1"
                        />
                    </div>
                    <div className="form-group margin-top">
                        <div className="form-group__1-col form-group__1-col--center">
                            <input type="submit" className="form__submit" value="  "/>
                        </div>
                    </div>
                </Fragment>
            );
        } else {
            return (
                <div className="reseted-info">
                        ,        {this.state.navigatingTimer} 
                </div>
            );
        }
    }

    render() {
        return (
            <PublicForm 
                onSubmit={this.onResetPassSubmit} 
                type="login"
            >
                { this.renderReset() }
            </PublicForm>
        )
    }
}

const mapStateToProps = state => {
    return {
        alert: state.alert
    }
}

export default connect(mapStateToProps, {
    resetPasswordAction
})(ResetPass);