// @ts-nocheck
import React, { Component } from 'react';
import { connect } from 'react-redux';

import ListLayout from '../../../components/List/ListLayout';
import PaginatedList from '../../../components/List/PaginatedList';
import SearchFilter from '../../../components/List/SearchFilter';
import { fetchSemestersAction } from '../../../actions/pagesActions/SystemAdministrator/semesterActions';
import parseDateToString, { onMinDateChange } from '../../../helpers/parseDate';
import InputField from '../../../components/Form/InputField';
import CustomDatePicker from '../../../components/Form/CustomDatePicker';
import { clearTableListAction } from '../../../actions/utilsActions/clearTableListAction';
import alertActions from '../../../actions/utilsActions/alertActions';
import DropMenu from 'components/Form/DropMenu';

const COLUMN_SEMESTER_HEADERS = [
    {
        label: '',
        key: 'id'
    },
    {
        label: ' ',
        key: 'title'
    },
    {
        label: ' ',
        key: 'startDate'
    },
    {
        label: ' ',
        key: 'endDate'
    },
    {
        label: '',
        key: 'path'
    }
];

class SemestersList extends Component {
    
    constructor(props) {
        super(props);
        this.baseState = { ...this.state };
    }

    state = { 
        page: 1,
        id: '',
        title: '',
        fromStartDate: '',
        toStartDate: '',
        fromEndDate: '',
        toEndDate: ''
    };

    convertParamsDateToString = () => {
        // convert dates into strings for API server to understand it through URL
        const newParams = { ...this.state };
        newParams.fromStartDate = newParams.fromStartDate ? parseDateToString(newParams.fromStartDate) : '';
        newParams.toStartDate = newParams.toStartDate ? parseDateToString(newParams.toStartDate) : '';
        newParams.fromEndDate = newParams.fromEndDate ? parseDateToString(newParams.fromEndDate) : '';
        newParams.toEndDate = newParams.toEndDate ? parseDateToString(newParams.toEndDate) : '';
        return newParams;
    }

    convertStringToDate = (semesters=[]) => {
        // Convert string to date, to display it in dd/MM/yyyy format without time
        return semesters.map(semester => {
            semester.startDate = parseDateToString(new Date(semester.startDate));
            semester.endDate = parseDateToString(new Date(semester.endDate));
            return semester
        });
    }

    onPageChange = (currentPage) => {

        // clear alerts, becasue when item deleted, it will be send here
        this.props.clearAlert();

        // set the currentPage, and make API call after that
        this.setState({ page: currentPage.selected+1 }, () => {
            // this call will be invoked when compnent first render, becasue of initial page value
            this.props.fetchSemestersAction(this.convertParamsDateToString());
        });
    }

    onFiltersChange = (value, fieldName, toDateFieldName='') => {
        switch (fieldName) {
            case 'fromStartDate':
            case 'fromEndDate':
                if(this.state[toDateFieldName] && value.getTime() >= this.state[toDateFieldName].getTime()) {
                    // reset the 'to' date becasue it maybe become before the 'from' date
                    this.setState({ [fieldName]: value, [toDateFieldName]: '' });
                    return;
                }
                this.setState({ [fieldName]: value });
                break;
            default:
                this.setState({ [fieldName]: value });
                break;
        }
    }

    onFilterSubmit = (e) => {
        // after submit, set the filter submit params, and after that make API request
        e.preventDefault();
        this.setState({ page: 1 }, () => {
            this.props.fetchSemestersAction(this.convertParamsDateToString());
        })
    }

    onFilterReset = () => {
        this.setState({ ...this.baseState });
    }

    render() {
        return (
            <ListLayout>
                <PaginatedList 
                    totalPages={this.props.totalPages}
                    data={this.convertStringToDate(this.props.semesters)}
                    onPageChange={this.onPageChange}
                    colHeaders = {COLUMN_SEMESTER_HEADERS}
                    initialPage={this.state.page - 1}
                />
                    <SearchFilter onSubmit={this.onFilterSubmit}>
                        
                        <div className="from-group">
                            <InputField
                                id="id"
                                name="id"
                                initial={true}
                                initialValue={this.state.id}
                                onChangeHandler={(e) => this.onFiltersChange(e, 'id')}
                                colSize="1"
                                placeholder="  "
                                icon="fas fa-search"
                            />
                        </div>
                        <div className="from-group">
                            <InputField
                                id="title"
                                name="title"
                                initial={true}
                                initialValue={this.state.title}
                                onChangeHandler={(e) => this.onFiltersChange(e, 'title')}
                                colSize="1"
                                placeholder="  "
                                icon="fas fa-search"
                            />
                        </div>
                        <div className="form-group">
                            <DropMenu 
                                colSize="1"
                                title=" "
                            >
                                <CustomDatePicker 
                                    colSize="1"
                                    placeholder=" "
                                    onChangeHandler={(newDate) => this.onFiltersChange(newDate, 'fromStartDate', 'toStartDate') }
                                    initial={true}
                                    initialDate={this.state.fromStartDate}
                                />
                                <CustomDatePicker 
                                    colSize="1"
                                    placeholder=" "
                                    onChangeHandler={(newDate) => this.onFiltersChange(newDate, 'toStartDate') }
                                    initial={true}
                                    initialDate={this.state.toStartDate}
                                    minDate={onMinDateChange(this.state.fromStartDate)}
                                />
                            </DropMenu>
                        </div>
                        <div className="form-group">
                            <DropMenu 
                                colSize="1"
                                title=" "
                            >
                                <CustomDatePicker 
                                    colSize="1"
                                    placeholder=" "
                                    onChangeHandler={(newDate) => this.onFiltersChange(newDate, 'fromEndDate', 'toEndDate') }
                                    initial={true}
                                    initialDate={this.state.fromEndDate}
                                />
                                <CustomDatePicker 
                                    colSize="1"
                                    placeholder=" "
                                    onChangeHandler={(newDate) => this.onFiltersChange(newDate, 'toEndDate') }
                                    initial={true}
                                    initialDate={this.state.toEndDate}
                                    minDate={onMinDateChange(this.state.fromEndDate)}
                                />
                            </DropMenu>
                        </div>
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value="" />
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onFilterReset} />
                        </div>
                    </SearchFilter>
            </ListLayout>
        )
    }

    componentWillUnmount () {
        // clear the table list in redux store when unmount
        this.props.clearTableListAction();
    }

}

const mapStateToProps = state => {
    return {
        semesters: state.tableList.semesters,
        totalPages: state.tableList.totalPages
    }
}

export default connect(mapStateToProps, { 
    fetchSemestersAction, 
    clearTableListAction, 
    clearAlert: alertActions.clear 
})(SemestersList);
