// @ts-nocheck
import React, { useState, useEffect } from 'react'

const TextArea = ({ 
    name, 
    label, 
    requiredInput, 
    onChangeHandler, 
    type, 
    id, 
    placeholder, 
    colSize, 
    initialValue,
    locked,
    initial 
}) => {

    const [text, setText] = useState('');
    const [limitClassName, setLimitClassName] = useState('');

    useEffect( () => {
        // When the initialValue change, re-render the component
        if(initial) {
            // This useEffect will make change only if the initial props set to true
            setText(initialValue);
        } 
    }, [initialValue, initial]);

    const onChangeText = (e) => {
        if(e.target.value.length <= 250) {
            setText(e.target.value);
            onChangeHandler(e);
            if(limitClassName) {
                setLimitClassName('');
            }
        }
        if(e.target.value.length === 250) {
            setLimitClassName('form__text-area--full-limit');
        }
    }

    const renderLabel = () => {
        return (
            label ? <label htmlFor={id} className="form__label"> {label} </label> : null 
        );
    }

    return (
        <div className={`form-group__${colSize}-col`}>
            { renderLabel() }
            <div className="form-control">
                <textarea 
                    name={name}
                    id={id}
                    type={type} 
                    className="form__text-area"
                    value={text}
                    onChange={onChangeText}
                    disabled={locked ? locked : false}
                    required={requiredInput}
                    placeholder={placeholder}
                />
            </div>
            {locked ? null : <div className="form__text-area--min-limit"> <span className={limitClassName}>{text.length}</span> / 250</div> }
        </div>
    )
}

export default TextArea;
