import mylabs from '../../../apis/mylabs';
import catchHandler from '../../../helpers/catchHandler';
import loadingActions from '../../utilsActions/loadingActions';
import alertActions from '../../utilsActions/alertActions';

export const createInviteAction = (invite) => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to patch
            const response = await mylabs.post('/invite-member', { ...invite })

            // send to the invites reducer
            dispatch({ type: 'CREATE_INVITE', payload: { ...response.data.invitaion } });

            // remove the loading indicator
            dispatch(loadingActions.remove());

            // message to the user that the school is created
            dispatch(alertActions.success({
                message: "   ",
                code: 200,
                success: true
            }));


        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}

export const fetchInvitesAction = () => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to get
            const response = await mylabs.get('/invite-member')

            // send to the invites reducer
            dispatch({ type: 'FETCH_INVITES', payload: response.data.invitations });

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}

export const cancelInviteAction = (inviteId) => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to delete
            await mylabs.delete(`/invite-member/${inviteId}`);

            // send to the invites reducer
            dispatch({ type: 'DELETE_INVITE', payload: inviteId });

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch (err) {
            
            if(err.message === "404 Not Found:     ") {
                // send to the invites reducer, in case the school member accept before canceling the invite
                dispatch({ type: 'DELETE_INVITE', payload: inviteId });
            }
            catchHandler(dispatch, err);
            
            
        }
    }
}
