// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import alertActions from '../../../actions/utilsActions/alertActions';
import CustomDropdown from '../../../components/Form/CustomDropdown';
import Form from '../../../components/Form/Form';
import FormHeader from '../../../components/Form/FormHeader';
import InputField from '../../../components/Form/InputField';
import Alert from '../../../components/Utils/Alert';
import ContentPadding from '../../../components/Utils/ContentPadding';
import { validateEmpty } from '../../../helpers/validate';
import ContentDivider from '../../../components/Utils/ContentDivider';
import { createSchoolAction, fetchCreateSchoolRequirementsAction } from '../../../actions/pagesActions/SchoolManager/schoolAction';
import { getLabelObject } from '../../../helpers/objectsHelpers';
import NotAllowed from '../../../components/Utils/NotAllowed';

class SchoolCreate extends Component {

    constructor(props) {
        super(props);
        this.cityDropdownRef = React.createRef();
        this.areasDropdownRef = React.createRef();

        this.state = {
            schoolId: '',
            name: '',
            cityId: '',
            areaId: '',
            errorMessages: []
        }
        this.baseState = { ...this.state };
    }

    componentDidMount() {
        // if he has school already dont make api call
        if( !this.props.hasSchool && !this.props.cities.length ) {
            // call to get the cities and their areas
            this.props.fetchCreateSchoolRequirementsAction();
        }
    }

    validateSchoolData = () => {
        return [
            validateEmpty(this.state.schoolId, '   '),
            validateEmpty(this.state.name, '      '),
            validateEmpty(this.state.cityId, '    '),
            validateEmpty(this.state.areaId, '    ')
        ].filter(message => message)
    }

    createSchool = () => {
        if(this.state.errorMessages.length === 0) {
            // Action call to create school
            this.props.createSchoolAction({
                ...this.state,
                id: this.state.schoolId,
            })
        }
    }

    onFieldChange = (value, fieldName) => {
        switch(fieldName) {
            case 'cityId':
                this.setState({ [fieldName]: value, areaId: '' }, () => {
                    // after choosing city, clearSelection and show areas of the selected city
                    this.areasDropdownRef.current.clearSelection();
                });
                break;
            default:
                this.setState({ [fieldName]: value })
        }
    }

    onFormSubmit = (e) => {
        e.preventDefault();

        this.props.clearAlert();
        // performing validation and setting messages
        this.setState({
            errorMessages: this.validateSchoolData()
        }, () => {
            this.createSchool();
        });
    }

    onResetHandler = () => {
        // reset form values
        this.setState({ ...this.baseState });
        try{
            // becasue if user try to reset before chosen the value, error will happen
            this.cityDropdownRef.current.clearSelection();
        } catch(e) { }
    }

    renderAreasDropwdown = () => {
        if(this.state.cityId) {
            // whenever the city change, render other area dropdown menu
            return (
                <CustomDropdown
                    colSize="2"
                    onChange={(selectedArea) => this.onFieldChange(selectedArea, 'areaId')}
                    list={getLabelObject(this.state.cityId, this.props.cities).areas}
                    title='  '
                    label="  "
                    setRef={(ref) => this.areasDropdownRef = ref}
                />
            )
        }
        return null;
    }

    render() {
        return (
            <NotAllowed
                hasSchool={!this.props.hasSchool}
                referer="/school"
                statusCode="403"
                message="    ,    "
                btnLabel=" "
            >
                    <Form onSubmit={this.onFormSubmit}>
                        <FormHeader label=" " />
                        <ContentPadding>
                            <Alert messages={this.state.errorMessages} type="error" />
                            <div className="form-group">
                                <InputField
                                    id="id"
                                    name="id"
                                    type="text"
                                    onChangeHandler={(e) => this.onFieldChange(e.target.value, 'schoolId')}
                                    initial={true}
                                    initialValue={this.state.schoolId}
                                    label=" "
                                    placeholder=": 10510"
                                    icon="fas fa-user"
                                    colSize="2"
                                />
                                <InputField
                                    id="name"
                                    name="name"
                                    type="text"
                                    onChangeHandler={(e) => this.onFieldChange(e.target.value, 'name')}
                                    initial={true}
                                    initialValue={this.state.name}
                                    label=" "
                                    placeholder=":  "
                                    icon="fas fa-signature"
                                    colSize="2"
                                />
                            </div>
                            <div className="form-group">
                                <CustomDropdown 
                                    colSize="2"
                                    onChange={(selectedCity) => this.onFieldChange(selectedCity, 'cityId')}
                                    list={this.props.cities}
                                    title=' '
                                    label=""
                                    setRef={(ref) => this.cityDropdownRef = ref}
                                />
                                {this.renderAreasDropwdown()}
                            </div>
                            <ContentDivider />
                            <div className="form-group confirm-group margin-top-big">
                                <input className="btn submit-btn" type="submit" value=""/>
                                <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                            </div>
                        </ContentPadding>
                    </Form>
            </NotAllowed>
        )
    }
}

const mapStateToProps = state => {
    return {
        cities: Object.values(state.cities),
        hasSchool: state.auth.user.data.hasSchool,
        userId: state.auth.user.sub
    }
}

export default connect(mapStateToProps, { 
    fetchCreateSchoolRequirementsAction,
    createSchoolAction,
    clearAlert: alertActions.clear
})(SchoolCreate);
