// @ts-nocheck
import ContentPadding from 'components/Utils/ContentPadding'
import TextContainer from 'components/Text/TextContainer'
import React, { Fragment } from 'react'
import { Link } from 'react-router-dom'
import BulletContainer from 'components/Text/BulletContainer'
import BulletPoint from 'components/Text/BulletPoint'
import TitleHeader from 'components/Text/TitleHeader'
import ParagraphBlock from 'components/Text/ParagraphBlock'
import { connect } from 'react-redux'
import LabManagerHomePage from './LabManager/LabManagerHomePage'
import ScienceTeacherHomePage from './ScienceTeacher/ScienceTeacherHomePage'

const SchoolMemberHomePage = ({ hasSchool, role }) => {


    const renderInstructions = () => {

        if(hasSchool) {
            switch (role) {
                case 'Science_Teacher':
                    return <ScienceTeacherHomePage />
                case 'Laboratory_Manager':
                    return <LabManagerHomePage />
                default:
                    return null;
            }
        }

        return (
            <Fragment>
                <BulletContainer
                    title=":      "
                >
                    <BulletPoint>
                        &nbsp;      
                        <Link to="/invites" className="color-primary">  </Link>           
                    </BulletPoint>
                    <BulletPoint>
                        .      
                    </BulletPoint>
                    <BulletPoint>
                            ,      
                    </BulletPoint>
                </BulletContainer>
            </Fragment>
        );
    }

    return (
        <TextContainer>
            <TitleHeader title=" " />
            <ParagraphBlock>
                               
                 ,           
                ,            
                     .       
                   .         
                        .       
                  ,        
                  .
            </ParagraphBlock>

            <TitleHeader title="" />
            <ContentPadding>
                <ContentPadding>
                    { renderInstructions() }
                </ContentPadding> 
            </ContentPadding> 
        </TextContainer>
    )
}

const mapStateToProps = (state) => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
        role: state.auth.user.permissions,
    }
}

export default connect(mapStateToProps)(SchoolMemberHomePage)
