// @ts-nocheck
import React, { Fragment } from 'react';

import FormHeader from '../Form/FormHeader';
import Form from '../Form/Form';
import Alert from '../Utils/Alert';
import ContentPadding from '../Utils/ContentPadding';

const ConfirmDialog = ({ title, children, errorMessages=[], onOutSideClick }) => {
    return (
        <Fragment>
            <div className="confirm-dialog-shadow" onClick={onOutSideClick}></div>
            <div className="confirm-dialog-container">
                <Form>
                    <FormHeader 
                        label={title}
                    />
                    <ContentPadding>
                        <Alert 
                            messages={errorMessages}
                            type="error"
                        />
                        {children}
                    </ContentPadding>
                </Form>
            </div>
        </Fragment>
    );
  };

  export default ConfirmDialog;