// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';
import _ from 'lodash';

import CustomDatePicker from '../../../components/Form/CustomDatePicker';
import { Form } from '../../../components/Form/Form'
import InputField from '../../../components/Form/InputField';
import Alert from '../../../components/Utils/Alert';
import { validateEqualPropsObjects } from '../../../helpers/validate';
import { deleteSemesterAction, fetchSemesterAction, updateSemesterAction } from '../../../actions/pagesActions/SystemAdministrator/semesterActions';
import alertActions from '../../../actions/utilsActions/alertActions';
import NotFoundItem from '../../../components/Utils/NotFoundItem';
import TextArea from '../../../components/Form/TextArea';
import ContentDivider from '../../../components/Utils/ContentDivider';
import FormHeader from 'components/Form/FormHeader';
import { validateSemester } from './validateSemester';

class SemesterView extends Component {

    state = {
        id: '',
        title: '',
        startDate: null,
        endDate: null,
        note: '',
        errorMessages: []
    }

    async componentWillMount () {
        // call API action here for initial data, and set the initial state
        await this.props.fetchSemesterAction(this.props.match.params.id);
        this.settingState();
    }

    settingState = () => {
        const { semester } = this.props;
        if(!validateEqualPropsObjects(semester, {})) {
            this.setState({ ...this.props.semester });
        }
    }

    updateSemester = async () => {
        const originalSemester = this.props.semester;
        const updatedSemester = _.omit(_.omit(this.state, 'errorMessages'), 'note');
        if (this.state.errorMessages.length === 0 && 
            !validateEqualPropsObjects(originalSemester, updatedSemester)) {
            // Call API to PATCH semester
            await this.props.updateSemesterAction(updatedSemester)
            this.onResetHandler();
        }
    }

    onFieldChange = (value, fieldName) => {
        this.setState({ [fieldName]: value });
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        // Clear the Server alerts on each submit
        this.props.clearServerAlert();

        this.setState({
            errorMessages: validateSemester(this.state)
        }, () => {
            this.updateSemester();
        })
    }

    onResetHandler = () => {
        this.props.clearServerAlert();
        this.settingState();
    }

    onDeleteHandler = () => {
        // Delete action call
        this.props.deleteSemesterAction(this.props.match.params.id);
    }

    render() {
        return (
            <NotFoundItem
                referer="/"
            >
                <Form onSubmit={this.onFormSubmit}>
                    <FormHeader 
                        label={' '}
                        icon="fas fa-chevron-left"
                        refererLabel="  "
                        referer="/semester"
                    />
                    <div className="content-padding">
                        <Alert messages={this.state.errorMessages} type="error" />
                        <div className="form-group">
                            <InputField
                                id="id"
                                name="id"
                                type="text"
                                locked={true}
                                initial={true}
                                initialValue={this.state.id}
                                label=" "
                                icon="fas fa-lock"
                                colSize="2"
                            />
                            <InputField
                                id="title"
                                name="title"
                                onChangeHandler={(e) => this.onFieldChange(e.target.value, 'title')}
                                type="text"
                                initial={true}
                                initialValue={this.state.title}
                                label="  "
                                placeholder=":    2019/2020"
                                icon="fas fa-signature"
                                colSize="2"
                            />
                        </div>
                        <div className="form-group">
                            <CustomDatePicker
                                id="start_date"
                                name="startDate"
                                onChangeHandler={(startDate) => this.onFieldChange(startDate, 'startDate')}
                                initial={true}
                                initialDate={this.state.startDate}
                                label=" "
                                colSize="2"
                                minDate={new Date()}
                            />
                            <CustomDatePicker
                                id="end_date"
                                name="endDate"
                                onChangeHandler={(endDate) => this.onFieldChange(endDate, 'endDate')}
                                initial={true}
                                initialDate={this.state.endDate}
                                label=" "
                                colSize="2"
                                minDate=
                                {
                                    this.state.startDate 
                                    ? new Date().setDate(this.state.startDate.getDate() + 1) 
                                    : new Date().setDate(new Date().getDate() + 1)
                                }
                            />
                        </div>
                        <div className="form-group">
                            <TextArea
                                id="note"
                                name="note"
                                onChangeHandler={(e) => this.onFieldChange(e.target.value, 'note')}
                                initial={true}
                                initialValue={this.state.note}
                                label=""
                                placeholder=":         "
                                colSize="1"
                            />
                        </div>
                        <ContentDivider />
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value="" />
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler} />
                            <input className="btn delete-btn" type="reset" value="" onClick={this.onDeleteHandler} />
                        </div>
                    </div>
                </Form>
            </NotFoundItem>
        )
    }
}

const mapStateToProps = state => {
    return {
        alert: state.alert,
        semester: state.itemView
    }
}

export default connect(mapStateToProps,  
    { 
        clearServerAlert: alertActions.clear,
        fetchSemesterAction,
        updateSemesterAction,
        deleteSemesterAction
    })(SemesterView);