import alertActions from '../../utilsActions/alertActions';
import loadingActions from '../../utilsActions/loadingActions';
import mylabs from '../../../apis/mylabs';
import catchHandler from '../../../helpers/catchHandler';
import history from '../../../history';

export const fetchCreateExperimentRequirementsAction = () => {
    return async (dispatch) => {
        try {

            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());

            // fetch the semesters records from the API server 
            const response = await mylabs.get('/experiment/create-requirements');
            
            // Send to form Reducers
            dispatch( {type: 'FETCH_FORM_ITEMS', payload: response.data.items });
            dispatch( {type: 'FETCH_FORM_COURSES', payload: response.data.courses });


            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch(err) {
            catchHandler(dispatch, err);
        }
    }
}

export const createExperimentAction = (experiment) => {
    return async (dispatch) => {

        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make POST request to the API server
            const response = await mylabs.post('/experiment', experiment);

            // removing loading spinner after success
            dispatch(loadingActions.remove());

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true,
                path: `/experiment/${response.data.id}`
            }));

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
} 

export const fetchExperimentsAction = (filterParams) => {
    // FETCH list of experiments
    return async (dispatch) => {
        try {

            // show loading spinner
            dispatch(loadingActions.show());

            // make POST request to the API server
            const response = await mylabs.get('/experiment', {
                params: filterParams
            });

            // Send to Reducers
            dispatch( { type: 'FETCH_EXPERIMENTS', payload: response.data });

            dispatch({ type: 'FETCH_CORRECT_EXPERIMENTS', payload: response.data.experiments })

            dispatch({ type: 'FETCH_FORM_COURSES', payload: response.data.courses });
            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }

    }
}

export const fetchExperimentAction = (experimentId) => {
    // FETCH single experiment by ID
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make GET request to the API server
            const response = await mylabs.get(`/experiment/${experimentId}`);

            // GET request to get the create experiments requirements
            const formRequirementsResponse = await mylabs.get('/experiment/create-requirements');

            // Send to itemView Reducer
            dispatch( { type: 'FETCH_EXPERIMENT', payload: response.data.experiment });

            // send to form reducers
            dispatch({ type: 'FETCH_FORM_COURSES', payload: formRequirementsResponse.data.courses });
            dispatch({ type: 'FETCH_FORM_ITEMS', payload: formRequirementsResponse.data.items });

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }

    }
}

export const updateExperimentAction = (experiment) => {
    // PATCH single experiment by ID
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make GET request to the API server
            const response = await mylabs.patch('/experiment', experiment);

            dispatch( { type: 'FETCH_EXPERIMENT', payload: response.data.experiment });


            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true
            }));

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}

export const deleteExperimentAction = (experimentId) => {
    // DELETE experiment
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make PATCH request to the API server
            await mylabs.delete(`/experiment/${experimentId}`);

            // navigate user to the experiments list screen
            history.push('/experiment');

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true
            }));

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }

    }
}