// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import CustomDropdown from '../../../components/Form/CustomDropdown';
import Form from '../../../components/Form/Form';
import FormHeader from '../../../components/Form/FormHeader';
import InputField from '../../../components/Form/InputField';
import Alert from '../../../components/Utils/Alert';
import ContentPadding from '../../../components/Utils/ContentPadding';
import { validateEmpty, validateEqualPropsObjects } from '../../../helpers/validate';
import ContentDivider from '../../../components/Utils/ContentDivider';
import { fetchSchoolAction, updateSchoolAction } from '../../../actions/pagesActions/SchoolManager/schoolAction';
import { getLabelObject } from '../../../helpers/objectsHelpers';
import PreviewList from '../../../components/List/PreviewList/PreviewList';
import PreviewListContainer from '../../../components/List/PreviewList/PreviewListContainer';
import NotAllowed from '../../../components/Utils/NotAllowed';

class SchoolView extends Component {

    constructor(props) {
        super(props);
        this.cityDropdownRef = React.createRef();
        this.areasDropdownRef = React.createRef();

        this.state = {
            id: '',
            name: '',
            cityId: '',
            areaId: '',
            errorMessages: []
        }
    }

    async componentDidMount() {
        // if he hasSchool and didn't load the school info before, make the api call
        if(this.props.hasSchool && 
            (validateEqualPropsObjects(this.props.school, {}) || 
            validateEqualPropsObjects(this.props.cities, {}))) {
            // call to get all school info
            await this.props.fetchSchoolAction();
        }
        this.settingState();
    }

    settingState = () => {
        const { school } = this.props;
        if(!validateEqualPropsObjects(school, {})) {
            this.setState({
                ...this.state,
                id: school.id,
                name: school.name,
                cityId: school.location.city.value,
                areaId: school.location.area.value,
            }, async () => {
                if(this.props.hasSchool && this.cityDropdownRef.current && school.location){
                    // setting the values in the drop down-menu as the location
                    await this.cityDropdownRef.current.selectItem(school.location.city)
                    this.areasDropdownRef.current.selectItem(school.location.area)
                }
            })
        }
    }

    validateUpdatedSchool = () => {
        return [
            validateEmpty(this.state.name, '      '),
            validateEmpty(this.state.cityId, '    '),
            validateEmpty(this.state.areaId, '    ')
        ].filter(message => message)
    }

    updateSchool = async () => {
        if(!validateEqualPropsObjects(this.props.school, {})) {
            const school = (({id, name, location}) => (
                {
                    id, 
                    name, 
                    cityId: location.city.value, 
                    areaId: location.area.value
                }
            ))(this.props.school)
            const updatedSchool = (({id, name, cityId, areaId}) => ({id, name, cityId, areaId}))(this.state);
            // before make API call, check if actually school data updated
            if(this.state.errorMessages.length === 0 && !validateEqualPropsObjects(school, updatedSchool)) {
                // Action call to patch school
                await this.props.updateSchoolAction(updatedSchool);
                this.settingState();
            }
        }
    }

    convertRole = (list) => {
        return list.map(member => {
            return { ...member, role: member.role.ar };
        })
    }

    onFieldChange = (value, fieldName) => {
        switch(fieldName) {
            case 'cityId':
                this.setState( {  
                    cityId: value, 
                    areaId: '' , 
                    areas: getLabelObject(value, this.props.cities).areas 
                }, () => {
                    // after choosing city, clearSelection of area dropdown
                    this.areasDropdownRef.current.clearSelection()
                });
                break;
            default:
                this.setState({ [fieldName]: value });
        }
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        // performing validation
        this.setState({
            errorMessages: this.validateUpdatedSchool()
        }, () => {
            // make request
            this.updateSchool()
        });
    }

    onResetHandler = () => {
        this.settingState();
    }

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.hasSchool}
                referer="/school/create"
                statusCode="403"
                message="    ,   "
                btnLabel="  "
            >
                <Form onSubmit={this.onFormSubmit}>
                    <FormHeader label=" " />
                    <ContentPadding>
                        <Alert messages={this.state.errorMessages} type="error" />
                        <div className="form-group">
                            <InputField
                                id="id"
                                name="id"
                                type="text"
                                initial={true}
                                initialValue={this.state.id}
                                label=" "
                                placeholder=": 10541"
                                icon="fas fa-lock"
                                locked={true}
                                colSize="2"
                            />
                            <InputField
                                id="name"
                                name="name"
                                type="text"
                                onChangeHandler={(e) => this.onFieldChange(e.target.value, 'name')}
                                initial={true}
                                initialValue={this.state.name}
                                label=" "
                                placeholder=":  "
                                icon="fas fa-signature"
                                colSize="2"
                            />
                        </div>
                        <div className="form-group">
                            <CustomDropdown 
                                onChange={(cityId) => this.onFieldChange(cityId, 'cityId')}
                                list={this.props.cities ? this.props.cities : []}
                                title=' '
                                label=""
                                setRef={(ref) => this.cityDropdownRef = ref}
                                colSize="2"
                            />
                            <CustomDropdown
                                onChange={(areaId) => this.onFieldChange(areaId, 'areaId')}
                                list={this.state.areas ? this.state.areas : []}
                                title='  '
                                label="  "
                                setRef={(ref) => this.areasDropdownRef = ref}
                                colSize="2"
                            />
                        </div>
                        <ContentDivider />
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value=""/>
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                        </div>
                    </ContentPadding>
                </Form>
                <PreviewListContainer>
                    <PreviewList
                        listHeader=""
                        list={this.convertRole(this.props.members)}
                        referer="/members/create"
                        icon="fas fa-plus"
                        refererLabel=""
                        emptyMessage="   "
                        title="name"
                        subTitle="role"
                    />
                    <PreviewList
                        listHeader=""
                        list={this.props.laboratories} 
                        referer="/laboratory/create"
                        icon="fas fa-plus"
                        emptyMessage="   "
                        title="id"
                        previewTitleMessage="  : "
                    />
                </PreviewListContainer>
            </NotAllowed>
        )
    }
}

const mapStateToProps = state => {
    return {
        school: state.school,
        hasSchool: state.auth.user.data.hasSchool,
        cities: Object.values(state.cities),
        laboratories: Object.values(state.laboratories),
        members: Object.values({...state.scienceTeachers, ...state.laboratoryManagers})
    }
}

export default connect(mapStateToProps, { 
    fetchSchoolAction,
    updateSchoolAction
})(SchoolView);
