// @ts-nocheck
import React from 'react'
import { connect } from 'react-redux';
import { Switch } from 'react-router-dom';
import LoadingOverlay from 'react-loading-overlay';

import PrivateRoute from '../routes/PrivateRoute';
import PagesContainer from '../components/PagesContainer';
import Sidebar from '../components/Sidebar';
import MobileHeader from '../components/MobileHeader';
import Profile from './Profile';
import SemesterView from './SystemAdministrator/Semester/SemesterView';
import ItemView from './SystemAdministrator/Item/ItemView';
import CourseView from './SystemAdministrator/Course/CourseView';
import InviteMember from './SchoolManager/Member/InviteMember';
import LabCreate from './SchoolManager/Laboratory/LabCreate';
import ExperimentView from './SystemAdministrator/Experiment/ExperimentView';
import CityView from './SystemAdministrator/City/CityView';
import SchoolCreate from './SchoolManager/School/SchoolCreate';
import schoolView from './SchoolManager/School/schoolView';
import InvitesList from './SchoolMember/InvitesList';
import TeacherView from './SchoolManager/Member/TeacherView';
import LabManagerView from './SchoolManager/Member/LabManagerView';
import LabView from './SchoolManager/Laboratory/LabView';
import LaboratoriesList from './SchoolMember/LabManager/LaboratoriesList';
import LaboratoryTab from './SchoolMember/LabManager/Laboratory/LaboratoryTab';
import LabItemView from './SchoolMember/LabManager/LabItemView';
import PracticalExperimentTab from './SchoolMember/ScienceTeacher/PracticalExperiment/PracticalExperimentTab';
import PracticalExperimentView from './SchoolMember/ScienceTeacher/PracticalExperimentView';
import PracticalRequestView from './SchoolMember/LabManager/PracticalRequestView';
import PracticalExperimentList from './SchoolMember/LabManager/PracticalExperimentList';
import PracticalExperimentEvaluate from './SchoolMember/ScienceTeacher/PracticalExperimentEvaluate';
import EducationalSupervisorTab from './SystemAdministrator/EducationalSupervisor/EducationalSupervisorTab';
import EducationalSupervisorView from './SystemAdministrator/EducationalSupervisor/EducationalSupervisorView';
import VisitCurrent from './EducationalSupervisor/VisitCurrent';
import LaboratoryDashboard from './EducationalSupervisor/Laboratory/LaboratoryDashboard';
import VisitCreate from './EducationalSupervisor/VisitCreate';
import LaboratoryView from './EducationalSupervisor/Laboratory/LaboratoryView';
import LaboratoryManagerView from './EducationalSupervisor/LaboratoryManager/LaboratoryManagerView';
import LaboratoryManagerDashboard from './EducationalSupervisor/LaboratoryManager/LaboratoryManagerDashboard';
import LaboratoryEvaluate from './EducationalSupervisor/Laboratory/LaboratoryEvaluate';
import LaboratoryManagerEvaluate from './EducationalSupervisor/LaboratoryManager/LaboratoryManagerEvaluate';
import LabDashboard from './SchoolManager/Laboratory/LabDashboard';
import LabManagerDashboard from './SchoolManager/Member/LabManagerDashboard';
import QuestionView from './SystemAdministrator/Question/QuestionView';
import HomePage from './HomePage';
import CityTab from './SystemAdministrator/City/CityTab';
import CourseTab from './SystemAdministrator/Course/CourseTab';
import ExperimentTab from './SystemAdministrator/Experiment/ExperimentTab';
import ItemTab from './SystemAdministrator/Item/ItemTab';
import QuestionTab from './SystemAdministrator/Question/QuestionTab';
import SemesterTab from './SystemAdministrator/Semester/SemesterTab';
import ReportDefect from './Defect/ReportDefect';
import DefectsList from './Support/DefectsList';
import DefectView from './Support/DefectView';

function IndexPage(props) {
    // The IndexPage responsible for managing the App Routes based on User role
    return (
        <div className="app-container">            
            <LoadingOverlay
                        active={props.isLoading}
                        text=" "
                        spinner
            >
                <Sidebar />
                <MobileHeader />
                <PagesContainer match={props.match}>
                    <Switch>

                        {/*Private General Routes for all Roles*/}
                        <PrivateRoute path="/" exact component={HomePage} roles={['School_Manager', 'Science_Teacher', 'Laboratory_Manager', 'System_Administrator', 'Educational_Supervisor', 'Support']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/profile" exact component={Profile} roles={['School_Manager', 'Science_Teacher', 'Laboratory_Manager', 'System_Administrator', 'Educational_Supervisor', 'Support']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/defect/create" exact component={ReportDefect} roles={['School_Manager', 'Science_Teacher', 'Laboratory_Manager', 'System_Administrator', 'Educational_Supervisor']} currentUserRole={props.currentUser.permissions} />

                        {/* Support Routes */}
                        <PrivateRoute path="/defect" exact component={DefectsList} roles={['Support']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/defect/:id" exact component={DefectView} roles={['Support']} currentUserRole={props.currentUser.permissions} />

                        {/* System Administrator Routes */}
                        <PrivateRoute path="/semester" exact component={SemesterTab} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/semester/create" exact component={SemesterTab} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/semester/:id" exact component={SemesterView} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />

                        <PrivateRoute path="/item" exact component={ItemTab} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/item/create" exact component={ItemTab} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/item/:id" exact component={ItemView} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />

                        <PrivateRoute path="/course" exact component={CourseTab} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/course/create" exact component={CourseTab} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/course/:id" exact component={CourseView} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />

                        <PrivateRoute path="/experiment" exact component={ExperimentTab} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/experiment/create" exact component={ExperimentTab} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/experiment/:id" exact component={ExperimentView} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />

                        <PrivateRoute path="/city" exact component={CityTab} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/city/create" exact component={CityTab} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/city/:id" exact component={CityView} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />

                        <PrivateRoute path="/question" exact component={QuestionTab} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/question/add" exact component={QuestionTab} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/question/:id" exact component={QuestionView} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />

                        <PrivateRoute path="/educational-supervisor/create" exact component={EducationalSupervisorTab} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/educational-supervisor/" exact component={EducationalSupervisorTab} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/educational-supervisor/:id" exact component={EducationalSupervisorView} roles={['System_Administrator']} currentUserRole={props.currentUser.permissions} />

                        {/* School Manager Routes */}
                        <PrivateRoute path="/school" exact component={schoolView} roles={['School_Manager']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/school/create" exact component={SchoolCreate} roles={['School_Manager']} currentUserRole={props.currentUser.permissions} />

                        <PrivateRoute path="/dashboard/laboratory" exact component={LabDashboard} roles={['School_Manager']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/dashboard/laboratory-manager" exact component={LabManagerDashboard} roles={['School_Manager']} currentUserRole={props.currentUser.permissions} />


                        <PrivateRoute path="/members/create" exact component={InviteMember} roles={['School_Manager']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/science-teacher/:id" exact component={TeacherView} roles={['School_Manager']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/laboratory-manager/:id" exact component={LabManagerView} roles={['School_Manager']} currentUserRole={props.currentUser.permissions} />

                        <PrivateRoute path="/laboratory/create" exact component={LabCreate} roles={['School_Manager']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/laboratory/:id" exact component={LabView} roles={['School_Manager']} currentUserRole={props.currentUser.permissions} />

                        { /* School Member Routes */ }
                        <PrivateRoute path="/invites" exact component={InvitesList} roles={['Science_Teacher', 'Laboratory_Manager']} currentUserRole={props.currentUser.permissions} />

                        { /* Laboratory Manager Routes */ }
                        <PrivateRoute path="/school-laboratory" exact component={LaboratoriesList} roles={['Laboratory_Manager']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/school-laboratory/:labId/item/:itemId" exact component={LabItemView} roles={['Laboratory_Manager']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/practical-request" exact component={PracticalExperimentList} roles={['Laboratory_Manager']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/practical-request/:id" exact component={PracticalRequestView} roles={['Laboratory_Manager']} currentUserRole={props.currentUser.permissions} />

                                        { /* Laboratory Manager Lab Tabs View */ }
                        <PrivateRoute path="/school-laboratory/:labId" exact component={LaboratoryTab} roles={['Laboratory_Manager']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path={`/school-laboratory/:labId/current-items`} exact component={LaboratoryTab} roles={['Laboratory_Manager']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path={`/school-laboratory/:labId/removed-items`} exact component={LaboratoryTab} roles={['Laboratory_Manager']} currentUserRole={props.currentUser.permissions} />

                        {/* Sciecne Teacher Routes */}
                        <PrivateRoute path="/practical-experiment/create" exact component={PracticalExperimentTab} roles={['Science_Teacher']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/practical-experiment" exact component={PracticalExperimentTab} roles={['Science_Teacher']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/practical-experiment/:id" exact component={PracticalExperimentView} roles={['Science_Teacher']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/practical-experiment/:id/evaluate" exact component={PracticalExperimentEvaluate} roles={['Science_Teacher']} currentUserRole={props.currentUser.permissions} />

                        { /* Educational Supervisor Routes */ }
                        <PrivateRoute path="/visit/create" exact component={VisitCreate} roles={['Educational_Supervisor']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/visit/current" exact component={VisitCurrent} roles={['Educational_Supervisor']} currentUserRole={props.currentUser.permissions} />

                        <PrivateRoute path="/visit/current/laboratory/:id" exact component={LaboratoryView} roles={['Educational_Supervisor']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/visit/current/laboratory/:id/dashboard" exact component={LaboratoryDashboard} roles={['Educational_Supervisor']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/visit/current/laboratory/:id/evaluate" exact component={LaboratoryEvaluate} roles={['Educational_Supervisor']} currentUserRole={props.currentUser.permissions} />

                        <PrivateRoute path="/visit/current/laboratory-manager/:id" exact component={LaboratoryManagerView} roles={['Educational_Supervisor']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/visit/current/laboratory-manager/:id/dashboard" exact component={LaboratoryManagerDashboard} roles={['Educational_Supervisor']} currentUserRole={props.currentUser.permissions} />
                        <PrivateRoute path="/visit/current/laboratory-manager/:id/evaluate" exact component={LaboratoryManagerEvaluate} roles={['Educational_Supervisor']} currentUserRole={props.currentUser.permissions} />

                    </Switch>
                </PagesContainer>
            </LoadingOverlay>
        </div>
    );
}

const mapStateToProps = (state) => {
    return {
        currentUser: state.auth.user,
        isLoading: state.isLoading
    }
}

export default connect(mapStateToProps)(IndexPage)
