// @ts-nocheck
import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

import { Form } from 'components/Form/Form';
import FormHeader from 'components/Form/FormHeader';
import InputField from 'components/Form/InputField';
import ContentDivider from 'components/Utils/ContentDivider';
import ContentPadding from 'components/Utils/ContentPadding';
import NotFoundItem from 'components/Utils/NotFoundItem';
import { fetchCurrentVisitLaboratoryAction } from 'actions/pagesActions/EducationalSupervisor/visitAction';

const LaboratoryView = ({ laboratory, fetchCurrentVisitLaboratoryAction, match }) => {

    if(!laboratory.id) {
        fetchCurrentVisitLaboratoryAction(match.params.id);
    }

    const renderLabManagers = () => {
        // display the laboratory managers of the sepecific lab
        if(laboratory) {
            return laboratory.labManagers.map((labManager, index) => {
                return (
                    <div className="form-group" key={labManager.value}>
                        <InputField 
                            id={`labManager${labManager.value}`}
                            name={`labManager${labManager.value}`}
                            type="text"
                            label={` : ${index+1}`}
                            locked={true}
                            initial={true}
                            initialValue={labManager.label}
                            icon="fas fa-lock"
                            colSize="1"
                        />
                    </div>
                );
            })
        }
    }

    return (
        <NotFoundItem
            referer="/visit/current"
        >
            <Form>
                <FormHeader 
                    label=" " 
                    refererLabel=" "
                    icon="fas fa-chevron-left"
                    referer="/visit/current"
                />
                <ContentPadding>
                    <div className="form-group">
                        <InputField
                            id="id"
                            name="id"
                            type="text"
                            initial={true}
                            initialValue={laboratory.id}
                            label=" "
                            icon="fas fa-lock"
                            locked={true}
                            colSize="2"
                        />
                        <InputField
                            id="rate"
                            name="rate"
                            type="text"
                            initial={true}
                            initialValue={laboratory.rate}
                            label=" "
                            icon="fas fa-lock"
                            locked={true}
                            colSize="2"
                        />
                    </div>
                    { renderLabManagers() }
                    <ContentDivider />
                    <div className="form-group confirm-group margin-top-big">
                        <Link 
                            to={`/visit/current/laboratory/${match.params.id}/evaluate`}
                            className="btn submit-btn link-btn" 
                        >
                             
                        </Link>
                        <Link 
                            to={`/visit/current/laboratory/${match.params.id}/dashboard`}
                            className="btn cancel-btn link-btn" 
                        >
                             
                        </Link>
                    </div>
                </ContentPadding>
            </Form>
        </NotFoundItem>
    );
}

const mapStateToProps = (state, ownProps) => {
    return {
        laboratory: state.laboratories[ownProps.match.params.id]
    }
}

LaboratoryView.defaultProps = {
    laboratory: {
        id: '',
        rate: '',
        labManagers: []
    }
};

export default  connect(mapStateToProps, {
    fetchCurrentVisitLaboratoryAction
})(LaboratoryView);