// @ts-nocheck
import React, { Component } from 'react';
import { connect } from 'react-redux';

import CustomDropdown from 'components/Form/CustomDropdown';
import { Form } from 'components/Form/Form';
import FormHeader from 'components/Form/FormHeader';
import InputField from 'components/Form/InputField';
import ContentDivider from 'components/Utils/ContentDivider';
import ContentPadding from 'components/Utils/ContentPadding';
import NotFoundItem from 'components/Utils/NotFoundItem';
import { 
    validateEmail, 
    validateEmpty, 
    validateMatchedPasswords, 
    validateName, 
    validatePassword, 
    validateSaudiPhoneNumber 
} from 'helpers/validate';
import alertActions from '../../../actions/utilsActions/alertActions';
import Alert from 'components/Utils/Alert';
import { 
    createEducationalSupervisorAccountAction, 
    fetchEducationalSupervisorAccountRequirements 
} from 'actions/pagesActions/SystemAdministrator/supervisorActions';
import { getLabelObject } from 'helpers/objectsHelpers';

class EducationalSupervisorCreate extends Component {

    constructor(props) {
        super(props);
        this.cityDropdownRef = React.createRef();
        this.areasDropdownRef = React.createRef();
        this.courseDropdownRef = React.createRef();

        this.baseState = this.state;
    }

    state = {
        password: '',
        secondPassword: '',
        fname: '',
        lname: '',
        mname: '',
        email: '',
        phone: '',
        cityId: '',
        areaId: '',
        courseId: '',
        role: 'Educational_Supervisor',
        errorMessages: []
    }

    componentDidMount() {
        if(!this.props.cities.length || !this.props.courses.length) {
            this.props.fetchEducationalSupervisorAccountRequirements();
        }
    }

    validateEducationalSupervisorAccount = () => {
        return [
            validateSaudiPhoneNumber(this.state.phone),
            validateName([this.state.fname, this.state.mname, this.state.lname]),
            validatePassword(this.state.password),
            validateMatchedPasswords(this.state.password, this.state.secondPassword),
            validateEmail(this.state.email),
            validateEmpty(this.state.courseId, '      *'),
            validateEmpty(this.state.cityId, '      *'),
            this.state.cityId && validateEmpty(this.state.areaId, '       *')
            // The array filter method will drop empty messages
        ].filter((message) => message);
    }
    
    createEducationalSupervisorAccount = async () => {
        if(this.state.errorMessages.length === 0) {
            // calling the register action after validating that user inputs is in correct format
            await this.props.createEducationalSupervisorAccountAction(this.state)
            if(this.props.status) {
                this.onResetHandler();
            }
        }
    }

    handleChange = (value, fieldName) => {

        switch(fieldName) {
            case 'cityId':
                // The city is in dropbox, so its easier to give its own setState
                this.setState({ [fieldName]: value, areaId: '' }, () => {
                    // after choosing city, clearSelection and show areas of the selected city
                    this.areasDropdownRef.current.clearSelection();
                });
                break;
            default:
                // handle user input change dynamically using names
                this.setState({ [fieldName]: value });
                break;
        }
    }

    onCreateSupervisorAccountSubmit = (e) => {
        e.preventDefault();
        // clean the alerts
        this.props.clearAlert();

        // getting the user property from the state
        this.setState({
            errorMessages: this.validateEducationalSupervisorAccount()
        }, () => {
            this.createEducationalSupervisorAccount();
        });
    }

    onResetHandler = () => {
        this.setState(this.baseState, () => {
            this.cityDropdownRef.current.clearSelection();
            this.courseDropdownRef.current.clearSelection();
        })
    }

    renderAreasDropwdown = () => {
        if(this.state.cityId) {
            // whenever the city change, render other area dropdown menu
            return (
                <CustomDropdown
                    colSize="2"
                    onChange={(areaId) => this.handleChange(areaId, 'areaId')}
                    list={getLabelObject(this.state.cityId, this.props.cities).areas}
                    title='  '
                    label="  "
                    setRef={(ref) => this.areasDropdownRef = ref}
                />
            )
        }
        return null;
    }

    render() {
        return (
            <NotFoundItem
                referer="/"
            >
                <Form onSubmit={this.onCreateSupervisorAccountSubmit} >
                    <FormHeader 
                        label="  "
                    />
                    <ContentPadding>
                        <Alert messages={this.state.errorMessages} type="404" />
                        <div className="form-group">
                            <InputField
                                name="fname"
                                onChangeHandler={(e) => this.handleChange(e.target.value, 'fname')}
                                type="text"
                                initial={true}
                                initialValue={this.state.fname}
                                id="first-name"
                                label=" "
                                icon="fas fa-user"
                                colSize="3"
                            />
                            <InputField
                                id="middle-name"
                                name="mname"
                                onChangeHandler={(e) => this.handleChange(e.target.value, 'mname')}
                                type="text"
                                initial={true}
                                initialValue={this.state.mname}
                                label=" "
                                icon="fas fa-user"
                                colSize="3"
                            />
                            <InputField
                                id="last-name"
                                name="lname"
                                onChangeHandler={(e) => this.handleChange(e.target.value, 'lname')}
                                type="text"
                                initial={true}
                                initialValue={this.state.lname}
                                label=" "
                                icon="fas fa-user"
                                colSize="3"
                            />
                        </div>
                        <div className="form-group">
                            <InputField
                                id="email"
                                name="email"
                                onChangeHandler={(e) => this.handleChange(e.target.value, 'email')}
                                type="text"
                                initial={true}
                                initialValue={this.state.email}
                                label=" "
                                icon="fas fa-at"
                                colSize="2"
                            />
                            <InputField
                                name='phone'
                                onChangeHandler={(e) => this.handleChange(e.target.value, 'phone')}
                                type="tel"
                                id="phone"
                                initial={true}
                                initialValue={this.state.phone}
                                label=" "
                                icon="fas fa-phone"
                                colSize="2"
                            />
                        </div>
                        <div className="form-group">
                            <InputField 
                                id="password"
                                name="password"
                                onChangeHandler={(e) => this.handleChange(e.target.value, 'password')}
                                type="password"
                                initial={true}
                                initialValue={this.state.password}
                                label=" "
                                icon="fas fa-key"
                                colSize="2"
                            />
                            <InputField
                                id="second-password"
                                name="secondPassword"
                                onChangeHandler={(e) => this.handleChange(e.target.value, 'secondPassword')}
                                type="password"
                                initial={true}
                                initialValue={this.state.secondPassword}
                                label="  "
                                icon="fas fa-key"
                                colSize="2"
                            />
                        </div>
                        <div className="form-group">
                            <CustomDropdown 
                                onChange={(courseId) => this.handleChange(courseId, 'courseId')}
                                colSize="2"
                                list={this.props.courses}
                                label=" "
                                title='  '
                                setRef={(ref) => this.courseDropdownRef = ref}
                            />
                            <CustomDropdown 
                                onChange={(cityId) => this.handleChange(cityId, 'cityId')}
                                colSize="2"
                                list={this.props.cities}
                                label=" "
                                title=' '
                                setRef={(ref) => this.cityDropdownRef = ref}
                            />
                        </div>
                        <div className="form-group">
                            {this.renderAreasDropwdown()}
                        </div>
                        <ContentDivider />
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value=" " />
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                        </div>
                    </ContentPadding>

                </Form>
            </NotFoundItem>
        );
    }
}

const mapStateToProps = state => {
    return {
        cities: Object.values(state.cities),
        courses: Object.values(state.courses),
        status: state.alert.success
    }
}

export default connect(mapStateToProps, {
    fetchEducationalSupervisorAccountRequirements,
    createEducationalSupervisorAccountAction,
    clearAlert: alertActions.clear
 })(EducationalSupervisorCreate);