// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import Form from '../../../components/Form/Form'
import FormHeader from '../../../components/Form/FormHeader'
import InputField from '../../../components/Form/InputField'
import NotFoundItem from '../../../components/Utils/NotFoundItem';
import ContentPadding from '../../../components/Utils/ContentPadding'
import { validateEqualPropsObjects } from '../../../helpers/validate'
import alertActions from '../../../actions/utilsActions/alertActions';
import ContentDivider from '../../../components/Utils/ContentDivider';
import CustomMultipleDropdown from '../../../components/Form/CustomMultipleDropdown';
import { getItemsDifferenceStatus } from '../../../helpers/objectsHelpers';
import { fetchTeacherAction, updateTeacherCourses } from '../../../actions/pagesActions/SchoolManager/membersAction';
import NotAllowed from '../../../components/Utils/NotAllowed';

class TeacherView extends Component {

    state = {
        scienceTeacherId: '',
        name: '',
        courses: []
    }

    async componentDidMount () {
        // connecting to the dropdown menu to reach clearSelection functionality
        this.dropdownRef = React.createRef();
        try {
            // check if teacher exist in local redux store, and courses length not equal 0
            if(this.props.scienceTeacher && this.props.hasSchool && this.props.courses.length !== 0) {
                this.settingState();
            } else if(this.props.hasSchool){
                // if this particular science teacher not exist in local redux store, call API to fetch it
                await this.props.fetchTeacherAction(this.props.match.params.id)
                this.settingState();
            }
        } catch (e){ }
    }

    settingState = () => {
        // setting the state of science teacher from redux store
        const { scienceTeacher } = this.props;

        if(scienceTeacher) {
            this.setState({
                name: scienceTeacher.name,
                scienceTeacherId: scienceTeacher.id,
                courses: scienceTeacher.courses,
             }, () => {
                // setting the courses dropdown menu
                this.dropdownRef.current.deselectAll();
                this.dropdownRef.current.selectMultipleItems(this.state.courses);
             });
        }
    }

    onCoursesChangeHandler = (selectedCourses) => {
        this.setState({ courses: [ ...selectedCourses ] });
    }

    onFormSubmit = async (e) => {
        e.preventDefault();
        const { scienceTeacher } = this.props;
        if(scienceTeacher) {
            const { courses } = this.state;
            const originalCourses = scienceTeacher.courses;
            if(!validateEqualPropsObjects(courses, originalCourses)) {
                // update action
                await this.props.updateTeacherCourses({
                    courses: getItemsDifferenceStatus(originalCourses, courses),
                    scienceTeacherId: this.state.scienceTeacherId
                })
                // set user last update after success submit
                this.settingState();
            }
        }
    }

    onResetHandler = () => {
        // remove alerts
        this.props.clearAlert();
        
        // reset form values
        this.settingState();
    }

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.hasSchool}
                referer="/"
                message="    "
                statusCode="403"
                btnLabel="  "
            >
                <NotFoundItem 
                    referer="/school"
                >
                    <Form onSubmit={this.onFormSubmit}>
                        <FormHeader 
                            label="  " 
                            referer="/school"
                            refererLabel=""
                            icon="fas fa-chevron-left"
                        />
                        <ContentPadding>
                            <div className="form-group">
                                <InputField
                                    id="id"
                                    name="id"
                                    type="text"
                                    initial={true}
                                    initialValue={this.state.scienceTeacherId }
                                    label=" "
                                    locked={true}
                                    icon="fas fa-lock"
                                    colSize="2"
                                />
                                <InputField
                                    id="name"
                                    name="name"
                                    type="text"
                                    initial={true}
                                    initialValue={this.state.name}
                                    label=" "
                                    locked={true}
                                    icon="fas fa-lock"
                                    colSize="2"
                                />
                            </div>
                            <div className="form-group">
                                <CustomMultipleDropdown 
                                    colSize="2"
                                    label=" "
                                    setRef={(ref) => this.dropdownRef = ref}
                                    onChange={this.onCoursesChangeHandler}
                                    list={this.props.courses}
                                    title='  '
                                    selectedTitle=" "
                                />
                            </div>
                        </ContentPadding>
                        <ContentDivider />
                        <ContentPadding>
                            <div className="form-group confirm-group margin-top-big">
                                <input className="btn submit-btn" type="submit" value=""/>
                                <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                            </div>
                        </ContentPadding>
                    </Form>
                </NotFoundItem>
            </NotAllowed>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
        courses: Object.values(state.courses),
        scienceTeacher: Object.values(state.scienceTeachers)
                            .filter(scienceTeacher => scienceTeacher.id.toString() === ownProps.match.params.id)[0],
    }
}

export default connect(mapStateToProps, { 
    updateTeacherCourses,
    fetchTeacherAction,
    clearAlert: alertActions.clear
})(TeacherView);
