import mylabs from '../../../apis/mylabs';
import catchHandler from '../../../helpers/catchHandler';
import loadingActions from '../../utilsActions/loadingActions';
import alertActions from '../../utilsActions/alertActions';

export const createLaboratoryAction = (laboratory) => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to patch
            const response = await mylabs.post('/laboratory', { ...laboratory })

            // send the created laboratory to reducer
            dispatch({ type: 'CREATE_LABORATORY', payload: { ...response.data.lab } });

            // send updated laboratory managers to reducer
            dispatch({ type: 'UPDATE_LABORATORY_MANAGERS', payload: { ...response.data.labManagers }});

            // remove the loading indicator
            dispatch(loadingActions.remove());

            // message to the user that the school is created
            dispatch(alertActions.success({
                message: "   ",
                code: 200,
                success: true,
                path: '/laboratory/' + response.data.lab.id
            }));


        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}

export const fetchLabAction = (labId) => {
    return async (dispatch) => {
        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to patch
            const response = await mylabs.get(`/laboratory/${labId}`)

            // send to the laboratory to reducer
            dispatch({ type: 'FETCH_LABORATORY', payload: { ...response.data.laboratory } });

            dispatch({ type: 'FETCH_LABORATORY_MANAGERS', payload: { ...response.data.labManagers }})

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}

export const updateLabManagersOnLab = (updatedLab) => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to patch
            const response = await mylabs.patch('/assign-manager', { ...updatedLab })

            // send to the updated lab to reducer
            dispatch({ type: 'UPDATE_LABORATORY', payload: { ...response.data.lab } });

            // send the updated lab managers to reducer
            dispatch({ type: 'UPDATE_LABORATORY_MANAGERS', payload: { ...response.data.labManagers } });

            // remove the loading indicator
            dispatch(loadingActions.remove());

            // message to the user that the school is created
            dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true
            }));

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}
