import React, { Fragment } from 'react'
import { Route } from 'react-router'

import Tab, { TabElement } from 'components/Utils/Tab'
import ExperimentCreate from './ExperimentCreate'
import ExperimentList from './ExperimentList'

const ExperimentTab = ({ match }) => {
    return (
        <Fragment>
            <Tab>
                <TabElement 
                    to="/experiment/create"
                    match={match}
                    title="  "
                />
                <TabElement 
                    to="/experiment"
                    match={match}
                    title=" "
                />
            </Tab>
            <Route 
                path="/experiment/create"
                exact
                component={ExperimentCreate}
            />
            <Route 
                path="/experiment"
                exact
                component={ExperimentList}
            />
        </Fragment>
    )
}

export default ExperimentTab
