import alertActions from '../../utilsActions/alertActions';
import loadingActions from '../../utilsActions/loadingActions';
import mylabs from '../../../apis/mylabs';
import catchHandler from '../../../helpers/catchHandler';

export const fetchEducationalSupervisorAccountRequirements = () => {
    return async (dispatch) => {
        try {

            dispatch(alertActions.clear());

            // show loading spinner
            dispatch(loadingActions.show());

            // make POST request to the API server
            const response = await mylabs.get('/educational-supervisor/create-requirements');

            // send the response to the reducers
            dispatch({ type: 'FETCH_FORM_CITIES', payload: response.data.cities });

            // send the response to the reducers
            dispatch({ type: 'FETCH_FORM_COURSES', payload: response.data.courses });
            
            // removing loading spinner after success
            dispatch(loadingActions.remove());
                        
        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}

export const createEducationalSupervisorAccountAction = (account) => {
    return async (dispatch) => {
        try {

            dispatch(alertActions.clear());

            // show loading spinner
            dispatch(loadingActions.show());

            // make POST request to the API server
            const response = await mylabs.post('/educational-supervisor', account);

            dispatch({ type: 'CREATE_SUPERVISOR_ACCOUNT', payload: response.data.educationalSupervisor })

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "     ",
                code: 200,
                success: true,
                path: `/educational-supervisor/${response.data.educationalSupervisor.id}`
            }));

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}

export const fetchEducationalSupervisorAccountsAction = (filter) => {
    return async (dispatch) => {
        try {

            dispatch(alertActions.clear());

            // show loading spinner
            dispatch(loadingActions.show());

            // make GET request to the API server
            const response = await mylabs.get(`/educational-supervisor`, {
                params: { ...filter }
            });

            dispatch({ type: 'FETCH_SUPERVISORS_ACCOUNTS', payload: response.data.educationalSupervisors })

            dispatch({ type: 'FETCH_SUPERVISORS_ACCOUNTS_TABLE', payload: { ...response.data } })

            // send the response to the reducers
            dispatch({ type: 'FETCH_FORM_CITIES', payload: response.data.cities });

            // send the response to the reducers
            dispatch({ type: 'FETCH_FORM_COURSES', payload: response.data.courses });


            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}


