import axios from 'axios';

// building custom api caller using axios
const mylabs = axios.create({
    baseURL: 'http://128.199.19.55/'
});

mylabs.interceptors.request.use((config) => {
    // adding middleware to automatically include the JWT token with each user request of exist 
    const token = localStorage.getItem('token');
    config.headers.Authorization =  token ? `Bearer ${token}` : '';
    config.headers['Content-Type'] = 'application/json';
    return config;
})

export default mylabs;