// @ts-nocheck
import React from 'react'
import { connect } from 'react-redux';

import { fetchDefectAction, updateDefectStatus } from 'actions/pagesActions/Support/defectAction';
import { Form } from 'components/Form/Form';
import FormHeader from 'components/Form/FormHeader';
import InputField from 'components/Form/InputField';
import ContentDivider from 'components/Utils/ContentDivider';
import ContentPadding from 'components/Utils/ContentPadding';
import NotFoundItem from 'components/Utils/NotFoundItem';
import TextArea from 'components/Form/TextArea';

const DefectView = ({ match, defect, fetchDefectAction, updateDefectStatus}) => {

    if(!defect.id) {
        fetchDefectAction(match.params.id);
    }

    const onConfirmSubmit = (e) => {
        e.preventDefault();
        updateDefectStatus(match.params.id, !defect.status);
    }

    const renderSubmitButton = () => {
        if(defect.id) {
            if(defect.status) {
                return (
                    <input className="btn cancel-btn no-margin-right" type="submit" value="  " />
                );
            } else {
                return (
                    <input className="btn submit-btn" type="submit" value=" " />
                );
            }
        }
    }

    return (
        <NotFoundItem
            referer="/defect"
        >
            <Form onSubmit={onConfirmSubmit}>
                <FormHeader 
                    label="  " 
                    icon="fas fa-chevron-left"
                    refererLabel="  "
                    referer="/defect"
                />
                <ContentPadding>
                    <div className="form-group">
                        <InputField
                            id="id"
                            name="id"
                            type="text"
                            initial={true}
                            initialValue={defect.userId}
                            locked={true}
                            label=" "
                            icon="fas fa-lock"
                            colSize="2"
                        />
                        <InputField
                            id="id"
                            name="id"
                            type="text"
                            initial={true}
                            initialValue={defect.email}
                            locked={true}
                            label=" "
                            icon="fas fa-lock"
                            colSize="2"
                        />
                    </div>
                    <div className="form-group">
                        <InputField
                            id="id"
                            name="id"
                            type="text"
                            initial={true}
                            initialValue={defect.phone}
                            locked={true}
                            label=" "
                            icon="fas fa-lock"
                            colSize="2"
                        />
                    </div>
                </ContentPadding>
                <FormHeader 
                    label=" " 
                />
                <ContentPadding>
                    <div className="form-group">
                        <InputField
                            id="id"
                            name="id"
                            type="text"
                            initial={true}
                            initialValue={defect.id}
                            locked={true}
                            label=" "
                            icon="fas fa-lock"
                            colSize="2"
                        />
                        <InputField
                            id="title"
                            name="title"
                            type="text"
                            initial={true}
                            initialValue={defect.title}
                            locked={true}
                            label=" "
                            placeholder=": -1"
                            icon="fas fa-signature"
                            colSize="2"
                        />
                    </div>
                    <div className="form-group">
                        <InputField
                            id="status"
                            name="id"
                            type="text"
                            initial={true}
                            label=" "
                            initialValue={defect.status ? '  ' : ' '}
                            locked={true}
                            icon={defect.status === true ? 'fas fa-check' : 'fas fa-history'}
                            colSize="2"
                            className={defect.status ? "color-primary" : 'color-warning' }
                        />
                    </div>
                    <div className="form-group">
                        <TextArea
                            id="status"
                            name="id"
                            type="text"
                            initial={true}
                            initialValue={defect.text}
                            locked={true}
                            label=" "
                            icon="fas fa-lock"
                            colSize="1"
                        />
                    </div>
                </ContentPadding>
                <ContentPadding>
                    <ContentDivider />
                    <div className="form-group confirm-group margin-top-big">
                        { renderSubmitButton() } 
                    </div>
                </ContentPadding>
            </Form>
        </NotFoundItem>
    )
}

const mapStateToProps = (state, ownProps) => {
    let defect = state.defects[ownProps.match.params.id]; 
    return {
        defect: defect
    }
}

DefectView.defaultProps = {
    defect: {
        id: '',
        title: '',
        status: false,
        text: '',
        userId: '',
        email: '',
        phone: '',
    }
}

export default connect(mapStateToProps, {
    fetchDefectAction,
    updateDefectStatus
})(DefectView);