import { userConstants } from '../../actions/types';

const laboratoryQuestionsReducer = (state={}, action) => {
    switch(action.type) {
        case 'FETCH_LABORATORY_EVALUATE_QUESTIONS':
            return { ...action.payload };
        case userConstants.LOGOUT:
            return {};
        default:
            return state;
    }
}

export default laboratoryQuestionsReducer;