import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom';

function Tab({ children })  {

    return (
        <div className="tabs-container">
            { children }
        </div>
    )
}

export default Tab;

export const TabElement = ({ 
    to,
    title,
    match
}) => {

    const [className, setClassName ] = useState('');

    useEffect(() => {
        // pass element pathname on match change
        setActiveTab(to);
    })

    const setActiveTab = (pathname) => {
        // custome method to set the active tab upon given pathname
        if(pathname === match.url) {
            setClassName('tab-btn-active')
        } else {
            setClassName('')
        }
    }

    const onTabClick = (e) => {
        // pass the clicked pathname
        setActiveTab(e.target.pathname);
    }

    return (
        <Link 
            to={to}
            className={`tab-btn ${className}`}
            onClick={onTabClick}
        >
            { title }
        </Link>
    );
} 