// @ts-nocheck
import React, { Component } from 'react';
import { connect } from 'react-redux';

import CustomDropdown from 'components/Form/CustomDropdown';
import { Form } from 'components/Form/Form';
import FormHeader from 'components/Form/FormHeader';
import InputField from 'components/Form/InputField';
import ContentDivider from 'components/Utils/ContentDivider';
import ContentPadding from 'components/Utils/ContentPadding';
import NotFoundItem from 'components/Utils/NotFoundItem';
import { validateEmpty, validateEqualPropsObjects } from 'helpers/validate';
import alertActions from '../../../actions/utilsActions/alertActions';
import Alert from 'components/Utils/Alert';
import { fetchEducationalSupervisorAccountAction, updateEducationalSupervisorAccountAction } from 'actions/pagesActions/SystemAdministrator/supervisorActions';
import { getLabelObject } from 'helpers/objectsHelpers';

class EducationalSupervisorCreate extends Component {

    constructor(props) {
        super(props);
        this.baseState = this.state;
        this.cityDropdownRef = React.createRef();
        this.areasDropdownRef = React.createRef();
        this.courseDropdownRef = React.createRef();
    }

    state = {
        name: '',
        email: '',
        phone: '',
        cityId: '',
        areaId: '',
        courseId: '',
        errorMessages: []
    }

    async componentDidMount() {
        if(!this.props.cities.length || !this.props.courses.length || !this.props.supervisorAccount) {
            await this.props.fetchEducationalSupervisorAccountAction(this.props.match.params.id);
        }
        this.settingState();
    }

    componentDidUpdate(nextProps) {
        if(!validateEqualPropsObjects(nextProps.supervisorAccount, this.props.supervisorAccount) && nextProps.supervisorAccount) {
            this.settingState();
        }
    }

    settingState = () => {
        if(this.props.supervisorAccount) {
            // set state here with dropdown menues
            this.setState({ ...this.props.supervisorAccount, errorMessages: [] }, async () => {
                const city = getLabelObject(this.state.cityId, this.props.cities);
                const area = getLabelObject(this.state.areaId, city.areas);
                const course = getLabelObject(this.state.courseId, this.props.courses);
                await this.cityDropdownRef.current.selectItem(city);
                this.areasDropdownRef.current.selectItem(area);
                this.courseDropdownRef.current.selectItem(course);
            })
        }
    }

    validateUpdatedAccount = () => {
        return [
            validateEmpty(this.state.courseId, '      *'),
            validateEmpty(this.state.cityId, '      *'),
            this.state.cityId && validateEmpty(this.state.areaId, '       *')
            // The array filter method will drop empty messages
        ].filter((message) => message)
    }

    updateAccount = async () => {
        if(this.state.errorMessages.length === 0) {
            // calling the register action after validating that user inputs is in correct format
            await this.props.updateEducationalSupervisorAccountAction({
                educationalSupervisorId: this.state.id,
                courseId: this.state.courseId,
                areaId: this.state.areaId
            });
            this.settingState();
        }
    }

    onFieldChange = (value, fieldName) => {
        switch(fieldName) {
            case 'cityId':
                this.setState( {  
                    cityId: value, 
                    areaId: '' , 
                    areas: getLabelObject(value, this.props.cities).areas 
                }, () => {
                    // after choosing city, clearSelection of area dropdown
                    this.areasDropdownRef.current.clearSelection()
                });
                break;
            default:
                this.setState({ [fieldName]: value });
        }
    }

    onUpdateSupervisorAccountSubmit = (e) => {
        e.preventDefault();
        // clean the alerts
        this.props.clearAlert();

        // getting the user property from the state
        this.setState({
            errorMessages: this.validateUpdatedAccount()
        }, () => {
            this.updateAccount();
        });
    }

    onResetHandler = () => {
        this.settingState();
    }

    render() {
        return (
            <NotFoundItem
                referer="/"
            >
                <Form onSubmit={this.onUpdateSupervisorAccountSubmit} >
                    <FormHeader 
                        label=" "
                        referer="/educational-supervisor"
                        icon="fas fa-chevron-left"
                        refererLabel=" "
                    />
                    <ContentPadding>
                        <Alert messages={this.state.errorMessages} type="404" />
                        <div className="form-group">
                            <InputField
                                name="name"
                                type="text"
                                initial={true}
                                initialValue={this.state.name}
                                id="first-name"
                                label=" "
                                locked={true}
                                icon="fas fa-user"
                                colSize="2"
                            />
                            <InputField
                                id="email"
                                name="email"
                                type="text"
                                initial={true}
                                initialValue={this.state.email}
                                locked={true}
                                label=" "
                                icon="fas fa-at"
                                colSize="2"
                            />
                        </div>
                        <div className="form-group">
                            <InputField
                                name='phone'
                                type="tel"
                                id="phone"
                                initial={true}
                                initialValue={this.state.phone}
                                locked={true}
                                label=" "
                                icon="fas fa-phone"
                                colSize="2"
                            />
                            <CustomDropdown 
                                onChange={(courseId) => this.onFieldChange(courseId, 'courseId')}
                                colSize="2"
                                list={this.props.courses}
                                label=" "
                                title='  '
                                setRef={(ref) => this.courseDropdownRef = ref}
                            />
                        </div>
                        <div className="form-group">
                            <CustomDropdown 
                                onChange={(cityId) => this.onFieldChange(cityId, 'cityId')}
                                colSize="2"
                                list={this.props.cities}
                                label=" "
                                title=' '
                                setRef={(ref) => this.cityDropdownRef = ref}
                            />
                            <CustomDropdown
                                onChange={(areaId) => this.onFieldChange(areaId, 'areaId')}
                                list={this.state.areas ? this.state.areas : []}
                                title='  '
                                label="  "
                                setRef={(ref) => this.areasDropdownRef = ref}
                                colSize="2"
                            />
                        </div>
                        <ContentDivider />
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value="" />
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                        </div>
                    </ContentPadding>
                </Form>
            </NotFoundItem>
        );
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        supervisorAccount: state.educationalSupervisorsAccounts[ownProps.match.params.id],
        cities: Object.values(state.cities),
        courses: Object.values(state.courses)
    }
}

export default connect(mapStateToProps, {
    fetchEducationalSupervisorAccountAction,
    updateEducationalSupervisorAccountAction,
    clearAlert: alertActions.clear
 })(EducationalSupervisorCreate);