export const fetchLabManagerAction = (labManagerId) => {
    return async (dispatch) => {
        try {
            dispatch(loadingActions.show());

            // fetch lab manager info
            const response = await mylabs.get(`/laboratory-manager/${labManagerId}`);

            // get all the labs in the school, to show in drop down
            dispatch({ type: 'FETCH_LABORATORIES', payload: response.data.laboratories});
            
            // send the lab manager to store it in redux store
            dispatch({ type: 'FETCH_LABORATORY_MANAGER', payload: response.data.laboratoryManager});

            dispatch(loadingActions.remove());

        } catch (err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}

export const updateLabManagerLabs = (updatedLabManager) => {
    // PATCH labManager labs
    return async (dispatch) => {
        try {

            // show loading spinner
            dispatch(loadingActions.show());

            // make PATCH request to the API server
            const response = await mylabs.patch(`/assign-laboratory`, { ...updatedLabManager });

            // send updated lab manager to reducers
            dispatch({ type: 'UPDATE_LABORATORY_MANAGER', payload: response.data.laboratoryManager });

            // after updating the lab manager, get also the updated laboratories
            dispatch({ type: 'UPDATE_LABORATORIES', payload: response.data.laboratories });

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "     ",
                code: 200,
                success: true
            }));

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}