import React from 'react'
import QRCode from 'qrcode.react';

class CustomeQRCode extends React.Component {

    constructor(props) {
        super(props);
        this.componentRef = React.createRef();
    }

    render() {
        
        return (
        <div
            ref={this.componentRef}
        >
            <QRCode
                size={200}
                value={this.props.url}
            />
        </div>        
        )
    }
}

export default CustomeQRCode;