export const unitsList = [
    {
        label: 'piece',
        value: 'piece'
    },
    {
        label: 'cm',
        value: 'centimeter'
    },
    {
        label: 'g',
        value: 'gram'
    },
    {
        label: 'ml',
        value: 'millimeter'
    },
    {
        label: 'kg',
        value: 'kilogram'
    },
    {
        label: 'mg',
        value: 'milligram'
    },
    {
        label: 'oz',
        value: 'ounces'
    }
];

export const getItemUnit = (value) => {
    for(let item of unitsList) {
        if(item.value.toLowerCase() === value.toLowerCase()) {
            return item;
        }
    }
}

