import mylabs from '../../../apis/mylabs';
import catchHandler from '../../../helpers/catchHandler';
import loadingActions from '../../utilsActions/loadingActions';
import alertActions from '../../utilsActions/alertActions';
import history from '../../../history';
import { renewJwtAction } from '../../utilsActions/jwtAction';

export const fetchMemberInvites = () => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to get
            const response = await mylabs.get('/invitation-school')

            // send to the invites reducer
            dispatch({ type: 'FETCH_MEMBER_INVITES', payload: response.data.invitations });

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}

export const rejectMemberInvite = (schoolId) => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to get
            await mylabs.delete(`/invitation-school/${schoolId}`)

            // send to the invites reducer
            dispatch({ type: 'REJECT_MEMBER_INVITE', payload: schoolId });

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}

export const acceptMemberInvite = (schoolId) => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to get
            const response = await mylabs.post(`/invitation-school`, { schoolId })

            // send to the invites reducer
            dispatch({ type: 'ACCEPT_MEMBER_INVITE' });

            // Renew the Token here first
            dispatch(renewJwtAction(response.data.token));

            // navigate the user to the schoolView page, 
            history.push('/');

            // message to the user that the school is created
            dispatch(alertActions.success({
                message: "   ,     ",
                code: 200,
                success: true
            }));
            
            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}
