import React from 'react'

const Card = ({ children, title }) => {
    return (
        <div className="dashboard-card">
            <div className="dashboard-overview-title">{ title }</div>
            <div className="dashboard-card__content">
                <div className="dashboard-card__content--value">{ children }</div>
            </div>
        </div>
    )
}

export default Card;
