import React from 'react'

const List = ({ title, colHeaders=[], data=[] }) => {

    const getLabelHeaders = () => {
        return colHeaders.map((col) => col.label ).reverse();
    }

    const getKeys = () => {
        // return keys of colHeaders
        return colHeaders.map((col) => col.key ).reverse();
    }

    const getHeader = () => {
        // contain the logic that return our header component
        var keys = getLabelHeaders();
        return keys.map((key) => {
            return <div className="dashboard-list__header--title" key={key}>{key.toUpperCase()}</div>
        });
    }

    const getRowsData = () => {
        // contain logic to iterate through the rows data and build it
        let items = data;
        let keys = getKeys();
        return items.map((row, index) => {
            return (
                <div key={index} className="dashboard-list__row">
                    <RenderRow keys={keys} data={row}  />
                </div>
            );
        })
    }

    return (
        <div className="dashboard-list-container">
            <div className="dashboard-overview-title"> {title} </div>
            <div className="dashboard-list">
                <div className="dashboard-list__header">
                    { getHeader() }
                </div>
                <div className="dashboard-list__body">
                    { getRowsData() }
                </div>
            </div>
        </div>
    );
}

export default List;


const RenderRow = (props) => {
    // This component responsible to render each row 
    if(props.data) {
        return props.keys.map((key) => {
            return <div className="dashboard-list__row--value" key={props.data[key]+key}>{ props.data[key] }</div>
        })
    } else {
        return null;
    }
}