// @ts-nocheck
import React, { Component } from 'react';
import { connect } from 'react-redux';

import ListLayout from '../../../components/List/ListLayout';
import PaginatedList from '../../../components/List/PaginatedList';
import SearchFilter from '../../../components/List/SearchFilter';
import InputField from '../../../components/Form/InputField';
import { clearTableListAction } from '../../../actions/utilsActions/clearTableListAction';
import alertActions from '../../../actions/utilsActions/alertActions';
import { fetchExperimentsAction } from '../../../actions/pagesActions/SystemAdministrator/experimentAction';
import CustomDropdown from 'components/Form/CustomDropdown';

const COLUMN_EXPERIMENT_HEADERS = [
    {
        label: '',
        key: 'id'
    },
    {
        label: ' ',
        key: 'title'
    },
    {
        label: ' ',
        key: 'courseName'
    },
    {
        label: '',
        key: 'path'
    }
]

class ExperimentList extends Component {

    constructor(props) {
        super(props);
        this.coursesRef = React.createRef();

        this.state = {
            page: 1,
            id: '',
            experimentName: '',
            courseId: 'all'
        }
        this.baseState = { ...this.state };
    }
    
    componentDidMount() {
        if(this.coursesRef.current) {
            this.coursesRef.current.selectItem({label: ' ', value: 'all'})
        }
    }

    onPageChange = (currentPage) => {
        // clear alerts, becasue when item deleted, it will be send here
        this.props.clearAlert();

        // set the currentPage, and make API call after that
        this.setState({ page: currentPage.selected + 1 }, () => {
            // This will make the call when the component mount first
            this.props.fetchExperimentsAction(this.state);
            
        });
    }

    onFiltersChange = (value, fieldName) => {
        this.setState({ [fieldName]: value });
    }

    onFilterSubmit = (e) => {
        // after submit, set the filter submit params, and after that make API request
        e.preventDefault();
        this.setState({ page: 1 }, () => {
            this.props.fetchExperimentsAction(this.state);
        })
    }

    onFilterReset = () => {
        this.setState({ ...this.baseState }, () => {
            // reset dropmenu
            if(this.coursesRef.current) {
                this.coursesRef.current.selectItem({label: ' ', value: 'all'})
            }
        });
    }

    render() {
        return (
            <ListLayout>
                <PaginatedList 
                    totalPages={this.props.totalPages}
                    data={ this.props.experiments }
                    onPageChange={this.onPageChange}
                    colHeaders = {COLUMN_EXPERIMENT_HEADERS}
                    initialPage={this.state.page - 1}
                />
                <SearchFilter onSubmit={this.onFilterSubmit}>
                    <div className="form-group">
                        <InputField 
                            id="id"
                            name="id"
                            initial={true}
                            initialValue={this.state.id}
                            onChangeHandler={(e) => this.onFiltersChange(e.target.value, 'id')}
                            colSize="1"
                            icon="fas fa-search"
                            placeholder="  "
                        />
                    </div>
                    <div className="form-group">
                        <InputField 
                            id="experimentName"
                            name="experimentName"
                            initial={true}
                            initialValue={this.state.experimentName}
                            onChangeHandler={(e) => this.onFiltersChange(e.target.value, 'experimentName')}
                            colSize="1"
                            placeholder="  "
                            icon="fas fa-search"
                        />
                    </div>
                    <div className="form-group">
                        <CustomDropdown 
                            colSize="1"
                            title=" "
                            setRef={(ref) => this.coursesRef = ref}
                            list={[{label: ' ', value: 'all', id: 'all'}, ...this.props.courses]}
                            onChange={(value) => this.onFiltersChange(value, 'courseId')}
                        />
                    </div>
                    <div className="form-group confirm-group margin-top-big">
                        <input className="btn submit-btn" type="submit" value="" />
                        <input className="btn cancel-btn" type="reset" value=" " onClick={ this.onFilterReset }/>
                    </div>
                </SearchFilter>
            </ListLayout>
        )
    }
    componentWillUnmount() {
        // clear the table list in redux store when unmount
        this.props.clearTableListAction();
    }
}

const mapStateToProps = state => {
    return {
        experiments: state.tableList.experiments,
        totalPages: state.tableList.totalPages,
        courses: Object.values(state.courses)
    }
}

export default connect(mapStateToProps, { 
    fetchExperimentsAction,
    clearTableListAction, 
    clearAlert: alertActions.clear 
})(ExperimentList);
