// @ts-nocheck
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { fetchLaboratoriesAction } from '../../../actions/pagesActions/SchoolMember/LaboratoryManager/LaboratoriesAction';

import PreviewList from '../../../components/List/PreviewList/PreviewList';
import PreviewListContainer from '../../../components/List/PreviewList/PreviewListContainer';
import NotAllowed from '../../../components/Utils/NotAllowed';

class LaboratoriesList extends Component {

    componentDidMount() {
        // action call for the available invites
        if(this.props.hasSchool && !this.props.laboratories.length) {
            // action to load laboratories
            this.props.fetchLaboratoriesAction();
            
        }
    }

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.hasSchool}
                referer="/invites"
                statusCode="403"
                message="       "
                btnLabel="   "
            >
                <PreviewListContainer>
                    <PreviewList 
                        list={this.props.laboratories}
                        className="full"
                        tableClassName="fixed-big-height"
                        listHeader="  "
                        emptyMessage="    "
                        previewTitleMessage="  : "
                        title="id"
                    />
                </PreviewListContainer>
            </NotAllowed>
        );
    }
}

const mapStateToProps = state => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
        laboratories: Object.values(state.laboratories)
    }
}

export default connect(mapStateToProps, { 
    fetchLaboratoriesAction
})(LaboratoriesList);