import React from 'react'
import { Link } from 'react-router-dom';

function NotFound({ statusCode, message, referer, btnLabel, className }) {
    // this component for Not-Found Routes

    return (
        <div className={`resource-not-found ${className ? className : ''}`}>
            <p className="resource-not-found__code">{statusCode}</p>
            <p className="resource-not-found__message">{message}</p>
            <Link to={referer} className="resource-not-found__referer btn">{btnLabel}</Link>
        </div>
    );
}

export default NotFound;
