import mylabs from "../../../apis/mylabs";
import catchHandler from "../../../helpers/catchHandler";
import history from "../../../history";
import alertActions from "../../utilsActions/alertActions";
import loadingActions from "../../utilsActions/loadingActions";


export const createCourseAction = (course) => {
    return async (dispatch) => {

        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make POST request to the API server
            const response = await mylabs.post('/course', course);

            // removing loading spinner after success
            dispatch(loadingActions.remove());

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true,
                path: `/course/${response.data.id}`
            }));

            dispatch({ type: 'CREATE_FORM_COURSE', payload: {
                label: course.name,
                value: response.data.id
            }});

            
        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }

    }
}

export const fetchCoursesAction = (filterParams) => {
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());
    
            // make GET request to the API server
            const response = await mylabs.get('/course', {
                params: filterParams
            });
    
            // send the response to the reducers
            dispatch({ type: 'FETCH_COURSES', payload: response.data });
    
            // removing loading spinner after success
            dispatch(loadingActions.remove());
            
        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    
    }
}

export const fetchCourseAction = (courseId) => {
    return async (dispatch) => {
        try {

            // show loading spinner
            dispatch(loadingActions.show());
    
            // make GET request to the API server
            const response = await mylabs.get(`/course/${courseId}`);
    
            // send the response to the reducers
            dispatch({ type: 'FETCH_COURSE', payload: response.data });
            
            // getting the up to date course
            dispatch({ type: 'FETCH_UP_TO_DATE_FORM_COURSE', payload: {
                label: response.data.course.name,
                value: response.data.course.id
            }});

            // removing loading spinner after success
            dispatch(loadingActions.remove());
            
        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}


export const patchCourseAction = (course) => {
    // PATCH course
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make PATCH request to the API server
            const response = await mylabs.patch(`/course`, { ...course, newName: course.name });

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true
            }));

            dispatch({ type: 'PATCH_FORM_COURSE', payload: {
                label: course.name,
                value: course.id
            }});

            dispatch({ type: 'FETCH_COURSE', payload: response.data });

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}


export const deleteCourseAction = (courseId) => {
    // DELETE course
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make PATCH request to the API server
            await mylabs.delete(`/course/${courseId}`);

            // after deleting the course, navigate the user to corses list
            history.push('/course');

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true
            }));

            dispatch({ type: 'DELETE_FORM_COURSE', payload: courseId });

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}