// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import { createItemAction } from '../../../actions/pagesActions/SystemAdministrator/itemAction';
import CustomDropdown from '../../../components/Form/CustomDropdown';
import Form from '../../../components/Form/Form';
import FormHeader from '../../../components/Form/FormHeader';
import InputField from '../../../components/Form/InputField';
import Alert from '../../../components/Utils/Alert';
import ContentPadding from '../../../components/Utils/ContentPadding';
import { unitsList } from '../../../helpers/fixedLists';
import ContentDivider from '../../../components/Utils/ContentDivider';
import CheckBox from 'components/Form/CheckBox';
import { validateItem } from './validateItem';

class ItemCreate extends Component {

    constructor(props) {
        super(props);

        this.state = {
            name: '',
            unit: '',
            safety: false,
            errorMessages: []
        }
        this.baseState = { ...this.state };
    }

    componentDidMount() {
        this.dropdownRef = React.createRef();
    }

    createItem = async () => {
        if(this.state.errorMessages.length === 0) {
            // Action call to create item
            await this.props.createItemAction({
                name: this.state.name,
                unit: this.state.unit,
                safety: this.state.safety
            })

            if(this.props.status) {
                // after success api call, reset fiels
                this.onResetHandler();
            }
        }
    }

    onFieldChange = (value, fieldName) => {
        this.setState({ [fieldName]: value });
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        // performing validation and setting messages
        this.setState({
            errorMessages: validateItem(this.state)
        }, () => {
            this.createItem();
        });
    }

    onResetHandler = () => {
        // reset form values
        this.setState({ ...this.baseState }, () => {
            this.dropdownRef.current.clearSelection();
        });
    }

    render() {
        return (
            <Form onSubmit={this.onFormSubmit}>
                <FormHeader label=" " />
                <ContentPadding>
                    <Alert messages={this.state.errorMessages} type="error" />
                    <div className="form-group">
                        <InputField
                            id="name"
                            name="name"
                            type="text"
                            onChangeHandler={(e) => this.onFieldChange(e.target.value, 'name')}
                            initial={true}
                            initialValue={this.state.name}
                            label=" "
                            placeholder=":   "
                            icon="fas fa-signature"
                            colSize="2"
                        />
                        <CustomDropdown 
                            colSize="2"
                            onChange={(selectedUnit) => this.onFieldChange(selectedUnit, 'unit')}
                            list={unitsList}
                            title='  '
                            label=" "
                            setRef={(ref) => this.dropdownRef = ref}
                        />
                    </div>
                    <div className="form-group">
                        <CheckBox 
                            label=" "
                            initial={true}
                            initialValue={this.state.safety}
                            onChange={(checked) => this.onFieldChange(checked, 'safety')}
                        />
                    </div>
                    <ContentDivider />
                    <div className="form-group confirm-group margin-top-big">
                        <input className="btn submit-btn" type="submit" value=""/>
                        <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                    </div>
                </ContentPadding>
            </Form>
        )
    }
}

const mapStateToProps = state => {
    return {
        status: state.alert.success
    }
}

export default connect(mapStateToProps, { createItemAction })(ItemCreate);
