// @ts-nocheck
import React, { useEffect, useState } from 'react'
import RadioOption from './RadioOption'

const RadioQuestion = ({ question, onChange, initialValue }) => {

    const [answer, setAnswer] = useState(initialValue);

    useEffect(() => {
        setAnswer(initialValue);
    }, [initialValue])

    const onChangeOption = (newAnswer) => {
        setAnswer(newAnswer);
        onChange(newAnswer);
    }

    const renderOptions = () => {
        return question.options.map((option) => {
            return (
                <RadioOption
                    key={option.value+''+question.id+''+answer}
                    id={option.value+''+question.id}
                    optionLabel={option.label}
                    value={option.value}
                    questionId={question.id}
                    onClick={(newAnswer) => onChangeOption(newAnswer)}
                    answer={answer}
                />
            );
        })
    }

    return (
        <div className="radio-question">
            <p className="radio-question__question-text">{ question.text }</p>
            <div className="radio-question__options-container">
                { renderOptions() }
            </div>
        </div>
    )
}

export default RadioQuestion;
