import React, { Fragment } from 'react'
import { Route } from 'react-router'

import Tab, { TabElement } from 'components/Utils/Tab'
import SemesterCreate from './SemesterCreate'
import SemestersList from './SemestersList'

const SemesterTab = ({ match }) => {
    return (
        <Fragment>
            <Tab>
                <TabElement 
                    to="/semester/create"
                    match={match}
                    title="  "
                />
                <TabElement 
                    to="/semester"
                    match={match}
                    title="  "
                />
            </Tab>
            <Route 
                path="/semester/create"
                exact
                component={SemesterCreate}
            />
            <Route 
                path="/semester"
                exact
                component={SemestersList}
            />
        </Fragment>
    )
}

export default SemesterTab
