// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import { Form } from '../../../components/Form/Form';
import FormHeader from '../../../components/Form/FormHeader';
import InputField from '../../../components/Form/InputField';
import Alert from '../../../components/Utils/Alert';
import ContentPadding from '../../../components/Utils/ContentPadding';
import { validateEmpty } from '../../../helpers/validate';
import alertActions from '../../../actions/utilsActions/alertActions';
import { deleteCourseAction, fetchCourseAction, patchCourseAction } from '../../../actions/pagesActions/SystemAdministrator/courseAction';
import NotFoundItem from '../../../components/Utils/NotFoundItem';
import ContentDivider from '../../../components/Utils/ContentDivider';

class CourseView extends Component {

    async componentDidMount () {
        // API action call
        await this.props.fetchCourseAction(this.props.match.params.id);
        this.settingState();
    }

    state = {
        id: '',
        name: '',
        errorMessages: []
    }

    settingState = () => {
        if(this.props.course) {
            this.setState({ ...this.props.course, errorMessages: [] });
        }
    }

    validateCourse = () => {
        return [
            validateEmpty(this.state.name, '      ')
        ].filter(message => message);
    }

    updateCourse = async () => {
        if(this.state.errorMessages.length === 0 && this.state.name !== this.props.course.name) {
            // createAction call here
            await this.props.patchCourseAction({ ...this.state })
            this.onResetHandler();
        }
    }

    onNameChangeHandler = (e) => {
        this.setState({ name: e.target.value });        
    }

    onFormSubmit = (e) => {
        e.preventDefault();

        // clear server alert at submit
        this.props.clearAlert();
        
        this.setState({ 
            errorMessages: this.validateCourse()
         }, () => {
            this.updateCourse();
         });
    }

    onResetHandler = () => {
        // remove alerts
        this.props.clearAlert();
        // reset values
        this.settingState();
    }

    onDeleteHandler = () => {
        this.props.deleteCourseAction(this.state.id);
    }
    
    render() {
        return (
            <NotFoundItem
                referer="/course"
            >
                <Form onSubmit={this.onFormSubmit}>
                    <FormHeader 
                        label=" " 
                        icon="fas fa-chevron-left"
                        refererLabel="  "
                        referer="/course"
                    />
                    <ContentPadding>
                        <Alert messages={this.state.errorMessages} type="error" />
                        <div className="form-group">
                            <InputField
                                id="id"
                                name="id"
                                type="text"
                                initial={true}
                                initialValue={this.state.id}
                                locked={true}
                                label=" "
                                icon="fas fa-lock"
                                colSize="2"
                            />
                            <InputField
                                id="name"
                                name="name"
                                type="text"
                                onChangeHandler={this.onNameChangeHandler}
                                initial={true}
                                initialValue={this.state.name}
                                label=" "
                                placeholder=": -1"
                                icon="fas fa-signature"
                                colSize="2"
                            />
                        </div>
                        <ContentDivider />
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value="" />
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                            <input className="btn delete-btn" type="reset" value="" onClick={this.onDeleteHandler}/>
                        </div>
                    </ContentPadding>
                </Form>
            </NotFoundItem>
        )
    }
}

const mapStateToProps = state => {
    return {
        course: state.itemView.course
    }
}

export default connect(mapStateToProps, { 
    fetchCourseAction, 
    clearAlert: alertActions.clear ,
    patchCourseAction,
    deleteCourseAction
})(CourseView);
