// @ts-nocheck
import React from 'react'
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

import { 
    validateMatchedEmail, 
    validateName, 
    validatePassword, 
    validateSaudiPhoneNumber, 
    validateRoleType, 
    validateEmail,
    validateMatchedPasswords 
} from 'helpers/validate';
import ContentDivider from 'components/Utils/ContentDivider';
import CustomDropdown from 'components/Form/CustomDropdown';
import InputField from 'components/Form/InputField';
import PublicForm from 'components/Form/PublicForm';
import Alert from 'components/Utils/Alert';
import { registerAction, logoutAction } from 'actions/userActions';
import alertActions from 'actions/utilsActions/alertActions';
import LoadingOverlay from 'react-loading-overlay';

const rolesList = [
    {
        label: ' ',
        value: 'School_Manager'
    },
    {
        label: ' ',
        value: 'Science_Teacher'
    },
    {
        label: ' ',
        value: 'Laboratory_Manager'
    }
]

class Register extends React.Component {

    componentDidMount() {
        // logout to clean the localStorage from old JWT token 
        this.props.logoutAction();
    }
    
    state = {
        password: '',
        secondPassword: '',
        fname: '',
        lname: '',
        mname: '',
        email: '',
        secondEmail: '',
        phone: '',
        role: '',
        errorMessages: []
    }

    handleChange = (event) => {
        // handle user input change dynamically using names
        const { name, value } = event.target;
        this.setState({
            [name]: value
        });
    }

    setRole = (selectedRole) => {
        // The role is in dropbox, so its easier to give its own setState
        this.setState({
            role: selectedRole
        })
    }

    validateUserData = () => {
        const user = this.state;

        return [
            validateSaudiPhoneNumber(user.phone),
            validateName([user.fname, user.mname, user.lname]),
            validatePassword(user.password),
            validateMatchedPasswords(user.password, user.secondPassword),
            validateEmail(user.email),
            validateMatchedEmail(user.email, user.secondEmail),
            validateRoleType(user.role)
            // The array filter method will drop empty messages
        ].filter((message) => message)
    }

    registerUser = () => {
        if(this.state.errorMessages.length === 0) {
            // calling the register action after ensuring no validate error messages
            this.props.registerAction({ ...this.state });
        }
    }

    onRegisterSubmit = (e) => {
        e.preventDefault();
        // clean the alerts
        this.props.clearAlert();

        // getting the user property from the state
        this.setState({
            errorMessages: this.validateUserData()
        }, () => {
            this.registerUser();
        });
    }

    render() {
        return (
            <div className="form-overlay">
                <LoadingOverlay
                    active={this.props.isLoading}
                    text=" "
                    spinner
                >
                    <PublicForm 
                        onSubmit={this.onRegisterSubmit} 
                        type="register"
                    >
                        <Alert messages={[...this.state.errorMessages, this.props.alert.message]} type="error" />
                        <div className="form-group">
                            <InputField
                                name="fname"
                                onChangeHandler={this.handleChange}
                                type="text"
                                id="first-name"
                                placeholder=" "
                                icon="fas fa-user"
                                colSize="3"
                            />
                            <InputField
                                name="mname"
                                onChangeHandler={this.handleChange}
                                type="text"
                                id="middle-name"
                                placeholder=" "
                                icon="fas fa-user"
                                colSize="3"
                            />
                            <InputField
                                name="lname"
                                onChangeHandler={this.handleChange}
                                type="text"
                                id="last-name"
                                placeholder=" "
                                icon="fas fa-user"
                                colSize="3"
                            />
                        </div>
                        <div className="form-group">
                            <InputField
                                name="email"
                                onChangeHandler={this.handleChange}
                                type="text"
                                id="email"
                                placeholder=" "
                                icon="fas fa-at"
                                colSize="2"
                            />
                            <InputField
                                name="secondEmail"
                                onChangeHandler={this.handleChange}
                                type="text"
                                id="secondEmail"
                                placeholder="  "
                                icon="fas fa-at"
                                colSize="2"
                            />
                        </div>
                        <div className="form-group">
                            <InputField 
                                name="password"
                                onChangeHandler={this.handleChange}
                                type="password"
                                id="password"
                                placeholder=" "
                                icon="fas fa-key"
                                colSize="2"
                            />
                            <InputField
                                name="secondPassword"
                                onChangeHandler={this.handleChange}
                                type="password"
                                id="second-password"
                                placeholder="  "
                                icon="fas fa-key"
                                colSize="2"
                            />
                        </div>
                        <div className="form-group">
                            <InputField
                                name='phone'
                                onChangeHandler={this.handleChange}
                                type="tel"
                                id="phone"
                                placeholder=" "
                                icon="fas fa-phone"
                                colSize="2"
                            />
                            <CustomDropdown 
                                onChange={this.setRole}
                                colSize="2"
                                list={rolesList}
                                title=' '
                            />
                        </div>
                        <div className="form-group">
                            <div className="form-group__1-col form-group__1-col--center">
                                <input type="submit" className="form__submit" value=""/>
                            </div>
                        </div>
                        <div className="form-group">
                            <ContentDivider label="  "/>
                        </div>
                        <div className="form-group">
                            <div className="form-group__1-col form-group__1-col--center">
                                <Link className="btn btn--white" to="/login">  </Link>
                            </div>
                        </div>
                    </PublicForm>
                </LoadingOverlay>
            </div>

        );
    }
}

const mapStateToProps = (state) => {
    return {
        alert: state.alert,
        isLoading: state.isLoading
    }
}

export default connect(mapStateToProps, 
    { 
        registerAction, 
        logoutAction, 
        clearAlert: alertActions.clear 
    })(Register);