import mylabs from '../../../../apis/mylabs';
import catchHandler from '../../../../helpers/catchHandler';
import loadingActions from '../../../utilsActions/loadingActions';

export const fetchLaboratoriesAction = () => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to get school info
            const response = await mylabs.get(`/school-laboratory`)

            // send all laboratories to reducer
            dispatch({ type: 'FETCH_LABORATORIES', payload: response.data.laboratories });

            // send ministry items to the reducer
            dispatch({ type: 'FETCH_FORM_ITEMS', payload: response.data.ministryItems });

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}

export const fetchLaboratoryAction = (labId) => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to get school info
            const response = await mylabs.get(`/school-laboratory/${labId}`)

            // send all laboratories to reducer
            dispatch({ type: 'FETCH_LABORATORY', payload: response.data.laboratory });

            // send ministry items to the reducer
            dispatch({ type: 'FETCH_FORM_ITEMS', payload: response.data.ministryItems });

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}