import _ from "lodash";

export const getItemsDifferenceStatus = (oldItems, newItems) => {
    // get added items, by getting difference [newItems - oldItems]
    const addedItems = _.differenceWith(newItems, oldItems, _.isEqual)
        .map(item => ({...item, status: 'add'}));
    // get deleted items, by getting difference [oldItems - newItems]
    const deletedItems = _.differenceWith(oldItems, newItems, _.isEqual)
        .map(item => ({...item, status: 'delete'}));
    // get same items, by getting [old items intersect newItems]
    const intersectedItems = _.intersectionWith(oldItems, newItems, _.isEqual)
        .map(item => ({...item, status: 'same'}));
    return [ ...addedItems, ...deletedItems, ...intersectedItems ]
}

export const getItemsDifferenceStatusWithUpdate = (oldItems, newItems, itemId, itemUpdatedField) => {
    // get add, delete and same items status
    let differentStatusItems = getItemsDifferenceStatus(oldItems, newItems);
    // get updated items, by getting [newItems intersect oldItems using custom function 'isUpdated']
    const updatedItems = _.intersectionWith(newItems, oldItems, (obj1, obj2) => {
        return isUpdated(obj1, obj2, itemId, itemUpdatedField)
    }).map(item => ({...item, status: 'update'}));
    // get the differnece by [differentStatusItems - updatedItems]
    differentStatusItems = _.differenceWith(differentStatusItems, updatedItems, (obj1, obj2) => {
        return isSameId(obj1, obj2, itemId)
    });
    return [ ...differentStatusItems, ...updatedItems ];
}

const isUpdated = (obj1, obj2, itemId, itemUpdatedField) => {
    return obj1[itemId] === obj2[itemId] && obj1[itemUpdatedField] !== obj2[itemUpdatedField]
}

const isSameId = (obj1, obj2, itemId) => {
    return obj1[itemId] === obj2[itemId]
}

export const getLabelObject = (value, courses) => {
    // get object out of list based on ID as value prop
    for(let course of courses) {
        if(course.value === value){
            return course;
        }
    }
    return null;
}

export const convertObjectPropOfArrayToNumber = (array, prop) => {
    const clonedArray = _.cloneDeep(array);

    for(let obj of clonedArray) {
        obj[prop] = Number(obj[prop]);
    }

    return clonedArray.filter((item) => item.value);
}

export const isSameProps = (obj1, obj2) => {
    return _.difference(_.keys(obj1), _.keys(obj2)).length <= 0;
}