// @ts-nocheck
import { connect } from 'react-redux';
import React, { Component } from 'react'

import { createVisitAction } from 'actions/pagesActions/EducationalSupervisor/visitAction';
import { Form } from 'components/Form/Form';
import FormHeader from 'components/Form/FormHeader';
import InputField from 'components/Form/InputField';
import TextArea from 'components/Form/TextArea';
import Alert from 'components/Utils/Alert';
import ContentDivider from 'components/Utils/ContentDivider';
import ContentPadding from 'components/Utils/ContentPadding';
import { isNumberGreatThanZero } from 'helpers/validate';
import NotAllowed from 'components/Utils/NotAllowed';

class VisitCreate extends Component {

    constructor(props) {
        super(props);

        this.state = {
            schoolId: '',
            note: '',
            errorMessages: []
        }
        this.baseState = { ...this.state };
    }

    validateVisit = () => {
        return [
            isNumberGreatThanZero(this.state.schoolId, 
                '     ', 
                '       ',
                '        '
            )
        ].filter(message => message) 
    }

    createVisit = async () => {
        if(!this.state.errorMessages.length) {
            this.props.createVisitAction({
                schoolId: Number(this.state.schoolId),
                note: this.state.note
            });
        }
    }

    onFieldChange = (value, fieldName) => {
        this.setState({ [fieldName]: value });
    }

    onFormSubmit = (e) => {
        e.preventDefault();

        this.setState({ 
            errorMessages: this.validateVisit()
        }, () => {
            this.createVisit()
        })
    }

    onResetHandler = () => {
        this.setState({ ...this.baseState });
    }

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.alertCode !== 401}
                referer="/visit/current"
                message="         "
                btnLabel="  "
            >
                <Form onSubmit={this.onFormSubmit}>
                    <FormHeader label="  " />
                    <ContentPadding>
                        <Alert messages={this.state.errorMessages} type="error" />
                        <div className="form-group">
                            <InputField
                                id="id"
                                name="id"
                                type="text"
                                onChangeHandler={(e) => this.onFieldChange(e.target.value, 'schoolId')}
                                initial={true}
                                initialValue={this.state.schoolId}
                                label=" "
                                placeholder=": 10510"
                                icon="fas fa-user"
                                colSize="1"
                            />
                        </div>
                        <div className="form-group">
                            <TextArea
                                id="id"
                                name="id"
                                onChangeHandler={(e) => this.onFieldChange(e.target.value, 'note')}
                                initial={true}
                                initialValue={this.state.note}
                                label=" ()"
                                placeholder=":      "
                                icon="fas fa-user"
                                colSize="1"
                            />
                        </div>
                        <ContentDivider />
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value=""/>
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                        </div>
                    </ContentPadding>
                </Form>
            </NotAllowed>
        )
    }
}

const mapStateToProps = state => {
    return {
        alertCode: state.alert.code
    }
}

export default connect(mapStateToProps, {
    createVisitAction
})(VisitCreate);