import React, { Fragment } from 'react'
import { Route } from 'react-router'

import Tab, { TabElement } from 'components/Utils/Tab'
import CourseCreate from './CourseCreate'
import CourseList from './CourseList'

const CourseTab = ({ match }) => {
    return (
        <Fragment>
            <Tab>
                <TabElement 
                    to="/course/create"
                    match={match}
                    title=" "
                />
                <TabElement 
                    to="/course"
                    match={match}
                    title=" "
                />
            </Tab>
            <Route 
                path="/course/create"
                exact
                component={CourseCreate}
            />
            <Route 
                path="/course"
                exact
                component={CourseList}
            />
        </Fragment>
    )
}

export default CourseTab
