import React, { useState } from 'react'

const DropMenu = ({ colSize, id, name, className, title, onChange, locked, children }) => {

    const [ openCloseClass, setOpenCloseClass ] = useState('close');

    return (
        <div className={`form-group__${colSize}-col dropmenu-container`} >
            <div className="form-control" onClick={() => setOpenCloseClass(openCloseClass === 'close' ? 'open' : 'close')}>
                <div className={`form__input form__dropmenu ${className}`}>
                    {title}
                </div>
                <i className={`form__input--icon fas fa-chevron-${openCloseClass === 'open' ? 'up' : 'down'}`}></i>
            </div>
            <div className={`dropmenu__list-container ${openCloseClass} `}>
                <div className="margin-bottom-small">
                    {children}
                </div>
            </div>
        </div>
    )
}

export default DropMenu
