import alertActions from '../../utilsActions/alertActions';
import loadingActions from '../../utilsActions/loadingActions';
import mylabs from '../../../apis/mylabs';
import catchHandler from '../../../helpers/catchHandler';
import history from '../../../history';

export const createCityAction = (city) => {
    return async (dispatch) => {

        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make POST request to the API server
            const response = await mylabs.post('/city', city);

            // removing loading spinner after success
            dispatch(loadingActions.remove());

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true,
                path: `/city/${response.data.id}`
            }));

            
        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }

    }
}

export const fetchCityAction = (cityId) => {
    return async (dispatch) => {
        try {

            // show loading spinner
            dispatch(loadingActions.show());
    
            // make GET request to the API server
            const response = await mylabs.get(`/city/${cityId}`);
    
            // send the response to the reducers
            dispatch({ type: 'FETCH_CITY', payload: response.data });
    
            // removing loading spinner after success
            dispatch(loadingActions.remove());
            
        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}

export const fetchCitiesAction = (filterParams) => {
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());
    
            // make POST request to the API server
            const response = await mylabs.get('/city', {
                params: filterParams
            });
    
            // send the response to the reducers
            dispatch({ type: 'FETCH_CITIES', payload: response.data });
    
            // removing loading spinner after success
            dispatch(loadingActions.remove());
            
        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    
    }
}

export const updateCityAction = (city) => {
    // PATCH city
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make PATCH request to the API server
            const response = await mylabs.patch(`/city`, { ...city });

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true
            }));

            dispatch({ type: 'FETCH_CITY', payload: response.data });

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}

export const deleteCityAction = (cityId) => {
    // DELETE city
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make PATCH request to the API server
            await mylabs.delete(`/city/${cityId}`);

            // after deleting the course, navigate the user to corses list
            history.push('/city');

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "   ",
                code: 200,
                success: true
            }));

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}