import { alertConstants } from '../types';

const success = (response) => {
    return {
        type: alertConstants.SUCCESS,
        payload: response
    }
}

const error = (response) => {
    return {
        type: alertConstants.ERROR,
        payload: response
    }
}

const clear = () => {
    return {
        type: alertConstants.CLEAR
    }
}


const alertActions = {
    success: success,
    error: error,
    clear: clear
}



export default alertActions;