// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import CustomDropdown from '../../../components/Form/CustomDropdown'
import Form from '../../../components/Form/Form'
import FormHeader from '../../../components/Form/FormHeader'
import InputField from '../../../components/Form/InputField'
import Alert from '../../../components/Utils/Alert'
import NotFoundItem from '../../../components/Utils/NotFoundItem';
import ContentPadding from '../../../components/Utils/ContentPadding'
import { getItemUnit, unitsList } from '../../../helpers/fixedLists'
import { validateEqualPropsObjects } from '../../../helpers/validate'
import { fetchItemAction, updateItemAction, deleteItemAction } from '../../../actions/pagesActions/SystemAdministrator/itemAction';
import alertActions from '../../../actions/utilsActions/alertActions';
import ContentDivider from '../../../components/Utils/ContentDivider';
import CheckBox from 'components/Form/CheckBox';
import { validateItem } from './validateItem';
import _ from 'lodash';


class ItemView extends Component {

    async componentDidMount () {
        // connecting to the dropdown menu to reach clearSelection functionality
        this.dropdownRef = React.createRef();
        // API action call, First check if the tableList contain the item, or bring it from server
        await this.props.fetchItemAction(this.props.match.params.id);
        this.settingState();
    }

    state = {
        id: '',
        name: '',
        unit: '',
        safety: false,
        errorMessages: []
    }

    settingState = () => {
        const { item } = this.props.item;
        if(!validateEqualPropsObjects(item, {})) {
            this.setState({ 
                ...this.props.item,
                errorMessages: []
            }, () => {
                const item = getItemUnit(this.props.item.unit);
                this.dropdownRef.current.selectSingleItem(item);
            });
        }
    }

    updateItem = async () => {
        const originalItem = this.props.item;
        const updatedItem = _.omit(this.state, 'errorMessages');

        if(this.state.errorMessages.length === 0 && 
            !validateEqualPropsObjects(originalItem, updatedItem)) {
            // update action
            await this.props.updateItemAction(updatedItem)
            // set the submittedItem equal to the user last update after success submit
            this.settingState();
        }
    }

    onFieldChange = (value, fieldName) => {
        this.setState({ [fieldName]: value });
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        // performing validation and setting messages
        this.setState({
            errorMessages: validateItem(this.state)
        }, () => {
            this.updateItem();
        })
    }

    onResetHandler = (e) => {
        // remove alerts
        this.props.clearAlert();   
        e.preventDefault();
        // reset form values
        this.settingState();
    }

    onDeleteHandler = () => {
        this.props.deleteItemAction(this.props.match.params.id);
    }

    render() {
        return (
            <NotFoundItem 
                referer="/item"
            >
                <Form onSubmit={this.onFormSubmit}>
                    <FormHeader 
                        label=" " 
                        icon="fas fa-chevron-left"
                        refererLabel=" "
                        referer="/item"
                    />
                    <ContentPadding>
                        <Alert messages={this.state.errorMessages} type="error" />
                        <div className="form-group">
                            <InputField
                                id="id"
                                name="id"
                                type="text"
                                initial={true}
                                initialValue={this.state.id}
                                label=" "
                                locked={true}
                                icon="fas fa-lock"
                                colSize="1"
                            />
                        </div>
                        <div className="form-group">
                            <InputField
                                id="name"
                                name="name"
                                type="text"
                                onChangeHandler={(e) => this.onFieldChange(e.target.value, 'name')}
                                initial={true}
                                initialValue={this.state.name}
                                label=" "
                                placeholder=":   "
                                icon="fas fa-signature"
                                colSize="2"
                            />
                            <CustomDropdown 
                                colSize="2"
                                onChange={(unitId) => this.onFieldChange(unitId, 'unit')}
                                list={unitsList}
                                title='  '
                                label=" "
                                setRef={(ref) => this.dropdownRef = ref}
                            />
                        </div>
                        <div className="form-group">
                            <CheckBox 
                                id="safety"
                                name="safety"
                                label=" "
                                initial={true}
                                initialValue={this.state.safety}
                                onChange={(checked) => this.onFieldChange(checked, 'safety')}
                            />
                        </div>

                    </ContentPadding>
                    <ContentDivider />
                    <ContentPadding>
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value=""/>
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                            <input className="btn delete-btn" type="reset" value="" onClick={this.onDeleteHandler}/>
                        </div>
                    </ContentPadding>
                </Form>
            </NotFoundItem>

        )
    }

}

const mapStateToProps = state => {
    return {
        item: state.itemView
    }
}

export default connect(mapStateToProps, { 
    fetchItemAction, 
    updateItemAction, 
    deleteItemAction, 
    clearAlert: alertActions.clear 
})(ItemView);
