export const cancelConfirmedPracticalRequestAction = (reqId) => {
    
    return async (dispatch) => {

        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make POST request to the API server
            const response = await mylabs.delete(`/practical-request/${reqId}`);

            // send new created item to lab manager reducer
            dispatch({ type: 'FETCH_UP_TO_DATE_EXPERIMENT', payload: response.data.request });

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true
            }));

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }
    }
}

export const confirmPracticalExecutionAction = (reqId, executed) => {
    return async (dispatch) => {

        try{
            // show loading spinner
            dispatch(loadingActions.show());

            // make POST request to the API server
            const response = await mylabs.post(`/practical-request/${reqId}/executed`, { executed });

            // send new created item to lab manager reducer
            dispatch({ type: 'FETCH_UP_TO_DATE_EXPERIMENT', payload: response.data.request });

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true
            }));

            // removing loading spinner after success
            dispatch(loadingActions.remove());
        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }

    }
}