// @ts-nocheck
import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

import { Form } from 'components/Form/Form';
import FormHeader from 'components/Form/FormHeader';
import InputField from 'components/Form/InputField';
import ContentDivider from 'components/Utils/ContentDivider';
import ContentPadding from 'components/Utils/ContentPadding';
import NotFoundItem from 'components/Utils/NotFoundItem';
import { fetchCurrentVisitLaboratoryManagerAction } from 'actions/pagesActions/EducationalSupervisor/visitAction';

const LaboratoryManagerView = ({ labManager, fetchCurrentVisitLaboratoryManagerAction, match }) => {

    if(!labManager.id) {
        fetchCurrentVisitLaboratoryManagerAction((match.params.id));
    }

    const renderLabs = () => {
        // display the labManager laboratories of the sepecific lab
        if(labManager) {
            return labManager.laboratories.map( lab => {
                return (
                    <div className="form-group" key={lab.value}>
                        <InputField 
                            id={`lab${lab.value}`}
                            name={`lab${lab.value}`}
                            type="text"
                            locked={true}
                            initial={true}
                            initialValue={lab.label + " : "}
                            icon="fas fa-lock"
                            colSize="1"
                        />
                    </div>
                );
            })
        }
    }

    return (
        <NotFoundItem
            referer="/visit/current"
        >
            <Form>
                <FormHeader 
                    label="  " 
                    refererLabel=" "
                    icon="fas fa-chevron-left"
                    referer="/visit/current"
                />
                <ContentPadding>
                    <div className="form-group">
                        <InputField
                            id="id"
                            name="id"
                            type="text"
                            initial={true}
                            initialValue={labManager.id}
                            label=" "
                            icon="fas fa-lock"
                            locked={true}
                            colSize="2"
                        />
                        <InputField
                            id="name"
                            name="name"
                            type="text"
                            initial={true}
                            initialValue={labManager.name}
                            label=" "
                            icon="fas fa-lock"
                            locked={true}
                            colSize="2"
                        />
                    </div>
                    <div className="form-group">
                        <InputField
                            id="rate"
                            name="rate"
                            type="text"
                            initial={true}
                            initialValue={labManager.rate}
                            label=""
                            icon="fas fa-lock"
                            locked={true}
                            colSize="2"
                        />
                    </div>
                </ContentPadding>
                <FormHeader 
                    label=""
                />
                <ContentPadding>
                    { renderLabs() }
                </ContentPadding>
                <ContentPadding>
                    <ContentDivider />
                    <div className="form-group confirm-group margin-top-big">
                        <Link 
                            to={`/visit/current/laboratory-manager/${match.params.id}/evaluate`}
                            className="btn submit-btn link-btn" 
                        >
                             
                        </Link>
                        <Link 
                            to={`/visit/current/laboratory-manager/${match.params.id}/dashboard`}
                            className="btn cancel-btn link-btn" 
                        >
                             
                        </Link>
                    </div>
                </ContentPadding>
            </Form>
        </NotFoundItem>
    );
}

const mapStateToProps = (state, ownProps) => {
    return {
        labManager: state.laboratoryManagers[ownProps.match.params.id]
    }
}

LaboratoryManagerView.defaultProps = {
    labManager: {
        id: '',
        name: '',
        rate: '',
        laboratories: []
    }
};

export default  connect(mapStateToProps, {
    fetchCurrentVisitLaboratoryManagerAction
})(LaboratoryManagerView);