import React from 'react';
import { connect } from 'react-redux';
import { Route } from 'react-router-dom';

import NotAllowed from 'components/Utils/NotAllowed';
import Tab, { TabElement } from 'components/Utils/Tab';
import PracticalExperimentCreate from './PracticalExperimentCreate';
import PracticalExperimentList from './PracticalExperimentList';

const PracticalExperimentTab = ({ match, hasSchool }) => {

    return (
        <NotAllowed
            hasSchool={hasSchool}
            referer="/invites"
            statusCode="403"
            message="       "
            btnLabel="   "
        >
            <Tab>
                <TabElement 
                    title="  "
                    match={match}
                    to="/practical-experiment/create"
                />
                <TabElement 
                    title=" "
                    match={match}
                    to="/practical-experiment"
                />
            </Tab>
            <Route path="/practical-experiment/create" exact component={PracticalExperimentCreate} />
            <Route path="/practical-experiment" exact component={PracticalExperimentList} />
        </NotAllowed>
    )
}

const mapStateToProps = state => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
    }
}

export default connect(mapStateToProps, {})(PracticalExperimentTab);