import React from 'react'
import { Link } from 'react-router-dom';

const FormHeader = ({ label, referer, icon, refererLabel }) => {
    return (
        <div className="form-divider">
            {label}
            { referer ? 
                <Link className="form-divider__referer" to={referer}>
                    { icon ? <i className={icon}/> : null }
                    { refererLabel ? <p>{refererLabel}</p> : null }
                </Link> : null }
        </div>
    )
}

export default FormHeader
