// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import { Form } from '../../../components/Form/Form';
import FormHeader from '../../../components/Form/FormHeader';
import InputField from '../../../components/Form/InputField';
import Alert from '../../../components/Utils/Alert';
import ContentPadding from '../../../components/Utils/ContentPadding';
import { validateEqualPropsObjects } from '../../../helpers/validate';
import alertActions from '../../../actions/utilsActions/alertActions';
import NotFoundItem from '../../../components/Utils/NotFoundItem';
import { 
    deleteExperimentAction, 
    fetchExperimentAction, 
    updateExperimentAction 
} from '../../../actions/pagesActions/SystemAdministrator/experimentAction';
import CustomDropdown from '../../../components/Form/CustomDropdown';
import ContentDivider from '../../../components/Utils/ContentDivider';
import { 
    convertObjectPropOfArrayToNumber, 
    getItemsDifferenceStatusWithUpdate, 
    getLabelObject 
} from '../../../helpers/objectsHelpers';
import _ from 'lodash';
import { validateExperiment } from './validateExperiment';

class ExperimentViewTest extends Component {

    constructor(props) {
        super(props);
        this.state = {
            id: '',
            title: '',
            items: [],
            courseId: '',
            dynamicRefs: [],
            errorMessages: []
        }
    }

    async componentDidMount () {
        this.courseDropdownRef = React.createRef();
        // API action call to get the item
        await this.props.fetchExperimentAction(this.props.match.params.id);
        this.settingState();

    }

    settingState = () => {
        const { experiment } = this.props;
        if(experiment && !validateEqualPropsObjects(experiment, {})) {
            this.setState({ 
                ...experiment, 
                courseId: experiment.course.value,
            }, () => {
                this.courseDropdownRef.current.selectItem(getLabelObject(this.state.courseId, this.props.courses));
                for(let index=0; index < this.state.dynamicRefs.length && index < experiment.items.length; index++) {
                    this.state.dynamicRefs[index].current.selectItem(this.state.items[index])
                }
            })
        }
    }

    updateExperiment = async () => {
        // REFACTOR HERE
        const updatedExperiment = {
            id: this.state.id,
            title: this.state.title,
            items: this.state.items,
            course: getLabelObject(this.state.courseId, this.props.courses),
        }
        const originalExperiment = this.props.experiment;

        if(this.state.errorMessages.length === 0 && 
            !validateEqualPropsObjects(originalExperiment, updatedExperiment)) {
            // algorithm to check same, add, delete or updated items
            const updatedItems = getItemsDifferenceStatusWithUpdate(
                originalExperiment.items, 
                updatedExperiment.items, 
                'value', 'quantity'
            );
            const requestUpdatedItems = convertObjectPropOfArrayToNumber(updatedItems, 'quantity');
            // PATCH action call
            await this.props.updateExperimentAction({
                ...updatedExperiment,
                courseId: this.state.courseId,
                items: requestUpdatedItems
            })
            this.settingState();
        }
    }

    setDynamicItemsRef = (ref, index) => {
        // setting the dynamic refs for base
        const refsArray = this.state.dynamicRefs;
        refsArray[index] = ref;
    }
    
    onDynamicItemSelect = (value, index) => {
        const tempArray = _.cloneDeep(this.state.items);
        const quantity = tempArray[index].quantity;
        tempArray[index] = { 
            ...getLabelObject(value, this.props.items), 
            quantity: quantity ? quantity : '' 
        };
        this.setState({ items: [...tempArray] })
    }

    onFieldChange = (value, fieldName) => {
        this.setState({ [fieldName]: value });
    }

    onAddFieldClick = (e) => {
        e.preventDefault();
        // append new item field
        this.setState({ 
            items:[...this.state.items, {value: '', quantity: ''} ] 
        })
    }

    onDynamicQuantityChange = (e, index) => {
        const tempArray = _.cloneDeep(this.state.items);
        tempArray[index].quantity = e.target.value;
        this.setState({ items: [...tempArray] })
    }

    onRemoveFieldClick = (index) => {
        const list = _.cloneDeep(this.state.items);
        // remove the item and it's corresponding ref 
        list.splice(index, 1);
        this.state.dynamicRefs.splice(index, 1);
        // setting the new items without the removed one
        this.setState({ items: [...list] }, () => {
            // after removing item, we need to reset the dropdown menu's correctly
            for(let index in this.state.items) {
                const dropdown = this.state.dynamicRefs[index].current;
                const item = this.state.items[index]
                if(dropdown && item.label) {
                    dropdown.selectItem(item);
                } else {
                    dropdown.clearSelection();
                }
            }
        });
    }

    onDynamicRender = () => {
        const fieldArray = [];
        for(let fieldIndex in this.state.items) {
            fieldArray.push(
                <div className="form-group" key={fieldIndex}>
                    <CustomDropdown
                        name={fieldIndex}
                        colSize="2"
                        label={`    ${Number(fieldIndex)+1}`}
                        onChange={(value) => this.onDynamicItemSelect(value, fieldIndex)}
                        setRef={(ref) => this.setDynamicItemsRef(ref, fieldIndex)}
                        list={this.props.items}
                        title='  '
                    />
                    <div className="form-group-btn">
                        <InputField 
                            name={fieldIndex}
                            colSize="1"
                            locked={!this.state.items[fieldIndex].value}
                            icon="fas fa-sort-numeric-up-alt"
                            initial={true}
                            initialValue={this.state.items[fieldIndex].quantity}
                            onChangeHandler={(e) => this.onDynamicQuantityChange(e, fieldIndex)}
                            label={`   ${Number(fieldIndex)+1}  `}
                        />
                        <button className="delete-field-btn" type="reset" onClick={() => this.onRemoveFieldClick(fieldIndex)}> 
                            <i className="fas fa-times"/>
                        </button>
                    </div>
                </div>
            );
        }
        return fieldArray;
    }

    onFormSubmit = (e) => {
        e.preventDefault();

        // clear server alert at submit
        this.props.clearAlert();
        
        this.setState({ 
            errorMessages: validateExperiment(this.state, 'items')
         }, () => {
            this.updateExperiment();
         });
    }

    onResetHandler = () => {
        // remove alerts
        this.props.clearAlert();
        // reset values
        this.settingState();
    }

    onDeleteHandler = () => {
        this.props.deleteExperimentAction(this.props.match.params.id);
    }

    render() {
        return (
            <NotFoundItem
                referer="/experiment"
            >
                <Form onSubmit={this.onFormSubmit}>
                    <FormHeader 
                        label="  " 
                        icon="fas fa-chevron-left"
                        refererLabel="  "
                        referer="/experiment"
                    />
                    <ContentPadding>
                        <Alert messages={this.state.errorMessages} type="error" />
                        <div className="form-group">
                            <InputField
                                id="id"
                                name="id"
                                type="text"
                                initial={true}
                                initialValue={this.state.id}
                                locked={true}
                                label="  "
                                icon="fas fa-lock"
                                colSize="2"
                            />
                            <InputField
                                id="name"
                                name="name"
                                type="text"
                                onChangeHandler={(e) => this.onFieldChange(e.target.value, 'title')}
                                initial={true}
                                initialValue={this.state.title}
                                label=" "
                                placeholder=":   "
                                icon="fas fa-signature"
                                colSize="2"
                            />
                        </div>
                        <div className="form-group">
                            <CustomDropdown 
                                colSize="2"
                                label=" "
                                setRef={(ref) => this.courseDropdownRef = ref}
                                onChange={(selectedCourse) => this.onFieldChange(selectedCourse, 'courseId')}
                                list={this.props.courses}
                                title='  '
                            />

                        </div>
                        {this.onDynamicRender()}
                        <div className="form-group confirm-group margin-top-big">
                            <button className="btn submit-btn" onClick={this.onAddFieldClick}>    <i className=" btn-icon fas fa-plus"></i></button>
                        </div>
                        <ContentDivider />
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value="" />
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                            <input className="btn delete-btn" type="reset" value="" onClick={this.onDeleteHandler}/>
                        </div>
                    </ContentPadding>
                </Form>
            </NotFoundItem>
        )
    }
}

const mapStateToProps = state => {
    return {
        experiment: state.itemView,
        courses: Object.values(state.courses),
        items: Object.values(state.items),
        alertCode: state.alert.code
    }
}

export default connect(mapStateToProps, { 
    fetchExperimentAction, 
    clearAlert: alertActions.clear,
    updateExperimentAction,
    deleteExperimentAction
})(ExperimentViewTest);
