import _ from 'lodash';
import { userConstants } from '../../actions/types';

const supervisorAccountsReducer = (state={}, action) => {
    switch (action.type) {
        case 'FETCH_SUPERVISORS_ACCOUNTS':
            return { ...state, ..._.mapKeys(action.payload, 'id') }
        case 'CREATE_SUPERVISOR_ACCOUNT':
            return { ...state, [action.payload.id]: action.payload }
        case 'FETCH_SUPERVISOR_ACCOUNT':
            return { ...state, [action.payload.id]: action.payload }
        case 'PATCH_SUPERVISOR_ACCOUNT':
            return { ...state, [action.payload.id]: action.payload }
        case userConstants.LOGOUT:
            return {};
        default:
            return state;
    }
}

export default supervisorAccountsReducer;