// @ts-nocheck
import React, { Component } from 'react'

import CustomDropdown from 'components/Form/CustomDropdown';
import { Form } from 'components/Form/Form';
import FormHeader from 'components/Form/FormHeader';
import InputField from 'components/Form/InputField';
import Alert from 'components/Utils/Alert';
import ContentDivider from 'components/Utils/ContentDivider';
import ContentPadding from 'components/Utils/ContentPadding';
import { evaluateTypes } from 'helpers/fixedLists';
import { addQuestionTOEvaluateTypeAction } from 'actions/pagesActions/SystemAdministrator/questionAction';
import { connect } from 'react-redux';
import { validateQuestion } from './validateQuestion';

class QuestionAdd extends Component {

    constructor(props) {
        super(props);
        this.evaluateDropdownRef = React.createRef();

        this.state = {
            text: '',
            evaluateType: 0,
            errorMessages: []
        }
        this.baseState = { ...this.state };
    }

    addQuestion = async () => {
        if(!this.state.errorMessages.length) {
            // API call here
            await this.props.addQuestionTOEvaluateTypeAction(this.state)
            if(this.props.status) {
                this.onResetHandler();
            }
        }
    }

    onFieldChange = (value, fieldName) => {
        this.setState({ [fieldName]: value })
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        this.setState({ errorMessages: validateQuestion(this.state)
        }, () => {
            this.addQuestion();
        })
    }

    onResetHandler = () => {
        this.setState({ ...this.baseState }, () => {
            this.evaluateDropdownRef.current.clearSelection();
        });
    }

    render() {
        return (
            <Form onSubmit={this.onFormSubmit}>
                <FormHeader 
                    label="  " 
                    icon="fas fa-chevron-left"
                    referer="/question"
                    refererLabel=" "
                />
                <ContentPadding>
                    <Alert messages={this.state.errorMessages} type="error" />
                    <div className="form-group">
                        <InputField
                            id="text"
                            name="text"
                            type="text"
                            onChangeHandler={(e) => this.onFieldChange(e.target.value, 'text')}
                            initial={true}
                            initialValue={this.state.text}
                            label=" "
                            placeholder=":        "
                            icon="fas fa-signature"
                            colSize="2"
                        />
                        <CustomDropdown 
                            colSize="2"
                            onChange={(selectedEvaluate) => this.onFieldChange(selectedEvaluate, 'evaluateType')}
                            list={evaluateTypes}
                            title='  '
                            label=" "
                            setRef={(ref) => this.evaluateDropdownRef = ref}
                        />
                    </div>
                    <ContentDivider />
                    <div className="form-group confirm-group margin-top-big">
                        <input className="btn submit-btn" type="submit" value=""/>
                        <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                    </div>
                </ContentPadding>
            </Form>
        )
    }
}

const mapStateToProps = state => {
    return {
        status: state.alert.success
    }
}

export default connect(mapStateToProps, {
    addQuestionTOEvaluateTypeAction
})(QuestionAdd);