import { userConstants } from '../../actions/types';


const schoolReducer = (state={}, action) => {
    switch (action.type) {
        case 'CREATE_SCHOOL':
            return { ...action.payload };
        case 'FETCH_SCHOOL':
            return { ...action.payload };
        case 'FETCH_UP_TO_DATE_SCHOOL':
            return { ...action.payload };
        case 'PATCH_SCHOOL':
            return { ...action.payload };
        case userConstants.LOGOUT:
            return {};
        default:
            return state;
    }
}

export default schoolReducer;