import React from 'react';

const Alert = ({ messages, type }) => {

    // DO some login to change between error and success alert as classes
    if (messages.length === 0) {
        return null;
    }
    
    return (
        <div className={`alert alert__${type} alert`}>
            {
                messages.filter(message => message).map((message, index) => {
                    return (
                        <p className={`alert__${type}-message`} key={index}> {message} </p>
                    )
                })
            }
        </div>
    )
}

export default Alert;
