import React, { useRef } from 'react'
// @ts-ignore
import { DropdownMultiple } from 'reactjs-dropdown-component';
import { validateEqualPropsObjects } from '../../helpers/validate';

const CustomMultipleDropdown = ({ 
    list, 
    title, 
    onChange, 
    colSize,
    label,
    setRef,
    name,
    dynamic,
    selectedTitle
}) => {

    const dropdownRef = useRef(null);

    const renderLabel = () => {
        return (
            label ? <label htmlFor={label} className="form__label"> {label} </label> : null 
        );
    }

    setRef && setRef(dropdownRef)

    return (
        <div className={`form-group__${colSize}-col`}>
            {renderLabel()}
            <DropdownMultiple
                onChange={(items) => {

                    if(items.length === 1 && validateEqualPropsObjects(items[0], {label: '', value: ''})) {
                        // in case the list is empty and only contain {label: '', value: ''} object
                        dropdownRef.current.deselectAll();
                    }
                    // adding if condition, becasue in-case of (reseting) and the item will be undefined
                    else if(items && !dynamic) {
                        // this will work for simple dropdown items
                        onChange(items)
                    }
                    else if(items && dynamic) {
                        // this for dynamic rendering, we pass the name, to set it on the state
                        onChange(items, name);
                    }
                }}
                list={list !== undefined && list.length ? list : [{label: '', value: ''}]}
                title={title}
                titleSingular={selectedTitle}
                ref={dropdownRef}
                arrowUpIcon={<i className="fas fa-caret-up"></i>}
                arrowDownIcon={<i className="fas fa-caret-down"></i>}
                />
        </div>
    )
}

export default CustomMultipleDropdown;
