import React from 'react'
import { connect } from 'react-redux';

import logo from '../images/icon.svg';
import navToggleAction from '../actions/utilsActions/navToggleAction';


const MobileHeader = (props) => {

    const onClickToggle = () => {
        // clicking the burger menu button, open or close the side nav
        props.navToggleAction(props.navToggle);
    }

    return (
        <div className="mobile-header">
            <img className="mobile-header__logo" src={logo} alt="Mokhtabarati-logo"/>
            <i className="mobile-header__nav-toggle fas fa-bars" onClick={onClickToggle} />
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        navToggle: state.navToggle
    };
}

export default connect(mapStateToProps, { navToggleAction } )(MobileHeader)
