import { userConstants } from '../../actions/types';

const profileReducer = (state={}, action) => {
    switch (action.type) {
        case 'FETCH_PROFILE':
            return { ...action.payload };
        case 'UPDATE_PROFILE':
            return { ...action.payload };
        case userConstants.LOGOUT:
            return {};
        default:
            return state;
    }
}

export default profileReducer;