import React from 'react'

const OverviewMetric = ({ title, metricsData=[] }) => {

    const renderMetrics = () => {
        return metricsData.map((metric, index) => {
            return (
                <div key={index} className="dashboard-metrics__metric">
                    <p className={`dashboard-metrics__metric--value ${metric.color}`}>
                        {metric.value}
                    </p>
                    <div className="dashboard-metrics__metric--label">
                        {metric.label}
                    </div>
                </div>
            );
        })
    } 

    return (
        <div className="metric-container">
            <div className="dashboard-overview-title"> {title} </div>
            <div className="dashboard-metrics">
                { renderMetrics() }
            </div>
        </div>
    )
}

export default OverviewMetric
