import React from 'react';
import { connect } from 'react-redux';

import getNavList from 'helpers/navList';
import { validateEqualPropsObjects } from 'helpers/validate';
import { Link } from 'react-router-dom';

const BreadCrumbs = ({ role, hasSchool, match }) => {

    const getMainHeader = (navList) => {
        for(const categoryIndex in navList) {
            let routes = navList[categoryIndex].children;
            for(const routeIndex in routes) {
                if(match.path.includes(routes[routeIndex].path) && routes[routeIndex].path !== '/') {
                    return {
                        title: routes[routeIndex].label,
                        categoryIndex,
                        routeIndex,
                        path: routes[routeIndex].path,
                        headerTitle: routes[routeIndex].headerTitle ? routes[routeIndex].headerTitle : routes[routeIndex].label
                    };
                } else if(routes[routeIndex].path === match.path) {
                    return {
                        title: routes[routeIndex].label,
                        categoryIndex,
                        routeIndex,
                        path: match.path,
                        headerTitle: routes[routeIndex].headerTitle ? routes[routeIndex].headerTitle : routes[routeIndex].label
                    };
                }
            }
        }
        return {};
    }

    const navList = getNavList(role, hasSchool);    
    let mainHeader = getMainHeader(navList);

    const buildingBreadcrumbsItems = (subRoutes) => {
        if(!subRoutes) {
            return '';
        } else {
            for (let index = 0; index < subRoutes.length; index++) {
                if(subRoutes[index].path === match.path) {
                    let path = getCorrectPath(match, subRoutes[index].path);
                    
                    return [
                        <Link to={match.url} key={subRoutes[index].label} className="left-side__breadcrumbs-items--item">{subRoutes[index].label + ` ${path[1] ? path[1] : ''}`}</Link>,
                        <span className="separator" key={`separator-${subRoutes[index].label}`}>/</span>,
                        buildingBreadcrumbsItems(subRoutes[index].subRoutes),

                    ]
                } else if(match.path.includes(subRoutes[index].path)) {
                    let path = getCorrectPath(match, subRoutes[index].path);
                    return [
                        <Link to={path[0]} key={subRoutes[index].label} className="left-side__breadcrumbs-items--item">{subRoutes[index].label + ` ${path[1]}`}</Link>,
                        <span key={`separator-${subRoutes[index].label}`} className="separator">/</span>,
                        ...buildingBreadcrumbsItems(subRoutes[index].subRoutes),
                    ]
                }
            }
            return null;
        }
    }

    const getCorrectPath = (match, previousPath) => {
        let previousArray = previousPath.split(':');
        return [previousArray[0]+match.params[previousArray[1]], match.params[previousArray[1]]];
    }

    const renderMainHeader = (mainHeader) => {
        if(mainHeader.path && mainHeader.path !== '/' ) {
            return (
                <Link to={mainHeader.path} key={mainHeader.path} className="left-side__breadcrumbs-items--item">{mainHeader.title}</Link>
            );
        }
    }

    if(validateEqualPropsObjects({}, mainHeader)) {
        mainHeader.headerTitle='   ';
    }

    return (            
        <div className="left-side__breadcrumbs">
            <h2 className="secondary-header">{mainHeader.headerTitle}</h2>
            <p className="left-side__breadcrumbs-items">
                <Link to="/" className="left-side__breadcrumbs-items--item color-primary" >  <i className="fas fa-home" /></Link>    
                <span className="separator">/</span>
                { renderMainHeader(mainHeader) }
                <span className="separator">/</span>
                { navList[mainHeader.categoryIndex] ? buildingBreadcrumbsItems(navList[mainHeader.categoryIndex].children[mainHeader.routeIndex].subRoutes) : null }
            </p>
        </div>
    );
}

const mapStateToProps = (state) => {
    return {
        role: state.auth.user.permissions,
        hasSchool: state.auth.user.data.hasSchool
    }
}

export default connect(mapStateToProps)(BreadCrumbs);