import React from 'react';
import { connect } from 'react-redux';

import filterToggleAction from '../../actions/utilsActions/filterToggleAction';


const ListLayout = (props) => {

    const onFilterToggleClick = (e) => {
        props.filterToggleAction(props.filterToggleValue);
    }

    return (
        <div className="list-container">
            <div className="filter-responsive-btn" onClick={onFilterToggleClick}>
                <i className="fas fa-filter" />
            </div>
            {props.children}
        </div>
    )
}

const mapStateToProps = state => {
    return { 
        filterToggleValue: state.filterToggle
    }
}

export default connect(mapStateToProps, { filterToggleAction })(ListLayout);
