// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import CustomDatePicker from '../../../components/Form/CustomDatePicker';
import { Form } from '../../../components/Form/Form'
import InputField from '../../../components/Form/InputField';
import Alert from '../../../components/Utils/Alert';
import { createSemesterAction } from '../../../actions/pagesActions/SystemAdministrator/semesterActions';
import alertActions from '../../../actions/utilsActions/alertActions';
import ContentDivider from '../../../components/Utils/ContentDivider';
import { validateSemester } from './validateSemester';

class SemesterCreate extends Component {

    constructor(props) {
        super(props);
        this.baseState = { ...this.state };
    }

    state = {
        title: '',
        startDate: null,
        endDate: null,
        errorMessages: []
    }

    createSemester = async () => {
        if (this.state.errorMessages.length === 0) {
            // Call API to create SEMESTER
            await this.props.createSemesterAction(this.state)

            if(this.props.status) {
                // If the post semester call success, reset the fields.
                this.onResetHandler();
            }
        }
    }

    onFieldChange = (value, fieldName) => {
        switch (fieldName) {
            case 'startDate':
                if(this.state.endDate && value.getTime() >= this.state.endDate.getTime()) {
                    // reset the 'end' date becasue it maybe become before the 'start' date
                    this.setState({ [fieldName]: value, endDate: '' });
                    return;
                }
                this.setState({ [fieldName]: value });
                break;
            default:
                // other states that dont need extra operations
                this.setState({ [fieldName]: value })
                break;
        }
    }

    onFormSubmit = (e) => {

        e.preventDefault();
        
        // Clear the Server alerts on form submit
        this.props.clearServerAlert();

        this.setState({
            errorMessages: validateSemester(this.state)
        }, () => {
            this.createSemester();
        })
    }

    onResetHandler = () => {
        this.setState({ ...this.baseState })
    }

    render() {
        return (
            <Form onSubmit={this.onFormSubmit}>
                <div className="form-divider">
                     
                </div>
                <div className="content-padding">
                    <Alert messages={this.state.errorMessages} type="error" />
                    <div className="form-group">
                        <InputField
                            id="title"
                            name="title"
                            onChangeHandler={(e) => this.onFieldChange(e.target.value, 'title')}
                            type="text"
                            initial={true}
                            initialValue={this.state.title}
                            label="  "
                            placeholder=":    2019/2020"
                            icon="fas fa-signature"
                            colSize="1"
                        />
                    </div>
                    <div className="form-group">
                        <CustomDatePicker
                            id="start_date"
                            name="startDate"
                            onChangeHandler={(startDate) => this.onFieldChange(startDate, 'startDate')}
                            initial={true}
                            initialDate={this.state.startDate}
                            label=" "
                            colSize="2"
                            minDate={new Date()}
                        />
                        <CustomDatePicker
                            id="end_date"
                            name="endDate"
                            onChangeHandler={(endDate) => this.onFieldChange(endDate, 'endDate')}
                            initial={true}
                            initialDate={this.state.endDate}
                            label=" "
                            colSize="2"
                            minDate=
                            { 
                                this.state.startDate 
                                ? new Date().setDate(this.state.startDate.getDate() + 1) 
                                : new Date().setDate(new Date().getDate() + 1)
                            }
                        />
                    </div>
                    <ContentDivider />
                    <div className="form-group confirm-group margin-top-big">
                        <input className="btn submit-btn" type="submit" value="  "/>
                        <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                    </div>
                </div>
            </Form>
        )
    }
}

const mapStateToProps = state => {
    return {
        status: state.alert.success,

    }
}

export default connect(mapStateToProps,  
    { 
        createSemesterAction, 
        clearServerAlert: alertActions.clear 
    })(SemesterCreate);