
import alertActions from '../../../utilsActions/alertActions';
import loadingActions from '../../../utilsActions/loadingActions';
import mylabs from '../../../../apis/mylabs';
import catchHandler from '../../../../helpers/catchHandler';
import history from '../../../../history';

export const fetchCreateExperimentRequirementsAction = () => {
    return async (dispatch) => {
        try {

            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());

            // fetch the semesters records from the API server 
            const response = await mylabs.get('/practical-experiment/create-requirements');
            
            // Send to form Reducers
            dispatch( {type: 'FETCH_FORM_COURSES', payload: response.data.courses });

            const labs = response.data.laboraotries.map(lab => { return {...lab, id: lab.value }});

            dispatch( {type: 'FETCH_LABORATORIES', payload: labs });

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch(err) {
            catchHandler(dispatch, err);
        }
    }
}

export const createPracticalExperimentRequestAction = (experimentRequest) => {
    return async (dispatch) => {
        try {

            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());

            // fetch the semesters records from the API server 
            const response = await mylabs.post('/practical-experiment', experimentRequest);
            
            // Send to form Reducers
            dispatch({ type: 'CREATE_EXPERIMENT', payload: response.data.practicalExperiment });

            // message to the user that the school is created
            dispatch(alertActions.success({
                message: response.data.message,
                code: 200,
                success: true,
                path: `/practical-experiment/${response.data.practicalExperiment.id}`
            }));


            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch(err) {
            catchHandler(dispatch, err);
        }
    }
}

export const fetchPracticalExperimentRequestAction = (requestId) => {
    return async (dispatch) => {
        try {

            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());

            // fetch the semesters records from the API server 
            const response = await mylabs.get(`/practical-experiment/${requestId}`);
            
            const labs = response.data.laboratories.map(lab => { return {...lab, id: lab.value }});

            dispatch( {type: 'FETCH_LABORATORIES', payload: labs });

            // Send to form Reducers
            dispatch({ type: 'FETCH_UP_TO_DATE_EXPERIMENT', payload: response.data.practicalExperiment });

            // fetch questions to evaluate
            dispatch({ type: 'FETCH_SCIENCE_TEACHER_EVALUATE_QUESTIONS', payload: response.data.questions })

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch(err) {
            catchHandler(dispatch, err);
        }
    }
} 


export const updatePracticalExperimentRequestAction = (updatedExperiment) => {
    return async (dispatch) => {
        try {

            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());

            // fetch the semesters records from the API server 
            const response = await mylabs.patch('/practical-experiment', updatedExperiment);
            
            // Send to form Reducers
            dispatch({ type: 'PATCH_EXPERIMENT', payload: response.data.practicalExperiment });

            // message to the user that the school is created
            dispatch(alertActions.success({
                message: response.data.message,
                code: 200,
                success: true
            }));

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch(err) {
            catchHandler(dispatch, err);
        }
    }
}


export const cancelPracticalExperimentRequestAction = (experimentReqId) => {
    return async (dispatch) => {

        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make POST request to the API server
            await mylabs.delete(`/practical-experiment/${experimentReqId}`);

            history.push(`/practical-experiment`);

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "   ",
                code: 200,
                success: true
            }));

            // remove deleted request from experiments reducer
            dispatch({ type: 'DELETE_EXPERIMENT', payload: experimentReqId });

            // removing loading spinner after success
            dispatch(loadingActions.remove());
            
        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }

    }
}

export const fetchPracticalExperimentRequestsAction = (filterPramas) => {
    return async (dispatch) => {
        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // fetch the semesters records from the API server 
            const response = await mylabs.get(`/practical-experiment`, { params: filterPramas });
            
            const labs = response.data.laboratories.map(lab => { return {...lab, id: lab.value }});

            dispatch( {type: 'FETCH_LABORATORIES', payload: labs });

            // send new created item to lab manager reducer
            dispatch({ type: 'FETCH_PRACTICAL_REQUESTS_TABLE', payload: response.data });

            // Send to form Reducers
            dispatch({ type: 'FETCH_CORRECT_EXPERIMENTS', payload: response.data.practicalExperiments });

            // fetch questions to evaluate
            dispatch({ type: 'FETCH_SCIENCE_TEACHER_EVALUATE_QUESTIONS', payload: response.data.questions })

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch(err) {
            catchHandler(dispatch, err);
        }
    }
}


export const evaluatePracticalExperimentAction = (reqId, answers) => {
    return async (dispatch) => {
        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // fetch the semesters records from the API server 
            const response = await mylabs.post(`/practical-experiment/${reqId}/evaluate`, answers );
            // Send to form Reducers
            dispatch({ type: 'PATCH_EXPERIMENT', payload: response.data.practicalExperiment });

            history.push(`/practical-experiment/${reqId}`);

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "   ",
                code: 200,
                success: true
            }));

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch(err) {
            catchHandler(dispatch, err);
        }
    }
} 
