import React from 'react'

import logo from '../../images/icon.svg';

const PublicForm = ({ children, onSubmit, type }) => {

    return (
        <div className="public-page">
            <div className="form-container">
                <div className="form-container__logo">
                    <img className="form-container__logo--medium" src={logo} alt="mylabs logo"/>
                </div>
                <form 
                    action="/" 
                    className={`form form--${type}`} 
                    onSubmit={onSubmit}
                >
                    {children}

                </form>
            </div>
        </div>
    )
}

export default PublicForm;
