import _ from 'lodash';
import { userConstants } from '../../actions/types';

const itemsReducer = (state={}, action) => {
    switch (action.type) {

        case 'FETCH_FORM_ITEMS':
            return { ...state, ..._.mapKeys(action.payload, 'value') };
        case 'CREATE_FORM_ITEM':
            return { ...state, [action.payload.value]: action.payload };
        case 'FETCH_UP_TO_DATE_FORM_ITEM':
            return { ...state, [action.payload.value]: action.payload };
        case 'PATCH_FORM_ITEM':
            return { ...state, [action.payload.value]: action.payload };
        case 'DELETE_FORM_ITEM':
            return _.omit(state, action.payload);
        case userConstants.LOGOUT:
            return {};
        default:
            return state;
    }
} 

export default itemsReducer;