const parseDateToString = (date) => {
    if(date) 
        return `${date.getMonth()+1}-${date.getDate()}-${date.getFullYear()}`;
    return ''
}

export const onMinDateChange = (date) => {
    const newMinDate = new Date();
    if(date) {
        newMinDate.setDate(date.getDate() + 1);
        newMinDate.setMonth(date.getMonth());
        newMinDate.setFullYear(date.getFullYear());
        return newMinDate;
    }
    return null;
}

export const parseDateForMoment = (date) => {
    return `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
}

export default parseDateToString;