// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import { Form } from '../../../components/Form/Form';
import FormHeader from '../../../components/Form/FormHeader';
import InputField from '../../../components/Form/InputField';
import Alert from '../../../components/Utils/Alert';
import ContentPadding from '../../../components/Utils/ContentPadding';
import { validateArrayOfEmptyObjects, validateEmpty, validateEqualPropsObjects } from '../../../helpers/validate';

import alertActions from '../../../actions/utilsActions/alertActions';
import ContentDivider from '../../../components/Utils/ContentDivider';
import { createCityAction } from '../../../actions/pagesActions/SystemAdministrator/cityAction';


class CityCreate extends Component {

    constructor(props) {
        super(props);

        this.state = {
            name: '',
            areas: [{ label: '' }],
            errorMessages: []
        }
        this.baseState = { ...this.state };
    }

    validateCityData = () => {
        return [
            validateEmpty(this.state.name, '      '),
            validateArrayOfEmptyObjects(this.state.areas, {label: ''}, '     ')
        ].filter(message => message)
    }

    createCity = async () => {
        if(this.state.errorMessages.length === 0) {
            // createAction call here
            await this.props.createCityAction({
                ...this.state,
                areas: this.state.areas.filter(area => !validateEqualPropsObjects(area, { label: '' }))
            })
            if(this.props.status) {
                // reset after successful POST
                this.onResetHandler();
            }
        }
    }

    onNameChangeHandler = (e) => {
        this.setState({ name: e.target.value });   
    }

    onAddFieldClick = (e) => {
        e.preventDefault();
        this.setState({ areas: [ ...this.state.areas, { label: '' } ] })
    }

    onRemoveFieldClick = (index) => {
        const list = [...this.state.areas];
        list.splice(index, 1);
        this.setState({ areas: [...list] });
    }

    onDynamicFieldsChange = (e, index) => {
        const { name, value } = e.target;
        const list = [...this.state.areas];
        list[index][name] = value;
        this.setState({ areas: [...list] });
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        // clear server alert at submit
        this.props.clearAlert();
        
        this.setState({ 
            errorMessages: this.validateCityData()
         }, () => {
            this.createCity();
         });
    }

    onResetHandler = () => {
        // reset values
        this.setState({ ...this.baseState });
    }

    dynamicFieldsRender = () => {
        return this.state.areas.map((field, index) => {
            return (
            <div className="form-group-dynamic" key={index}>
                <InputField
                    id={`areaName${index}`}
                    name="label"
                    type="text"
                    onChangeHandler={(e) => this.onDynamicFieldsChange(e, index)}
                    initial={true}
                    initialValue={field.label}
                    label={`${index+1} `}
                    placeholder=":  "
                    icon="fas fa-signature"
                    colSize="dynamic"
                />
                <div className="form-group__dynamic-col--btn">
                    <button className="delete-field-btn" type="reset" onClick={() => this.onRemoveFieldClick(index)}> 
                        <i className="fas fa-times"/>
                    </button>
                </div>
            </div>
            );
        })
    }
    
    render() {
        return (
            <Form onSubmit={this.onFormSubmit}>
                <FormHeader label=" " />
                <ContentPadding>
                    <Alert messages={this.state.errorMessages} type="error" />
                    <div className="form-group">
                        <InputField
                            id="name"
                            name="name"
                            type="text"
                            onChangeHandler={this.onNameChangeHandler}
                            initial={true}
                            initialValue={this.state.name}
                            label=" "
                            placeholder=": "
                            icon="fas fa-signature"
                            colSize="1"
                        />
                    </div>
                    {this.dynamicFieldsRender()}
                    <div className="form-group confirm-group margin-top-big">
                        <button className="btn submit-btn" onClick={this.onAddFieldClick}>  <i className=" btn-icon fas fa-plus"></i></button>
                    </div>
                    <ContentDivider />
                    <div className="form-group confirm-group margin-top-big">
                        <input className="btn submit-btn" type="submit" value="" />
                        <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                    </div>
                </ContentPadding>
            </Form>
        )
    }
}

const mapStateToProps = state => {
    return {
        status: state.alert.success
    }
}

export default connect(mapStateToProps, { 
    createCityAction, 
    clearAlert: alertActions.clear
})(CityCreate);
