// @ts-nocheck
import React, { Component } from 'react'
import Chart from "react-google-charts";
import { connect } from 'react-redux';

import { fetchCurrentVisitLaboraotryDashboardAction } from 'actions/pagesActions/EducationalSupervisor/dashboardAction';
import ChartContainer from 'components/Dashboard/ChartContainer';
import List from 'components/Dashboard/List';
import LoadingIndicator from 'components/Dashboard/LoadingIndicator';
import OverviewMetric from 'components/Dashboard/OverviewMetric';
import ContentPadding from 'components/Utils/ContentPadding';
import { convertActivityDashboardStringToDate, countingActivityDashboardYears } from 'helpers/dashboard';
import { isSameProps } from 'helpers/objectsHelpers';
import { validateEqualPropsObjects } from 'helpers/validate';

const EXPERIMENTS_COLUMNS_HEADERS = [
    {
        label: ' ',
        key: 'qunatity'
    },
    {
        label: ' ',
        key: 'experimentExecuted'
    }
];

const SAFETY_COLUMNS_HEADERS = [
    {
        label: '',
        key: 'quantity'
    },
    {
        label: ' ',
        key: 'safetyItem'
    }
];

class LaboratoryDashboard extends Component {

    state = {
        fromDate: '',
        toDate: ''
    }

    componentDidMount() {
        this.props.fetchCurrentVisitLaboraotryDashboardAction(this.props.match.params.id);
    }

    render() {
        return (
            <ContentPadding>
                <div className="dashboard">
                    { /* <DateFilter /> */ }
                    <div className="dashboard-flex">
                        <div className="dashboard-flex__50">
                            <OverviewMetric 
                                title=""
                                metricsData={[
                                    { label: '', value: this.props.dashboard.validItem, color: 'color-primary' },
                                    { label: ' ', value: this.props.dashboard.willExpireItem, color: 'color-warning' },
                                    { label: '', value: this.props.dashboard.expiredItem, color: 'color-danger' }
                                ]}
                            />
                            <ChartContainer>
                                <Chart
                                    height={'260px'}
                                    chartType="PieChart"
                                    loader={<LoadingIndicator />}
                                    data={[
                                        [' ', ''],
                                        ['', this.props.dashboard.validItem],
                                        [' ', this.props.dashboard.willExpireItem],
                                        ['', this.props.dashboard.expiredItem],
                                    ]}
                                    options={{
                                        // Just add this option
                                        pieHole: 0.3,
                                        colors: ['#0FA591','orange' ,'#C0392B']
                                    }}
                                    rootProps={{ 'data-testid': '3' }}
                                />
                            </ChartContainer>
                        </div>
                        <div className="dashboard-flex__50">
                            { /* Figure-out some chart */ }
                            <OverviewMetric 
                                title=" "
                                metricsData={[
                                    { label: '', value: this.props.dashboard.executedExperiment, color: 'color-primary' },
                                    { label: ' ', value: this.props.dashboard.notExecutedExperiment, color: 'color-danger' },
                                    { label: ' ', value: this.props.dashboard.waitingExperiment },
                                ]}
                            />
                            <ChartContainer >
                                <Chart
                                    height={'260px'}
                                    chartType="PieChart"
                                    loader={<LoadingIndicator />}
                                    data={[
                                        [' ', ''],
                                        ['', this.props.dashboard.executedExperiment],
                                        [' ',this.props.dashboard.notExecutedExperiment ],
                                        [' ',this.props.dashboard.waitingExperiment ],
                                    ]}
                                    options={{
                                        // Just add this option
                                        pieHole: 0.3,
                                        colors: ['#0FA591', '#C0392B', 'orange']
                                    }}
                                    rootProps={{ 'data-testid': '3' }}
                                />
                            </ChartContainer>
                        </div>
                    </div>
                    <div className="dashboard-flex">
                        <div className="dashboard-flex__50">
                            <List 
                                title=' '
                                colHeaders={SAFETY_COLUMNS_HEADERS}
                                data={this.props.dashboard.numberSafetyItem}
                            />
                        </div>
                        <div className="dashboard-flex__50">
                            <List 
                                title=' '
                                colHeaders={EXPERIMENTS_COLUMNS_HEADERS}
                                data={this.props.dashboard.numberExecuteExperiment}
                            />
                        </div>
                    </div>
                    <ChartContainer 
                        title=" "
                        className="dashboard-chart-calender"
                    >
                        <Chart
                            width={950}
                            height={170* countingActivityDashboardYears(this.props.dashboard.numberExecutedDate)}
                            chartType="Calendar"
                            loader={<LoadingIndicator />}
                            data={[
                                [{ type: 'date', id: 'Date' }, { type: 'number', id: 'Won/Loss' }],
                                ...convertActivityDashboardStringToDate(this.props.dashboard.numberExecutedDate)
                            ]}
                            options={{
                                noDataPattern: {
                                    backgroundColor: '#eee',
                                    color: '#ddd'
                                },
                                colorAxis: {
                                    minValue: 1,
                                    maxValue: 7,
                                    colors: ['#87d2c8', '#096357']
                                } 
                            }}
                            rootProps={{ 'data-testid': '1' }}
                        /> 
                    </ChartContainer>
                </div>
            </ContentPadding>
        )
    }

}

const mapStateToProps = (state) => {

    let dashboard = validateEqualPropsObjects(state.dashboard, {}) ? undefined : state.dashboard;

    if(dashboard && !isSameProps(dashboard, LaboratoryDashboard.defaultProps.dashboard)) {
        dashboard = undefined;
    }

    return {
        dashboard: dashboard,
    }
}

LaboratoryDashboard.defaultProps = {
    dashboard: {
        validItem: 0,
        willExpireItem: 0,
        expiredItem: 0,
        executedExperiment: 0,
        notExecutedExperiment: 0,
        waitingExperiment: 0,
        numberExecutedDate: [],
        numberExecuteExperiment: [],
        numberSafetyItem: [],
        startSemester: '',
        endSemester: ''
    }
}


export default connect(mapStateToProps, {
    fetchCurrentVisitLaboraotryDashboardAction,
})(LaboratoryDashboard);