// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import { Form } from '../../../components/Form/Form';
import FormHeader from '../../../components/Form/FormHeader';
import InputField from '../../../components/Form/InputField';
import Alert from '../../../components/Utils/Alert';
import ContentPadding from '../../../components/Utils/ContentPadding';
import alertActions from '../../../actions/utilsActions/alertActions';
import CustomDropdown from '../../../components/Form/CustomDropdown';
import ContentDivider from '../../../components/Utils/ContentDivider';
import { 
    createExperimentAction, 
    fetchCreateExperimentRequirementsAction 
} from '../../../actions/pagesActions/SystemAdministrator/experimentAction';
import { getLabelObject, convertObjectPropOfArrayToNumber } from '../../../helpers/objectsHelpers';
import _ from 'lodash';
import { validateExperiment } from './validateExperiment';

class ExperimentCreate extends Component {

    constructor(props) {
        super(props);

        this.state = {
            title: '',
            dynamicFieldsList: [],
            errorMessages: [],
            courseId: null
        }
        this.baseState = { ...this.state };
    }

    componentDidMount() {
        // initiate call to fill the formRequirments reducer, and get the list of items and courses
        this.dropdownRef = React.createRef();
        this.props.fetchCreateExperimentRequirementsAction();
    }

    createExperiment = async () => {
        if(this.state.errorMessages.length === 0) {
            await this.props.createExperimentAction({
                title: this.state.title,
                courseId: this.state.courseId,
                items: convertObjectPropOfArrayToNumber(this.state.dynamicFieldsList, 'quantity')
            })
            if(this.props.status) {
                this.onResetHandler();
            }
        }
    }

    onFieldChange = (value, fieldName) => {
        this.setState({ [fieldName]: value });
    }

    onAddFieldClick = (e) => {
        e.preventDefault();
        // add new item selection field to insert quantity
        this.setState({ dynamicFieldsList: [...this.state.dynamicFieldsList, {value: '', quantity: ''} ] })
    }

    onDynamicItemSelect = (itemId, fieldIndex) => {
        // fieldIndex, refer to which field selected
        const tempArray = _.cloneDeep(this.state.dynamicFieldsList);
        // get the choosed item and add it to the cloned array
        tempArray[fieldIndex] = { ...getLabelObject(itemId, this.props.items), quantity: '' }
        // change the state by adding the new item
        this.setState({ dynamicFieldsList: [...tempArray] });
    }

    onDynamicQuantityChange = (e, fieldIndex) => {
        const tempArray = _.cloneDeep(this.state.dynamicFieldsList);
        tempArray[fieldIndex] = { ...tempArray[fieldIndex], quantity: e.target.value}
        this.setState({
            dynamicFieldsList: [...tempArray]
        });
    }

    onDynamicRender = () => {
        const { dynamicFieldsList } = this.state;
        return dynamicFieldsList.map((item, fieldIndex) => {
            return (
                <div className="form-group" key={fieldIndex}>
                    <CustomDropdown
                        name={fieldIndex}
                        dynamic={true}
                        colSize="2"
                        label={`    ${Number(fieldIndex)+1}`}
                        onChange={this.onDynamicItemSelect}
                        list={this.props.items}
                        title='  '
                    />
                    <InputField 
                        name={fieldIndex}
                        colSize="2"
                        locked={!dynamicFieldsList[fieldIndex].value}
                        icon="fas fa-sort-numeric-up-alt"
                        initial={true}
                        initialValue={dynamicFieldsList[fieldIndex].quantity}
                        onChangeHandler={(e) => this.onDynamicQuantityChange(e, fieldIndex)}
                        label={`   ${Number(fieldIndex)+1}  `}
                    />
                </div>
            );
        })
    }

    onFormSubmit = (e) => {
        e.preventDefault();
        // clear server alert at submit
        this.props.clearAlert();
        
        this.setState({ 
            errorMessages: validateExperiment(this.state, 'dynamicFieldsList')
         }, () => {
            this.createExperiment();
         });
    }

    onResetHandler = () => {
        // reset values
        this.setState({ ...this.baseState }, () => {
            this.dropdownRef.current.clearSelection();
        });
    }
    
    render() {
        return (
            <Form onSubmit={this.onFormSubmit}>
                <FormHeader label="  " />
                <ContentPadding>
                    <Alert messages={this.state.errorMessages} type="error" />
                    <div className="form-group">
                        <InputField
                            id="name"
                            name="name"
                            type="text"
                            onChangeHandler={(e) => this.onFieldChange(e.target.value, 'title')}
                            initial={true}
                            initialValue={this.state.title}
                            label=" "
                            placeholder=":   "
                            icon="fas fa-signature"
                            colSize="2"
                        />
                        <CustomDropdown 
                            colSize="2"
                            setRef={(ref) => this.dropdownRef = ref}
                            onChange={(selectedCourse) => this.onFieldChange(selectedCourse, 'courseId')}
                            list={this.props.courses}
                            title='  '
                            label=" "
                        />
                    </div>
                    {this.onDynamicRender()}
                    <div className="form-group confirm-group margin-top-big">
                        <button className="btn submit-btn" onClick={this.onAddFieldClick}>     <i className=" btn-icon fas fa-plus"></i></button>
                    </div>
                    <ContentDivider />
                    <div className="form-group confirm-group margin-top-big">
                        <input className="btn submit-btn" type="submit" value="" />
                        <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                    </div>
                </ContentPadding>
            </Form>
        )
    }
}

const mapStateToProps = state => {
    return {
        items: Object.values(state.items),
        courses: Object.values(state.courses),
        status: state.alert.success
    }
}

export default connect(mapStateToProps, { 
    clearAlert: alertActions.clear,
    fetchCreateExperimentRequirementsAction,
    createExperimentAction
})(ExperimentCreate);
