import React, { Fragment } from 'react'
import { Route } from 'react-router'

import Tab, { TabElement } from 'components/Utils/Tab'
import ItemCreate from './ItemCreate'
import ItemList from './ItemList'

const ItemTab = ({ match }) => {
    return (
        <Fragment>
            <Tab>
                <TabElement 
                    to="/item/create"
                    match={match}
                    title=" "
                />
                <TabElement 
                    to="/item"
                    match={match}
                    title=" "
                />
            </Tab>
            <Route 
                path="/item/create"
                exact
                component={ItemCreate}
            />
            <Route 
                path="/item"
                exact
                component={ItemList}
            />
        </Fragment>
    )
}

export default ItemTab
