import React, { useState } from 'react';

import moment from 'moment';
import Countdown from 'react-countdown';

const TimedRender = ({ children, onOpenDate, hour=0, minute=0, seconds=0, timedLabel='' }) => {
    // This component rely on moment.js to work

    const [label, setLabel] = useState(timedLabel);
    const m = onOpenDate;
    // m.add(2, 'minute')

    m.set({
        hour: hour,
        minute: minute,
        second: seconds
    });

    return (
        <div className="timed-display">
            <Countdown
                className="timed-display__counter"
                date={m.format()}
                daysInHours={true}
                onComplete={() => setLabel('')}

            >
                { children }
            </Countdown>
            { m.isSameOrAfter(moment()) ? <span className='timed-display__label'>{ label }</span> : null }
        </div>
    );
}

export default TimedRender;