import { userConstants } from '../actions/types';

const formRequirmentsReducer = (state={}, action) => {
    switch (action.type) {
        case 'CREATE_EXPERIMENT_REQUIREMENTS':
            return {
                items: action.payload.items,
                courses: action.payload.courses
            }
        case 'CREATE_SCHOOL_REQUIREMENTS':
            return {
                cities: action.payload.cities
            }
        case userConstants.LOGOUT:
            return {};
        default:
            return state;
    }
}

export default formRequirmentsReducer;