// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';
import _ from 'lodash';

import { Form } from 'components/Form/Form';
import FormHeader from 'components/Form/FormHeader';
import RadioQuestion from 'components/Form/RadioQuestion';
import Alert from 'components/Utils/Alert';
import ContentDivider from 'components/Utils/ContentDivider';
import ContentPadding from 'components/Utils/ContentPadding';
import NotFoundItem from 'components/Utils/NotFoundItem';
import { evaluateLaboratoryManagerAction, fetchEvaluateLaboratoryManagerQuestionsAction } from 'actions/pagesActions/EducationalSupervisor/evaluateAction';

class LaboratoryEvaluate extends Component {

    state = {
        answers: [],
        errorMessages: [],
    }

    async componentDidMount() {
        if(!this.props.questions.length) {
            // API call HERE to get questions
            await this.props.fetchEvaluateLaboratoryManagerQuestionsAction(this.props.match.params.id)
        }
        this.settingState();
    }

    settingState = () => {
        this.setState({ 
            answers: this.props.questions.map(question => { return { questionId: question.id, answerId: '' }}),
            errorMessages: [] 
        });
    }

    checkEmptyQuestion = () => {
        for(let answer of this.state.answers) {
            if(answer.answerId === '') {
                return ['       ']
            }
        }
        return [];
    }

    submitEvaluate = () => {
        if(!this.state.errorMessages.length) {
            // API action call to submit questions answers
            this.props.evaluateLaboratoryManagerAction(this.props.match.params.id, { answers: this.state.answers })
        }
    }

    onQuestionChange = (value, index) => {
        // cloneDeep to avoid changing the state directly
        const currentAnswers = _.cloneDeep(this.state.answers);
        currentAnswers[index].answerId = value;
        this.setState({answers: currentAnswers});
    }

    onSubmit = (e) => {
        e.preventDefault();
        this.setState({ 
            errorMessages: this.checkEmptyQuestion()
        }, () => {
            this.submitEvaluate();
        })
    }

    onResetHandler = () => {
        // reset the form
        this.settingState()
    }

    renderQuestions = () => {
        return this.props.questions.map((question, index) => {
            return (
                <RadioQuestion 
                    key={question.id}
                    question={question}
                    onChange={(newAnswer) => this.onQuestionChange(newAnswer, index)}
                    initialValue={this.state.answers[index] ? this.state.answers[index].answerId : ''}
                />
            );
        });
    }

    render() {
        return (
            <NotFoundItem
                referer="/"
            >
                <Form onSubmit={this.onSubmit}>
                    <FormHeader 
                        label=" "
                        referer={`/visit/current/laboratory-manager/${this.props.match.params.id}`}
                        refererLabel="  "
                        icon="fas fa-chevron-left"
                    />
                    <ContentPadding>
                        <Alert type="404" messages={this.state.errorMessages} />
                        { this.renderQuestions() }
                    </ContentPadding>
                    <ContentPadding>
                        <ContentDivider />
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value=" "/>
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                        </div>
                    </ContentPadding>
                </Form>
            </NotFoundItem>
        )
    }
}

const mapStateToProps = state => {
    return {
        questions: Object.values(state.laboratoryManagerQuestions)
    }
}

export default connect(mapStateToProps, {
    fetchEvaluateLaboratoryManagerQuestionsAction,
    evaluateLaboratoryManagerAction
})(LaboratoryEvaluate);