import mylabs from '../../../apis/mylabs';
import catchHandler from '../../../helpers/catchHandler';
import loadingActions from '../../utilsActions/loadingActions';

export const fetchAllDefectsAction = () => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to patch
            const response = await mylabs.get('/defect-report')

            // send to the invites reducer
            dispatch({ type: 'FETCH_DEFECTS', payload: { ...response.data.reports } });

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}

export const fetchDefectAction = (defectId) => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to patch
            const response = await mylabs.get(`/defect-report/${defectId}`)

            // send to the invites reducer
            dispatch({ type: 'FETCH_DEFECT', payload: { ...response.data.report } });

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}

export const updateDefectStatus = (defectId, status) => {
    return async (dispatch) => {

        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            
            // Calling the Server API to patch
            const response = await mylabs.patch(`/defect-report`, { status, id: defectId })

            // send to the invites reducer
            dispatch({ type: 'UPDATE_DEFECT', payload: { ...response.data.report } });

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}