const getNavList = (role, hasSchool=true) => {
    // return the navigation of the user role.
    // if hasShool true, give him navigation with ability to access school pages
    switch (role) {
        case 'School_Manager':
            return hasSchool ? schoolManagerHasSchool : schoolManager;
        case 'Science_Teacher':
            return hasSchool ? scienceTeacher : schoolMemberWithNoSchool;
        case 'Educational_Supervisor':
            return educationalSupervisor;
        case 'Laboratory_Manager':
            return hasSchool ? labManager : schoolMemberWithNoSchool;
        case 'System_Administrator':
            return sysAdmin;
        case 'Support':
            return support;
        default:
            return [];
    }
};

export default getNavList;
