// @ts-nocheck
import React, { useState, useEffect } from 'react'
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

const CustomDatePicker = ({ 
    id, 
    label, 
    colSize, 
    name, 
    locked, 
    onChangeHandler, 
    initial, 
    initialDate, 
    minDate, 
    placeholder,
    weekend=true
}) => {

    const [selectedDate, setSelectedDate] = useState(initialDate);

    const renderLabel = () => {
        return (
            label ? <label htmlFor={id} className="form__label"> {label} </label> : null 
        );
    }

    const onDatePickerSelect = (newDate) => {
        setSelectedDate(newDate);
        onChangeHandler(newDate);
    }

    useEffect( () => {
        // When the initialValue change, re-render the component
        if(initial) {
            // This useEffect will make change only if the initial props set to true
            setSelectedDate(initialDate);
        }
    }, [initialDate, initial]);

    return (
        <div className={`form-group__${colSize}-col-datepicker`}>
            {renderLabel()}
            <div className="form-control-datepicker">
                <DatePicker
                    autoComplete="off"
                    name={name}
                    disabled={locked}
                    className="form__input"
                    selected={selectedDate}
                    onChange={onDatePickerSelect}
                    dateFormat='MM/dd/yyyy'
                    minDate={minDate}
                    filterDate={ date => weekend ? date.getDay() !== 6 && date.getDay() !== 5 : date }
                    showYearDropdown
                    scrollableMonthYearDropdown
                    placeholderText={placeholder}
                />
                <div className="icon-container">
                    <i className={`form__input--icon far fa-calendar-alt`}></i>
                </div>
            </div>
        </div>
    )


}

export default CustomDatePicker;
