import React, { Fragment } from 'react'
import { Route } from 'react-router'

import Tab, { TabElement } from 'components/Utils/Tab'
import EducationalSupervisorCreate from './EducationalSupervisorCreate'
import EducationalSupervisorList from './EducationalSupervisorList'


const EducationalSupervisorTab = ({ match }) => {
    return (
        <Fragment>
            <Tab>
                <TabElement 
                    to="/educational-supervisor/create"
                    match={match}
                    title=" "
                />
                <TabElement 
                    to="/educational-supervisor"
                    match={match}
                    title=" "
                />
            </Tab>
            <Route 
                path="/educational-supervisor/create"
                exact
                component={EducationalSupervisorCreate}
            />
            <Route 
                path="/educational-supervisor"
                exact
                component={EducationalSupervisorList}
            />
        </Fragment>
    )
}

export default EducationalSupervisorTab
