import React from 'react'
import { connect } from 'react-redux'

import EducationalSupervisorHomePage from './EducationalSupervisor/EducationalSupervisorHomePage'
import SchoolManagerHomePage from './SchoolManager/SchoolManagerHomePage';
import SchoolMemberHomePage from './SchoolMember/SchoolMemberHomePage';
import SystemAdministratorHomePage from './SystemAdministrator/SystemAdministratorHomePage';

const HomePage = ({ role }) => {

    const renderRoleHomepage = () => {
        switch(role) {
            case 'System_Administrator':
                return <SystemAdministratorHomePage />
            case 'Educational_Supervisor':
                return <EducationalSupervisorHomePage />
            case 'School_Manager':
                return <SchoolManagerHomePage />
            case 'Science_Teacher':
            case 'Laboratory_Manager':
                return <SchoolMemberHomePage />
            default:
                return null;
        }
    }

    return (
        <div>
            { renderRoleHomepage() }
        </div>
    )
}

const mapStateToProps = state => {
    return {
        role: state.auth.user.permissions,
    }
}

export default connect(mapStateToProps)(HomePage)
