// @ts-nocheck
import React from 'react'
import { connect } from 'react-redux';
import _ from 'lodash';
import { Link } from 'react-router-dom';

import { loginAction, logoutAction } from '../../actions/userActions';
import { validateEmail, validatePassword } from '../../helpers/validate';
import ContentDivider from '../../components/Utils/ContentDivider';
import InputField from '../../components/Form/InputField';
import PublicForm from '../../components/Form/PublicForm';
import { refererAction } from '../../actions/utilsActions/refererAction';
import alertActions from '../../actions/utilsActions/alertActions';
import Alert from '../../components/Utils/Alert';
import LoadingOverlay from 'react-loading-overlay';

class Login extends React.Component {
    
    componentDidMount() {
        // Getting the URL referer, to navigate the user to the page after login
        const referer = _.isEmpty(this.props.location.state) ? '/' : this.props.location.state.from.pathname;
        // logout to clean the localStorage from old JWT token 
        this.props.logoutAction();
        this.props.refererAction(referer);
    }

    state = {
        email: '',
        password: '',
        errorMessages: []
    };

    validateUserData = () => {
        return [
            validateEmail(this.state.email),
            validatePassword(this.state.password)
        // The array filter method will drop empty messages
        ].filter(message => message)
    }

    loginUser = () => {
        if(this.state.errorMessages.length === 0) {
            // if there is no error messages, make login call to the API server
            const user = this.state;
            this.props.loginAction({ ...user });
        }
    }

    onChangeHandler = (e) => {
        // Input field handler
        const { name, value } = e.target;
        this.setState({
            [name]: value
        });
    }

    onLoginSubmit = (e) => {
        // on user Submit
        e.preventDefault();
        this.props.clearAlert();
        this.setState({
            errorMessages: this.validateUserData()
        }, () => {
            this.loginUser();
        });
    }

    render () {
        return (
            <div className="form-overlay">
                <LoadingOverlay
                    active={this.props.isLoading}
                    text=" "
                    spinner
                >
                    <PublicForm 
                        onSubmit={this.onLoginSubmit} 
                        type="login"
                    >
                            <Alert messages={[...this.state.errorMessages, this.props.alert.message]} type="error" />

                            <div className="form-group">
                                <InputField
                                    name="email"
                                    onChangeHandler={this.onChangeHandler}
                                    type="text"
                                    id="email"
                                    placeholder=" "
                                    icon="fas fa-at"
                                    colSize="1"
                                />
                            </div>
                            <div className="form-group">
                                <InputField
                                    name="password"
                                    onChangeHandler={this.onChangeHandler}
                                    type="password"
                                    id="password"
                                    placeholder=" "
                                    icon="fas fa-key"
                                    colSize="1"
                        
                                />
                            </div>
                            <div className="form-group">
                                <Link
                                    to="/forget-pass"
                                    className="forget-pass"
                                >    </Link>
                            </div>
                            <div className="form-group">
                                <div className="form-group__1-col form-group__1-col--center">
                                    <input type="submit" className="form__submit display-block" value=""/>
                                </div>
                            </div>
                            <div className="form-group">
                                <ContentDivider label="  "/>
                            </div>
                            <div className="form-group">
                                <div className="form-group__1-col form-group__1-col--center">
                                    <Link className="btn btn--white" to="/register">  </Link>
                                </div>
                            </div>
                    </PublicForm>
                </LoadingOverlay>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        alert: state.alert,
        isLoading: state.isLoading
    }
}

export default connect(mapStateToProps, 
    { 
        loginAction, 
        logoutAction, 
        refererAction, 
        clearAlert: alertActions.clear 
    })(Login);