// @ts-nocheck
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { acceptMemberInvite, fetchMemberInvites, rejectMemberInvite } from '../../actions/pagesActions/SchoolMember/memberInvitesAction';
import ConfirmPreviewList from '../../components/List/PreviewList/ConfirmPreviewList';
import PreviewListContainer from '../../components/List/PreviewList/PreviewListContainer';

import NotAllowed from '../../components/Utils/NotAllowed';

class InvitesList extends Component {

    componentDidMount() {
        // action call for the available invites
        if(this.props.invites.length === 0 && !this.props.hasSchool) {
            this.props.fetchMemberInvites();
        }
    }

    onAcceptHandler = (schoolId) => {
        this.props.acceptMemberInvite(schoolId);
    }

    onRejectHandler = (schoolId) => {
        this.props.rejectMemberInvite(schoolId);
    }

    render() {
        return (
            <NotAllowed
                hasSchool={!this.props.hasSchool}
                referer="/"
                statusCode="403"
                message="    ,     "
                btnLabel="  "
            >
                <PreviewListContainer>
                    <ConfirmPreviewList 
                        list={this.props.invites}
                        className="full"
                        tableClassName="fixed-big-height"
                        listHeader="  "
                        emptyMessage="   "
                        acceptAction={this.onAcceptHandler}
                        rejectAction={this.onRejectHandler}
                    />
                </PreviewListContainer>
            </NotAllowed>
        );
    }
}

const mapStateToProps = state => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
        invites: Object.values(state.invites)
    }
}

export default connect(mapStateToProps, { 
    fetchMemberInvites, 
    rejectMemberInvite, 
    acceptMemberInvite 
})(InvitesList);