// @ts-nocheck
import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux';

import { Form } from 'components/Form/Form';
import FormHeader from 'components/Form/FormHeader';
import InputField from 'components/Form/InputField';
import TextArea from 'components/Form/TextArea';
import Alert from 'components/Utils/Alert';
import ContentDivider from 'components/Utils/ContentDivider';
import ContentPadding from 'components/Utils/ContentPadding';
import NotAllowed from 'components/Utils/NotAllowed';
import NotFoundItem from 'components/Utils/NotFoundItem';
import parseDateToString, { parseDateForMoment } from 'helpers/parseDate';
import { validateEqualPropsObjects } from 'helpers/validate';
import { acceptPracticalRequestAction, fetchPracticalRequestAction, rejectPracticalRequestAction, cancelConfirmedPracticalRequestAction, confirmPracticalExecutionAction } from 'actions/pagesActions/SchoolMember/LaboratoryManager/PracticalRequestAction';
import { getLabelObject } from 'helpers/objectsHelpers';
import { classesList } from 'helpers/fixedLists';
import TimedRender from 'components/Utils/TimedRender';
import moment from 'moment';

class PracticalRequestView extends Component {

    state = {
        id: '',
        createDate: '',
        scienceTeacher: '',
        labManager: '',
        reqStatus: null,
        confirmDate: '',
        courseName: '',
        experimentName: '',
        classNo: '',
        labId: '',
        quantity: '',
        executeDate: '',
        confirmNote: '',
        items_used: [],
        executed: null,
        evaluated: null
    }

    async componentDidMount() {
        if(this.props.hasSchool && (!this.props.request || validateEqualPropsObjects(this.props.request, {}))) {
            // API fetch call action
            await this.props.fetchPracticalRequestAction(this.props.match.params.id)
        }
        this.settingState();
    }

    componentDidUpdate(nextProps) {
        if(!validateEqualPropsObjects(nextProps.request, this.props.request)) {
            this.settingState();
        }
    }

    settingState = () => {
        if(this.props.request) {
            this.setState({
                ...this.props.request,
                reqStatus: this.props.request.status,
                createDate: new Date(this.props.request.createDate),
                executeDate: new Date(this.props.request.executeDate),
                confirmDate: new Date(this.props.request.confirmDate)
            })
        }
    }

    onNoteChange = (e) => {
        this.setState({ note: e.target.value });
    }

    onAcceptHandler = (e) => {
        e.preventDefault();
        this.props.acceptPracticalRequestAction({
            requestId: this.state.id,
            state: true,
            note: this.state.note
        })
    }

    onRejectHandler = () => {
        // reject the practical experiment request
        this.props.rejectPracticalRequestAction({
            requestId: this.state.id,
            state: false,
            note: this.state.note
        })
    }

    onCancelRequestConfirmation = () => {
        // cancel request confirm after accepting it
        this.props.cancelConfirmedPracticalRequestAction(this.state.id)
    }

    onConfirmExecutionHandler = (e) => {
        // CONFIRM that the practical experiment has been executed by science teacher
        e.preventDefault();
        this.props.confirmPracticalExecutionAction(this.state.id, true);
    }

    onDeniedExecutionHandler = () => {
        // confirm that the practical experiment has not been executed by science teacher
        this.props.confirmPracticalExecutionAction(this.state.id, false);
    }

    getFormOnSubmitFunction = () => {
        if(this.state.reqStatus === true && this.state.executed === null) {
            return this.onConfirmExecutionHandler;
        } else {
            return this.onAcceptHandler;
        }
    }

    renderUsedItems = () => {
        if(this.state.reqStatus) {
            return (
                <Fragment>
                    <FormHeader 
                        label="   " 
                    />
                    <ContentPadding>
                        { 
                            this.state.items_used.map((item) => {
                                return (
                                    <div className="form-group" key={item.value}>
                                        <InputField
                                            id="id"
                                            name="id"
                                            label={` `}
                                            initial={true}
                                            initialValue={item.value}
                                            locked={true}
                                            icon="fas fa-lock"
                                            colSize="3"
                                        />
                                        <InputField
                                            id="id"
                                            name="id"
                                            label={` `}
                                            initial={true}
                                            initialValue={item.label}
                                            locked={true}
                                            icon="fas fa-lock"
                                            colSize="3"
                                        />
                                        <InputField
                                            id="id"
                                            name="id"
                                            label=" "
                                            initial={true}
                                            initialValue={item.quantity}
                                            locked={true}
                                            icon="fas fa-lock"
                                            colSize="3"
                                        />
                                    </div>
                                );
                            })
                        }
                    </ContentPadding>
                </Fragment>
            )    
        }
    }

    renderFormButton = () => {
        const momentCurrent = moment(parseDateForMoment(moment()._d), 'YYYY-MM-DD');
        const momentExecuteDate = this.state.reqStatus ? moment(parseDateForMoment(this.state.executeDate), 'YYYY-MM-DD') : null;
        if(this.state.reqStatus === true && momentCurrent.isBefore(momentExecuteDate)) {
            // in case, it's accepted and the execute date have not come yet
            return (
                <ContentPadding>
                    <ContentDivider />
                    <div className="form-group confirm-group margin-top-big">
                        <input className="btn cancel-btn-red no-margin-right" type="reset" value="  " onClick={this.onCancelRequestConfirmation}/>
                    </div>
                </ContentPadding>
            );
        } else if(this.state.reqStatus === null) {
            // in case, it's not accepted by lab manager and need confirmation
            return (
                <ContentPadding>
                    <ContentDivider />
                    <div className="form-group confirm-group margin-top-big">
                        <input className="btn submit-btn" type="submit" value="" />
                        <input className="btn cancel-btn-red" type="reset" value="" onClick={this.onRejectHandler}/>
                    </div>
                </ContentPadding>
            );
        } else if(this.state.reqStatus === true && momentCurrent.isSameOrAfter(momentExecuteDate) && this.state.executed === null) {
            const hour= this.state.classNo ? this.state.classNo + 7 : 0;
            return (
                <ContentPadding>
                    <ContentDivider />
                    <TimedRender 
                        hour={hour} 
                        onOpenDate={momentExecuteDate} 
                        timedLabel={'     '}
                    >
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value="  " />
                            <input className="btn cancel-btn-red" type="reset" value="   " onClick={this.onDeniedExecutionHandler}/>
                        </div>
                    </TimedRender>
                </ContentPadding>
            );
        }

        return <div className="margin-top-big"/>;
    }

    renderConfirmedIndicatorFields = () => {
        if(this.state.reqStatus !== null) {
            // show the confirmation states and the confirmed date
            return (
                <div className="form-group">
                    <InputField
                        id="repeatExperiment"
                        name="repeatExperiment"
                        label="  "
                        initial={true}
                        initialValue={this.state.reqStatus ? '  ' : '  '}
                        locked={true}
                        icon={this.state.reqStatus ? 'fas fa-check' : 'fas fa-times'}
                        colSize="2"
                        className={this.state.reqStatus ? "color-primary" : 'color-danger' }
                    />
                    <InputField
                        id="repeatExperiment"
                        name="repeatExperiment"
                        label="  "
                        initial={true}
                        initialValue={parseDateToString(this.state.confirmDate)}
                        locked={true}
                        icon="fas fa-lock"
                        colSize="2"
                    />
                </div>
            );
        }
        return null;
    }

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.hasSchool}
                referer="/invites"
                statusCode="403"
                message="       "
                btnLabel="   "
            >
                <NotFoundItem
                    referer="/"
                >
                    <Form onSubmit={this.getFormOnSubmitFunction()}>
                        <FormHeader 
                            label={` `}
                            icon="fas fa-chevron-left"
                            refererLabel=" "
                            referer={`/practical-request`}   
                        />
                        <ContentPadding>
                            <Alert messages={[]} type="404" />
                            <div className="form-group">
                                <InputField
                                    id="id"
                                    name="id"
                                    label=" "
                                    initial={true}
                                    initialValue={this.state.id}
                                    locked={true}
                                    icon="fas fa-lock"
                                    colSize="2"
                                />
                                <InputField
                                    id="id"
                                    name="id"
                                    label="  "
                                    initial={true}
                                    initialValue={parseDateToString(this.state.createDate)}
                                    locked={true}
                                    icon="fas fa-lock"
                                    colSize="2"
                                />
                            </div>
                            <div className="form-group">
                                <InputField
                                    id="id"
                                    name="id"
                                    label=" "
                                    initial={true}
                                    initialValue={this.state.scienceTeacher}
                                    locked={true}
                                    icon="fas fa-lock"
                                    colSize="2"
                                />
                                { this.state.reqStatus !== null ? 
                                    <InputField
                                        id="id"
                                        name="id"
                                        label=" "
                                        initial={true}
                                        initialValue={this.state.labManager}
                                        locked={true}
                                        icon="fas fa-lock"
                                        colSize="2"
                                    /> 
                                    : 
                                    null    
                                }
                            </div>
                            { this.renderConfirmedIndicatorFields() }
                            
                        </ContentPadding>

                            <FormHeader 
                                label=" "
                            />
                            <ContentPadding>
                                <div className="form-group">
                                    <InputField 
                                        label=" "
                                        initial={true}
                                        initialValue={this.state.courseName}
                                        locked={true}
                                        icon="fas fa-lock"
                                        colSize="2"
                                    />
                                    <InputField 
                                        label=" "
                                        initial={true}
                                        initialValue={this.state.experimentName}
                                        locked={true}
                                        icon="fas fa-lock"
                                        colSize="2"
                                    />
                                </div>
                                <div className="form-group">
                                    <InputField 
                                        label=""
                                        initial={true}
                                        initialValue={this.state.labId}
                                        locked={true}
                                        icon="fas fa-lock"
                                        colSize="2"
                                    />
                                    <InputField 
                                        label=" "
                                        initial={true}
                                        initialValue={this.state.classNo ? getLabelObject(this.state.classNo, classesList).label : '' }
                                        locked={true}
                                        icon="fas fa-lock"
                                        colSize="2"
                                    />
                                </div>
                                <div className="form-group">
                                    <InputField
                                        id="repeatExperiment"
                                        name="repeatExperiment"
                                        label="  "
                                        initial={true}
                                        initialValue={this.state.quantity}
                                        locked={true}
                                        icon="fas fa-lock"
                                        colSize="2"
                                    />
                                    <InputField
                                        id="repeatExperiment"
                                        name="repeatExperiment"
                                        label="  "
                                        initial={true}
                                        initialValue={parseDateToString(this.state.executeDate)}
                                        locked={true}
                                        icon="fas fa-lock"
                                        colSize="2"
                                    />
                                </div>
                            </ContentPadding>
                            <ContentPadding>
                                <div className="form-group">
                                    <TextArea 
                                        label={this.state.reqStatus === null ? " " : "  "}
                                        locked={this.state.reqStatus !== null}
                                        initial={true}
                                        initialValue={!this.state.confirmNote && (this.state.reqStatus || this.state.reqStatus === false) ? ' ' : this.state.confirmNote}
                                        onChangeHandler={this.onNoteChange}
                                        placeholder=":       "
                                        colSize="1"
                                    />
                                </div>
                            </ContentPadding>
                            { this.renderUsedItems() }
                            { this.renderFormButton() }
                    </Form>
                </NotFoundItem>
            </NotAllowed>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
        request: state.experiments[ownProps.match.params.id]
    }
}

export default connect(mapStateToProps, {
    fetchPracticalRequestAction,
    acceptPracticalRequestAction,
    rejectPracticalRequestAction,
    cancelConfirmedPracticalRequestAction,
    confirmPracticalExecutionAction
})(PracticalRequestView);