import { userConstants } from '../actions/types';
import jwt_decode from 'jwt-decode';

let user = localStorage.getItem('token') ? jwt_decode(localStorage.getItem('token')) : null; //JSON.parse(localStorage.getItem('token'));
const initialState = user ? { LoggedIn: true, user } : {};

const authReducer = (state=initialState, action) => {
    switch (action.type) {
        case userConstants.LOGIN_REQUEST:
            // for indicating login process
            return {
                loggingIn: true
            }

        case userConstants.LOGIN_SUCCESS:
            // on login success, set the JWT token in localStorage
            localStorage.setItem('token', action.token);
            return {
                loggedIn: true,
                user: jwt_decode(action.token)
            }

        case userConstants.LOGIN_FAILURE:
            // in case of login failure
            localStorage.removeItem('token');
            return {};

        case 'RENEW_JWT':
            // set the new JWT in case of updating profile
            localStorage.setItem('token', action.token);
            return {
                loggedIn: true,
                user: jwt_decode(action.token)
            }

        case userConstants.LOGOUT:
            // Remove the JWT token
            localStorage.removeItem('token');
            return {};
            
        default:
            return state;
    }
}


export default authReducer;