// @ts-nocheck
import { fetchRemovedLabItemsAction } from 'actions/pagesActions/SchoolMember/LaboratoryManager/LaboratoryItemsAction';
import alertActions from 'actions/utilsActions/alertActions';
import { clearTableListAction } from 'actions/utilsActions/clearTableListAction';
import CheckBox from 'components/Form/CheckBox';
import CustomDatePicker from 'components/Form/CustomDatePicker';
import CustomDropdown from 'components/Form/CustomDropdown';
import DropMenu from 'components/Form/DropMenu';
import InputField from 'components/Form/InputField';
import ListLayout from 'components/List/ListLayout';
import PaginatedList from 'components/List/PaginatedList';
import SearchFilter from 'components/List/SearchFilter';
import parseDateToString, { onMinDateChange } from 'helpers/parseDate';
import React, { Component } from 'react';
import { connect } from 'react-redux';

const ITEMS_COLUMNS_HEADERS = [
    {
        label: '',
        key: 'id'
    },
    {
        label: ' ',
        key: 'name'
    },
    {
        label: ' ',
        key: 'removeDate'
    },
    {
        label: ' ',
        key:'reason'
    }
]

class RemovedItemsList extends Component {

    constructor(props) {
        super(props);
        this.itemsRef = React.createRef();

        this.state = {
            page: 1,
            id: '',
            itemId: 'all',
            fromDeleteDate: '',
            toDeleteDate: '',
            lost: false,
            broke: false,
            expired: false,
            tainted: false,
            used: false
        }

        this.baseState = { ...this.state };
    }

    componentDidMount() {
        if(this.itemsRef.current) {
            // set the labs dropmenu with ALL option at mount
            this.itemsRef.current.selectItem({label: ' ', value: 'all'})
        }
    }

    convertParamsDateToString = () => {
        // convert the date format for the API server
        const newParams = { ...this.state };
        newParams.fromDeleteDate = parseDateToString(newParams.fromDeleteDate);
        newParams.toDeleteDate = parseDateToString(newParams.toDeleteDate);
        return newParams;
    }

    convertStringToDate = (items=[]) => {
        // Convert string to date, to display it in dd/MM/yyyy format without time
        return items.map(item => {
            // clone the item, we don't want it to change in the reducer 
            const newItem = {...item}
            newItem.removeDate = parseDateToString(new Date(item.removeDate)).replace(/-/g,"/");
            return newItem
        });
    }

    onFiltersChange = (value, fieldName) => {
        switch (fieldName) {
            case 'fromDeleteDate':
                if(this.state.toDeleteDate && value.getTime() >= this.state.toDeleteDate.getTime()) {
                    // reset the 'to' date becasue it maybe become before the 'from' date
                    this.setState({ fromDeleteDate: value, toDeleteDate: '' });
                    return;
                }
                this.setState({ fromDeleteDate: value });
                break;
            default:
                this.setState({ [fieldName]: value });
                break;
        }
    }

    getRemovedItemsList = () => {
        // API call here
        this.props.fetchRemovedLabItemsAction(
            this.props.match.params.labId, 
            this.convertParamsDateToString()
        );
    }

    onPageChange = (currentPage) => {
        this.props.clearAlert();

        // set the currentPage, and make API call after that
        this.setState({ page: currentPage.selected + 1 }, () => {
            this.getRemovedItemsList();
        });
    }

    onFilterSubmit = (e) => {
        e.preventDefault();
        this.setState({ page: 1 }, () => {
            this.getRemovedItemsList();
        })
    }

    onFilterReset = () => {
        this.setState({ ...this.baseState }, () => {
            if(this.itemsRef.current) {
                // set the labs dropmenu with ALL option at mount
                this.itemsRef.current.selectItem({label: ' ', value: 'all'})
            }
        });
    }

    render() {
        return (
            <div className="margin-top-huge no-padding-top">
                <ListLayout>
                    <PaginatedList
                        totalPages={this.props.totalPages}
                        data={ this.convertStringToDate(this.props.items) }
                        onPageChange={this.onPageChange}
                        colHeaders = {ITEMS_COLUMNS_HEADERS}
                        initialPage={this.state.page - 1}
                    />
                    <SearchFilter onSubmit={this.onFilterSubmit}>
                        
                    <div className="from-group">
                        
                            <InputField
                                id="id"
                                name="id"
                                initial={true}
                                initialValue={this.state.id}
                                onChangeHandler={(e) => this.onFiltersChange(e.target.value, 'id')}
                                colSize="1"
                                placeholder="  "
                                icon="fas fa-search"
                            />
                        </div>
                        <div className='from-group'>
                            <CustomDropdown 
                                title="  "
                                colSize="1"
                                setRef={(ref) => this.itemsRef = ref}
                                list={[{label: ' ', value: 'all'}, ...this.props.ministryItems]}
                                onChange={(selectedLab) => this.onFiltersChange(selectedLab, 'itemId')}
                            />
                        </div>
                        <div className="form-group">
                            <DropMenu 
                                colSize="1"
                                title="  "
                            >
                                <CustomDatePicker 
                                    colSize="1"
                                    placeholder=" "
                                    onChangeHandler={(newDate) => this.onFiltersChange(newDate, 'fromDeleteDate') }
                                    initial={true}
                                    initialDate={this.state.fromDeleteDate}
                                />
                                <CustomDatePicker 
                                    colSize="1"
                                    placeholder=" "
                                    onChangeHandler={(newDate) => this.onFiltersChange(newDate, 'toDeleteDate') }
                                    initial={true}
                                    initialDate={this.state.toDeleteDate}
                                    minDate={onMinDateChange(this.state.fromDeleteDate)}
                                />
                            </DropMenu>
                        </div>
                        <div className="form-group">
                            <DropMenu 
                                colSize="1"
                                title=" "
                            >
                                <div className="margin-top">
                                    <CheckBox
                                        id="lost"
                                        name="lost"
                                        label=""
                                        initial={true}
                                        initialValue={this.state.lost}
                                        onChange={(isChecked) => {this.onFiltersChange(isChecked, 'lost')}}
                                    />
                                </div>
                                <div className="margin-top">
                                    <CheckBox 
                                        id="broke"
                                        name="broke"
                                        label=""
                                        initial={true}
                                        initialValue={this.state.broke}
                                        onChange={(isChecked) => {this.onFiltersChange(isChecked, 'broke')}}
                                    />
                                </div>
                                <div className="margin-top">
                                    <CheckBox 
                                        id="expired"
                                        name="expired"
                                        label=" "
                                        initial={true}
                                        initialValue={this.state.expired}
                                        onChange={(isChecked) => {this.onFiltersChange(isChecked, 'expired')}}
                                    />
                                </div>
                                <div className="margin-top">
                                    <CheckBox 
                                        id="tainted"
                                        name="tainted"
                                        label=""
                                        initial={true}
                                        initialValue={this.state.tainted}
                                        onChange={(isChecked) => {this.onFiltersChange(isChecked, 'tainted')}}
                                    />
                                </div>
                                <div className="margin-top">
                                    <CheckBox 
                                        id="used"
                                        name="used"
                                        label=""
                                        initial={true}
                                        initialValue={this.state.used}
                                        onChange={(isChecked) => {this.onFiltersChange(isChecked, 'used')}}
                                    />
                                </div>
                            </DropMenu>
                        </div>
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value="" />
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onFilterReset} />
                        </div>
                    </SearchFilter>
                </ListLayout>
            </div>
        )
    }
    componentWillUnmount() {
        // clear the table list in redux store when unmount
        this.props.clearTableListAction();
    }
}

const mapStateToProps = state => {
    return {
        items: state.tableList.removedLaboratoryItems,
        totalPages: state.tableList.totalPages,
        ministryItems: Object.values(state.items)
    }
}

export default connect(mapStateToProps, { 
    fetchRemovedLabItemsAction,
    clearTableListAction, 
    clearAlert: alertActions.clear 
})(RemovedItemsList);