export const convertActivityDashboardStringToDate = (datesArray) => {
    if(datesArray) {
        return datesArray.map( execute => {
            return [new Date(execute[0]), execute[1]];
        })
    }
    return [];
}

export const countingActivityDashboardYears = (datesArray) => {
    let count = 0;
    if(datesArray) {
        const yearsArray = [];
        for(let executeDate of datesArray) {
            const date = new Date(executeDate[0]);
            if(!yearsArray.includes(date.getFullYear())) {
                yearsArray.push(date.getFullYear());
                count += 1; 
            }
        }
    }
    return count ? count : 1;
}

export const rateColors = (rate) => {
    if(rate >= 4) {
        return 'color-success';
    } else if(rate <= 4 && rate > 3) {
        return 'color-primary';
    } else if(rate <= 3 && rate > 2) {
        return 'color-warning';
    } else if(rate <= 2 && rate > 0) {
        return 'color-danger';
    }
    return '';
}