// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import Form from '../../../components/Form/Form'
import FormHeader from '../../../components/Form/FormHeader'
import InputField from '../../../components/Form/InputField'
import NotFoundItem from '../../../components/Utils/NotFoundItem';
import ContentPadding from '../../../components/Utils/ContentPadding'
import { validateEqualPropsObjects } from '../../../helpers/validate'
import alertActions from '../../../actions/utilsActions/alertActions';
import ContentDivider from '../../../components/Utils/ContentDivider';
import CustomMultipleDropdown from '../../../components/Form/CustomMultipleDropdown';
import { getItemsDifferenceStatus } from '../../../helpers/objectsHelpers';
import { fetchLabManagerAction, updateLabManagerLabs } from '../../../actions/pagesActions/SchoolManager/membersAction';
import NotAllowed from '../../../components/Utils/NotAllowed';

class LabManagerView extends Component {

    state = {
        id: '',
        name: '',
        labs: []
    }

    async componentDidMount () {
        // connecting to the dropdown menu to reach clearSelection functionality
        this.dropdownRef = React.createRef();
        try {
            // check if teacher exist in local redux store, and courses length not equal 0
            if(this.props.labManager && this.props.hasSchool) {
                this.settingState();
            } else if(this.props.hasSchool){
                // if this particular lab manager not exist in local redux store, call API to fetch it
                await this.props.fetchLabManagerAction(this.props.match.params.id)
                this.settingState();
            }
        } catch (e){ }
    }

    settingState = () => {
        // setting the state of lab manager from redux store
        const { labManager } = this.props;
        if(labManager) {
            this.setState({
                id: labManager.id,
                name: labManager.name,
                labs: labManager.laboratories
             }, () => {
                if(this.props.laboratories.length !== 0) {
                    // avoid selecting default labs when the list actually empty
                    this.dropdownRef.current.deselectAll();
                    this.dropdownRef.current.selectMultipleItems(this.state.labs);
                }
             });
        }
    }

    onLabsChangeHandler = (selectedLabs) => {
        this.setState({ labs: [ ...selectedLabs ] });
    }

    onFormSubmit = async (e) => {
        e.preventDefault();
        const { labManager } = this.props;
        if(labManager) {
            const { labs } = this.state;
            const originalLabs = labManager.laboratories;
            if(!validateEqualPropsObjects(labs, originalLabs)) {
                // update action
                await this.props.updateLabManagerLabs({
                    laboratories: getItemsDifferenceStatus(originalLabs, labs),
                    labManagerId: this.state.id
                })
                // reset user last update after success submit
                this.settingState();
            }
        }
    }

    onResetHandler = () => {
        // remove alerts
        this.props.clearAlert();
        // reset form
        this.settingState();
    }

    render() {
        return (
            <NotAllowed
                hasSchool={this.props.hasSchool}
                referer="/"
                message="    "
                statusCode="403"
                btnLabel="  "
            >
                <NotFoundItem 
                    referer="/school"
                >
                    <Form onSubmit={this.onFormSubmit}>
                        <FormHeader 
                            label="  " 
                            referer="/school"
                            refererLabel=""
                            icon="fas fa-chevron-left"
                        />
                        <ContentPadding>
                            <div className="form-group">
                                <InputField
                                    id="id"
                                    name="id"
                                    type="text"
                                    initial={true}
                                    initialValue={this.state.id }
                                    label=" "
                                    locked={true}
                                    icon="fas fa-lock"
                                    colSize="2"
                                />
                                <InputField
                                    id="name"
                                    name="name"
                                    type="text"
                                    initial={true}
                                    initialValue={this.state.name}
                                    label=" "
                                    locked={true}
                                    icon="fas fa-lock"
                                    colSize="2"
                                />
                            </div>
                            <div className="form-group">
                                <CustomMultipleDropdown 
                                    colSize="2"
                                    label="   "
                                    setRef={(ref) => this.dropdownRef = ref}
                                    onChange={this.onLabsChangeHandler}
                                    list={this.props.laboratories}
                                    title=' '
                                    selectedTitle=" "
                                />
                            </div>
                        </ContentPadding>
                        <ContentDivider />
                        <ContentPadding>
                            <div className="form-group confirm-group margin-top-big">
                                <input className="btn submit-btn" type="submit" value=""/>
                                <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                            </div>
                        </ContentPadding>
                    </Form>
                </NotFoundItem>
            </NotAllowed>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
        laboratories: Object.values(state.laboratories)
                        .map(lab => { return {label: lab.id, value: lab.id } }),
        labManager: Object.values(state.laboratoryManagers)
                        .filter(labManager => labManager.id.toString() === ownProps.match.params.id)[0],
    }
}

export default connect(mapStateToProps, { 
    fetchLabManagerAction,
    updateLabManagerLabs,
    clearAlert: alertActions.clear
})(LabManagerView);
