import React, { useRef } from 'react'

const RadioOption = ({ optionLabel, id, questionId, value, onClick, answer }) => {

    const refRadio = useRef(null);

    const onRadioBtnClick = () => {
        // refRadio.current.checked = true;
        onClick(value);
    }
    

    return (
        <div className="radio-question__radio-option">
            <input id={id} className="radio-question__radio-btn" type="radio" name={questionId} value={value} ref={refRadio} onChange={onRadioBtnClick} checked={answer===value} />
            <label htmlFor={id} className="radio-question__radio-label" >
                <span className="radio-question__custom-radio-btn"></span>
                <p className="radio-question__radio-label-text">{optionLabel}</p>
            </label>
        </div>
    )
}

export default RadioOption;
