// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';
import _ from 'lodash';

import { fetchAllDefectsAction } from 'actions/pagesActions/Support/defectAction';
import CheckBox from 'components/Form/CheckBox';
import DropMenu from 'components/Form/DropMenu';
import InputField from 'components/Form/InputField';
import ListLayout from 'components/List/ListLayout';
import PreviewList from 'components/List/PreviewList/PreviewList';
import PreviewListContainer from 'components/List/PreviewList/PreviewListContainer';
import SearchFilter from 'components/List/SearchFilter';

class DefectsList extends Component {
    
    constructor(props) {
        super(props);
        this.baseState = { ...this.state };
    }

    state = {
        id: '',
        title: '',
        open: false,
        close: false
    }

    componentDidMount = async () => {
        if(!this.props.defects.length) {
            await this.props.fetchAllDefectsAction();
        }
        this.setState({ defectsList: this.props.defects });
    }

    filterDefects = () => {
        let filteredDefects = _.cloneDeep(this.props.defects);
        const {id, title, open, close} = this.state;
        if(id) {
            filteredDefects = filteredDefects.filter(defect => defect.id===Number(id));
        }
        if(title) {
            filteredDefects = filteredDefects.filter(defect => defect.title.includes(title));
        } 
        if(close && open) {
            return filteredDefects
        }
        if(open) {
            filteredDefects = filteredDefects.filter(defect => defect.status === false)
        }
        if(close) {
            filteredDefects = filteredDefects.filter(defect => defect.status === true)
        }
        return filteredDefects;
    }

    onFiltersChange = (value, fieldName) => {
        this.setState({ [fieldName]: value });
    }

    onFilterSubmit = (e) => {
        e.preventDefault();
        this.setState({ defectsList: this.filterDefects() })
    }

    onFilterReset = (e) => {
        e.preventDefault();
        this.setState({ ...this.baseState }, () => {
            this.setState({ defectsList: this.filterDefects() })
        })
    }

    render() {
        return (
            <ListLayout>
                <PreviewListContainer>
                    <PreviewList 
                        list={this.state.defectsList ? this.state.defectsList : []}
                        className="filter"
                        tableClassName="fixed-big-height"
                        listHeader=" "
                        emptyMessage="   "
                        title="title"
                        subTitle="text"
                    />
                </PreviewListContainer>
                <SearchFilter onSubmit={this.onFilterSubmit} >
                    <div className="from-group">
                        <InputField
                            id="id"
                            name="id"
                            initial={true}
                            initialValue={this.state.id}
                            onChangeHandler={(e) => this.onFiltersChange(e.target.value, 'id')}
                            colSize="1"
                            placeholder="  "
                            icon="fas fa-search"
                        />
                    </div>
                    <div className="from-group">
                        <InputField
                            id="title"
                            name="title"
                            initial={true}
                            initialValue={this.state.title}
                            onChangeHandler={(e) => this.onFiltersChange(e.target.value, 'title')}
                            colSize="1"
                            placeholder="  "
                            icon="fas fa-search"
                        />
                    </div>
                    <div className="form-group">
                        <DropMenu 
                                colSize="1"
                                title=" "
                        >
                            <div className="margin-top">
                                <CheckBox 
                                    id="open"
                                    name="open"
                                    label=""
                                    initial={true}
                                    initialValue={this.state.open}
                                    onChange={(isChecked) => {this.onFiltersChange(isChecked, 'open')}}
                                />
                            </div>
                            <div className="margin-top">
                                <CheckBox 
                                    id="close"
                                    name="close"
                                    label=""
                                    initial={true}
                                    initialValue={this.state.close}
                                    onChange={(isChecked) => {this.onFiltersChange(isChecked, 'close')}}
                                />
                            </div>
                        </DropMenu>
                    </div>
                    <div className="form-group confirm-group margin-top-big">
                        <input className="btn submit-btn" type="submit" value="" />
                        <input className="btn cancel-btn" type="reset" value=" " onClick={this.onFilterReset} />
                    </div>
                </SearchFilter>
            </ListLayout>
        )
    }
}

const mapStateToProps = state => {
    return {
        defects: Object.values(state.defects)
    }
}


export default connect(mapStateToProps, {
    fetchAllDefectsAction
})(DefectsList);