import React from 'react'
import { Link } from 'react-router-dom';

import ReactPaginate from 'react-paginate';

// giving the data default value of [], becasue if data empty display dummy rows
const PaginatedList = ({ children, totalPages, data=[], colHeaders, onPageChange, initialPage }) => {

    // Change this, will change the table rows displayed
    const ROWS_PER_PAGE = 10;

    const getHeader = () => {
        // contain the logic that return our header component

        var keys = getLabelHeaders();
        return keys.map((key) => {
            return <th className="list-table__col-header" key={key}>{key.toUpperCase()}</th>
        });
    }

    const getLabelHeaders = () => {
        return colHeaders.map((col) => col.label ).reverse();
    }

    const getKeys = () => {
        // return keys of colHeaders

        return colHeaders.map((col) => col.key ).reverse();
    }

    const getRowsData = () => {
        // contain logic to iterate through the rows data and build it
        let rowsArray = [];

        let items = data;
        let keys = getKeys();

        rowsArray = items.map((row, index) => {
            return (
                <tr className={`list-table__row-item ${row.highlightClassName}`} key={index} > 
                    <RenderRow data={row} keys={keys}/> 
                </tr>
            );
        })

        // Displaying dummy rows, if the data rows less than ROWS_PER_PAGE
        // also, if there is no data, show the rows, with message: '   '
        if(rowsArray.length < ROWS_PER_PAGE) {
            
            let dummyRows = [];
            for (let index = 0; index < (ROWS_PER_PAGE - rowsArray.length); index++) {
                let noDataRowsMessage = '';
                if(rowsArray.length === 0 && index === (ROWS_PER_PAGE / 2) - 1) {
                    noDataRowsMessage = '   '
                }
                dummyRows.push(
                    <tr className="list-table__row-item" key={index+rowsArray.length}>
                        <td style={{textAlign: 'center', padding: '0'}} colSpan={colHeaders.length} className="list-table__col-item">{noDataRowsMessage}</td>
                    </tr>
                )
            }
            rowsArray = [...rowsArray, ...dummyRows ];
        }

        return rowsArray;
    }




    return (
        <div className="list">
            <div className="list-table-page-container">
                <table className="list-table">
                    <thead>
                        <tr className="list-table__row-header" >
                            { data ? getHeader() : null }
                        </tr>
                    </thead>
                    <tbody>
                        { data ? getRowsData() : null }
                    </tbody>
                    
                </table>
                <ReactPaginate
                    pageCount={totalPages ? totalPages : 0}
                    pageRangeDisplayed={2}
                    marginPagesDisplayed={2}
                    forcePage={initialPage}
                    initialPage={initialPage}
                    previousLabel=""
                    nextLabel=""
                    containerClassName="pagination-container"
                    pageClassName="pagination__page"
                    pageLinkClassName="pagination__page--link"
                    activeClassName="pagination__active"
                    activeLinkClassName="pagination__active--link"
                    previousClassName="pagination__previous"
                    previousLinkClassName="pagination__previous--link"
                    nextClassName="pagination__next"
                    nextLinkClassName="pagination__next--link"
                    disabledClassName="pagination__disabled"
                    onPageChange={onPageChange}
                />
                {children}
            </div>
        </div>
    )
}

export default PaginatedList;


const RenderRow = (props) => {
    // This component responsible to render each row 
    if(props.data) {
        return props.keys.map((key) => {
            if(key === 'path') {
                return <td className="list-table__col-item--btn" key={props.data[key]+key}><Link className="btn submit-btn" to={props.data[key]} ></Link></td>
            } else if(key === 'statusLabel') {
                let fontClassName = '';
                if(props.data[key] === '')
                    fontClassName = 'color-primary';
                else if(props.data[key] === '')
                    fontClassName = 'color-danger';
                return <td className={`list-table__col-item ${fontClassName}`} key={props.data[key]+key}>{props.data[key] }</td>
            }
            else {
                return <td className="list-table__col-item" key={props.data[key]+key}>{props.data[key] }</td>
            }
        })
    } else {
        return null;
    }
}