// @ts-nocheck
import React, { Component } from 'react';
import { connect } from 'react-redux';

import ListLayout from '../../../components/List/ListLayout';
import PaginatedList from '../../../components/List/PaginatedList';
import SearchFilter from '../../../components/List/SearchFilter';
import InputField from '../../../components/Form/InputField';
import alertActions from '../../../actions/utilsActions/alertActions';
import { clearTableListAction } from '../../../actions/utilsActions/clearTableListAction';
import { fetchEducationalSupervisorAccountsAction } from 'actions/pagesActions/SystemAdministrator/supervisorActions';
import _ from 'lodash';
import { getLabelObject } from 'helpers/objectsHelpers';
import CustomDropdown from 'components/Form/CustomDropdown';

const COLUMN_COURSE_HEADERS = [
    {
        label: '',
        key: 'id'
    },
    {
        label: ' ',
        key: 'name'
    },
    {
        label: '',
        key: 'courseLabel'
    },
    {
        label: '',
        key: 'cityLabel'
    },
    {
        label: '',
        key: 'areaLabel'
    },
    {
        label: '',
        key: 'path'
    }
]

class EducationalSupervisorList extends Component {

    constructor(props) {
        super(props);
        this.courseDropdownRef = React.createRef();
        this.cityDropdownRef = React.createRef();
        this.areaDropdownRef = React.createRef();

        this.state = {
            page: 1,
            id: '',
            courseId: 'all',
            cityId: 'all',
            areaId: 'all',
            areas: [],
            areaErrorMessage: ''
        }
        this.baseState = { ...this.state };
    }

    componentDidMount() {
        if(this.courseDropdownRef.current && this.cityDropdownRef.current) {
            // set the labs dropmenu with ALL option at mount
            this.courseDropdownRef.current.selectItem({label: ' ', value: 'all'})
            this.cityDropdownRef.current.selectItem({label: ' ', value: 'all'})
        }
    }

    translateIdToLabel = () => {
        if(this.props.accounts && this.props.courses.length && this.props.cities.length) {
            const clonedAccounts = _.cloneDeep(this.props.accounts);
            return clonedAccounts.map(account => {
                const city = getLabelObject(account.cityId, this.props.cities);
                return {
                    ...account,
                    cityLabel: city.label,
                    courseLabel: getLabelObject(account.courseId, this.props.courses).label,
                    areaLabel: getLabelObject(account.areaId, city.areas).label
                }
            })
        }

        return [];
    }

    validateAreaFilter = () => {
        if(this.state.cityId !== 'all' && this.state.areaId === 'all') {
            return '      *'
        }
        return ''
    }

    makeSearchCall = () => {
        if(!this.state.areaErrorMessage) {
            this.props.fetchEducationalSupervisorAccountsAction({
                id: this.state.id,
                courseId: this.state.courseId,
                areaId: this.state.areaId
            });
        }
    }

    onFiltersChange = (value, fieldName) => {
        switch (fieldName) {
            case 'cityId':
                this.setState( {  
                    cityId: value, 
                    areaId: 'all', 
                    areas: value === 'all' ? [] : getLabelObject(value, this.props.cities).areas 
                }, () => {
                    // after choosing city, clearSelection of area dropdown
                    if(this.areaDropdownRef.current) {
                        this.areaDropdownRef.current.clearSelection();
                    }
                });
                break;
            default:
                this.setState({ [fieldName]: value });
                break;
        }
    }

    onPageChange = (currentPage) => {
        // clear alerts, becasue when item deleted, it will be send here
        this.props.clearAlert();
        
        // set the currentPage, and make API call after that
        this.setState({ 
            page: currentPage.selected + 1,
            areaErrorMessage: this.validateAreaFilter()
        }, () => {
            // This will make the call when the component mount first
            this.makeSearchCall();
        });
    }

    onFilterSubmit = (e) => {
        this.props.clearAlert();
        // after submit, set the filter submit params, and after that make API request
        e.preventDefault();
        this.setState({ 
            page: 1,
            areaErrorMessage: this.validateAreaFilter()
        }, () => {
            this.makeSearchCall();
        })
    }

    onFilterReset = () => {
        this.setState({ ...this.baseState }, () => {
            this.courseDropdownRef.current.selectItem({label: ' ', value: 'all'})
            this.cityDropdownRef.current.selectItem({label: ' ', value: 'all'})
            // this.areaDropdownRef.current.clearSelection();
        });
    }

    renderAreaDropdown = () => {
        if(this.state.cityId !== 'all'){
            return (
                <CustomDropdown 
                    title=" "
                    list={this.state.areas}
                    onChange={(areaId) => this.onFiltersChange(areaId, 'areaId')}
                    colSize="1"
                    setRef={(ref) => this.areaDropdownRef = ref}
                />
            );
        }
    }

    render() {
        return (
            <ListLayout>
                <PaginatedList 
                    totalPages={this.props.totalPages}
                    data={ this.translateIdToLabel() }
                    onPageChange={this.onPageChange}
                    colHeaders = {COLUMN_COURSE_HEADERS}
                    initialPage={this.state.page - 1}
                />
                <SearchFilter onSubmit={this.onFilterSubmit} >
                    <div className="form-group">
                        <InputField
                            id="id"
                            name="id"
                            initial={true}
                            initialValue={this.state.id}
                            onChangeHandler={(e) => this.onFiltersChange(e.target.value, 'id')}
                            placeholder="  "
                            icon="fas fa-search"
                            colSize="1"
                        />
                    </div>
                    <div className="form-group">
                        <CustomDropdown 
                            title=" "
                            list={[{ label: ' ', value: 'all'}, ...this.props.courses]}
                            onChange={(courseId) => this.onFiltersChange(courseId, 'courseId')}
                            colSize="1"
                            setRef={(ref) => this.courseDropdownRef = ref}
                        />
                    </div>
                    <div className="form-group">
                        <CustomDropdown 
                            title=""
                            list={[{ label: ' ', value: 'all'}, ...this.props.cities]}
                            onChange={(cityId) => this.onFiltersChange(cityId, 'cityId')}
                            colSize="1"
                            setRef={(ref) => this.cityDropdownRef = ref}
                        />
                    </div>
                    {this.state.areaErrorMessage && <p className="color-danger margin-top filter-error-message"> {this.state.areaErrorMessage} </p>}
                    <div className="form-group">
                        { this.renderAreaDropdown() }
                    </div>
                    <div className="form-group confirm-group margin-top-big">
                        <input className="btn submit-btn" type="submit" value="" />
                        <input className="btn cancel-btn" type="reset" value=" " onClick={ this.onFilterReset }/>
                    </div>
                </SearchFilter>
            </ListLayout>
        )
    }
    componentWillUnmount() {
        // clear the table list in redux store when unmount
        this.props.clearTableListAction();
    }
}

const mapStateToProps = state => {
    return {
        accounts: state.tableList.educationalSupervisors,
        totalPages: state.tableList.totalPages,
        courses: Object.values(state.courses),
        cities: Object.values(state.cities)
    }
}

export default connect(mapStateToProps, { 
    clearTableListAction, 
    clearAlert: alertActions.clear,
    fetchEducationalSupervisorAccountsAction
})(EducationalSupervisorList);
