import alertActions from '../../utilsActions/alertActions';
import loadingActions from '../../utilsActions/loadingActions';
import mylabs from '../../../apis/mylabs';
import catchHandler from '../../../helpers/catchHandler';
import parseDateToString from '../../../helpers/parseDate';
import history from '../../../history';

export const createSemesterAction = (semester) => {
    return async (dispatch) => {
        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            // Calling the Server API to patch
            const response = await mylabs.post('/semester', { 
                'title': semester.title,
                'startDate': parseDateToString(semester.startDate),
                'endDate': parseDateToString(semester.endDate)
             });

             // message to the user, will be displayed in the ServerAlert component
             dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true,
                path: `/semester/${response.data.id}`
            }));

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }

    }
}

export const fetchSemestersAction = (filterParams) => {
    return async (dispatch) => {
        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());

            // fetch the semesters records from the API server 
            const response = await mylabs.get('/semester', {
                params: filterParams
            });
            
            // Send to Reducers
            dispatch( { type: 'FETCH_SEMESTERS', payload: response.data });
            
            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch(err) {
            catchHandler(dispatch, err);
        }
    }
}

export const fetchSemesterAction = (semesterId) => {
    return async (dispatch) => {
        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());

            // fetch the semester from the API server by its ID 
            const response = await mylabs.get(`/semester/${semesterId}`);
            
            // Send to Reducers
            dispatch( { type: 'FETCH_SEMESTER', payload: response.data.semester });
            
            // remove the loading indicator
            dispatch(loadingActions.remove());
        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}

export const updateSemesterAction = (semester) => {
    return async (dispatch) => {
        try {
            // CREATE ACTION FOR isLoading
            dispatch(loadingActions.show());
            // fetch the semester from the API server by its ID 
            const response = await mylabs.patch(`/semester`, {
                ...semester,
                startDate: parseDateToString(semester.startDate),
                endDate: parseDateToString(semester.endDate)
            });

             // message to the user, will be displayed in the ServerAlert component
             dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true
            }));

            dispatch( { type: 'FETCH_SEMESTER', payload: response.data.semester });

            // remove the loading indicator
            dispatch(loadingActions.remove());

        } catch (err) {
            catchHandler(dispatch, err);
        }
    }
}


export const deleteSemesterAction = (semesterId) => {
    // DELETE item
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make DELETE request to the API server
            await mylabs.delete(`/semester/${semesterId}`);

            // navigate user to the items list screen
            history.push('/semester');

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "    ",
                code: 200,
                success: true
            }));

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }

    }
}