// @ts-nocheck
import React from 'react';
import { connect } from 'react-redux';
import { Route } from 'react-router-dom';

import NotAllowed from '../../../../components/Utils/NotAllowed';
import LabInfo from './LaboratoryView';
import CurrentItemsList from './CurrentItemsList';
import RemovedItemsList from './RemovedItemsList';
import Tab, { TabElement } from 'components/Utils/Tab';

const LaboratoryTab = ({ match, hasSchool }) => {
    return (
        <NotAllowed
            hasSchool={hasSchool}
            referer="/invites"
            statusCode="403"
            message="       "
            btnLabel="   "
        >
            <Tab>
                <TabElement 
                    to={`/school-laboratory/${match.params.labId}`}
                    match={match}
                    title=" "
                />
                <TabElement 
                    to={`/school-laboratory/${match.params.labId}/current-items`}
                    match={match}
                    title=" "
                />
                <TabElement 
                    to={`/school-laboratory/${match.params.labId}/removed-items`}
                    match={match}
                    title=" "
                />
            </Tab>
            <Route path={`/school-laboratory/:labId`} exact component={LabInfo} />
            <Route path={`/school-laboratory/:labId/current-items`} exact component={CurrentItemsList} />
            <Route path={`/school-laboratory/:labId/removed-items`} exact component={RemovedItemsList} />
        </NotAllowed>
    );
}

const mapStateToProps = (state) => {
    
    return {
        hasSchool: state.auth.user.data.hasSchool,
    }
}

export default connect(mapStateToProps)(LaboratoryTab);