import alertActions from '../../utilsActions/alertActions';
import loadingActions from '../../utilsActions/loadingActions';
import mylabs from '../../../apis/mylabs';
import catchHandler from '../../../helpers/catchHandler';
import history from '../../../history';


export const createItemAction = (item) => {
    return async (dispatch) => {

        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make POST request to the API server
            const response = await mylabs.post('/item', item);

            // removing loading spinner after success
            dispatch(loadingActions.remove());

            // updating the items form list
            dispatch({ type: 'CREATE_FORM_ITEM', payload: { 
                label: `${item.name}-${item.unit}`, 
                value: response.data.id 
            }});

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "   ",
                code: 200,
                success: true,
                path: `/item/${response.data.id}`
            }));

            
        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }

    }
}

export const fetchItemsAction = (filterParams) => {
    // FETCH list of items
    return async (dispatch) => {
        try {

            // show loading spinner
            dispatch(loadingActions.show());

            // make POST request to the API server
            const response = await mylabs.get('/item', {
                params: filterParams
            });

            // Send to Reducers
            dispatch( { type: 'FETCH_ITEMS', payload: response.data });

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }

    }
}

export const fetchItemAction = (itemId) => {
    // FETCH single item by ID
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make GET request to the API server
            const response = await mylabs.get(`/item/${itemId}`);

            // Send to Reducers
            dispatch( { type: 'FETCH_ITEM', payload: response.data.item });

            dispatch( {type: 'FETCH_UP_TO_DATE_FORM_ITEM', payload: {
                label: response.data.item.name,
                value: response.data.item.id
            }});

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }

    }
}

export const updateItemAction = (item) => {
        // PATCH item
        return async (dispatch) => {
            try {
                // show loading spinner
                dispatch(loadingActions.show());
    
                // make PATCH request to the API server
                const response = await mylabs.patch(`/item`, { ...item });

                // message to the user, will be displayed in the ServerAlert component
                dispatch(alertActions.success({
                    message: "   ",
                    code: 200,
                    success: true
                }));

                const itemRes = response.data.item;

                // updating the form item
                dispatch({ type: 'FETCH_ITEM', payload: itemRes })

                dispatch({ type: 'PATCH_FORM_ITEM', payload: {
                    label: itemRes.label,
                    value: itemRes.value
                }})

                // removing loading spinner after success
                dispatch(loadingActions.remove());
    
            } catch(err) {
                // reusable catch handler
                catchHandler(dispatch, err);
            }
        }
}

export const deleteItemAction = (itemId) => {
    // DELETE item
    return async (dispatch) => {
        try {
            // show loading spinner
            dispatch(loadingActions.show());

            // make PATCH request to the API server
            await mylabs.delete(`/item/${itemId}`);

            // navigate user to the items list screen
            history.push('/item');

            // message to the user, will be displayed in the ServerAlert component
            dispatch(alertActions.success({
                message: "   ",
                code: 200,
                success: true
            }));

            // deleting the form item
            dispatch({ type: 'DELETE_FORM_ITEM', payload: itemId })

            // removing loading spinner after success
            dispatch(loadingActions.remove());

        } catch(err) {
            // reusable catch handler
            catchHandler(dispatch, err);
        }

    }
}