// @ts-nocheck
import React, { Component } from 'react'
import { connect } from 'react-redux';

import CustomMultipleDropdown from '../../../components/Form/CustomMultipleDropdown';
import Form from '../../../components/Form/Form';
import FormHeader from '../../../components/Form/FormHeader';
import InputField from '../../../components/Form/InputField';
import Alert from '../../../components/Utils/Alert';
import ContentPadding from '../../../components/Utils/ContentPadding';
import { validateEmpty } from '../../../helpers/validate';
import ContentDivider from '../../../components/Utils/ContentDivider';
import NotAllowed from '../../../components/Utils/NotAllowed';
import { createLaboratoryAction } from '../../../actions/pagesActions/SchoolManager/laboratoryAction';
import { fetchLabManagersAction } from '../../../actions/pagesActions/SchoolManager/membersAction';

class LabCreate extends Component {

    constructor(props) {
        super(props);

        this.dropdownRef = React.createRef();
        this.state = {
            labId: '',
            labManagers: [],
            errorMessages: []
        }
        this.baseState = { ...this.state };
    }

    async componentDidMount() {
        // initial call to get the lab managers

        if(!this.props.labManagers.length && this.props.hasSchool) {
            await this.props.fetchLabManagersAction();
        }
    }

    validateLabData = () => {
        return [
            validateEmpty(this.state.labId, '       ')
        ].filter(message => message)
    }

    createLab = async () => {
        if(this.state.errorMessages.length === 0) {
            await this.props.createLaboratoryAction({
                labId: this.state.labId,
                labManagers: this.state.labManagers
            })
            if(this.props.status) {
                // after success api call, reset fields
                this.onResetHandler();
            }
        }
    }

    onFieldChange = (value, fieldName) => {
        this.setState({ [fieldName]: value })
    }

    onFormSubmit = (e) => {
        e.preventDefault();

        // performing validation
        this.setState({
            errorMessages: this.validateLabData()
        }, () => {
            this.createLab();
        });
    }

    onResetHandler = () => {
        // reset form values and deselect choosen options
        this.setState({ ...this.baseState });
        this.dropdownRef.current.deselectAll();
    }

    render() {
        return (
            <NotAllowed 
                hasSchool={this.props.hasSchool}
                referer="/"
                message="     "
                statusCode="403"
                btnLabel="  "
            >
                <Form onSubmit={this.onFormSubmit}>
                    <FormHeader label=" " />
                    <ContentPadding>
                        <Alert messages={this.state.errorMessages} type="error" />
                        <div className="form-group">
                            <InputField
                                id="id"
                                name="id"
                                type="text"
                                onChangeHandler={(e) => this.onFieldChange(e.target.value, 'labId')}
                                initial={true}
                                initialValue={this.state.labId}
                                label=" "
                                placeholder=": 10"
                                icon="fas fa-signature"
                                colSize="2"
                            />
                            <CustomMultipleDropdown 
                                onChange={(selectedManagers) => this.onFieldChange([...selectedManagers], 'labManagers')}
                                list={this.props.labManagers}
                                label="(    )  "
                                title='  '
                                selectedTitle=" "
                                setRef={(ref) => this.dropdownRef = ref}
                                colSize="2"
                            />
                        </div>
                        <ContentDivider />
                        <div className="form-group confirm-group margin-top-big">
                            <input className="btn submit-btn" type="submit" value=""/>
                            <input className="btn cancel-btn" type="reset" value=" " onClick={this.onResetHandler}/>
                        </div>
                    </ContentPadding>
                </Form>
            </NotAllowed>
        )
    }
}

const mapStateToProps = state => {
    return {
        hasSchool: state.auth.user.data.hasSchool,
        labManagers: Object.values(state.laboratoryManagers)
                        .map(labManager => { return { label: labManager.name, value: labManager.id } }),
        status: state.alert.success
    }
}

export default connect(mapStateToProps, { 
    createLaboratoryAction,
    fetchLabManagersAction
})(LabCreate);
