import { combineReducers } from 'redux';

import authReducer from './authReducer';
import alertReducer from './Utils/alertReducer';
import registerReducer from './registerReducer';
import refererReducer from './Utils/refererReducer';
import navToggleReducer from './Utils/navToggleReducer';
import loadingReducer from './Utils/loadingReducer';
import semesterReducer from './tableListReducer';
import filterToggleReducer from './Utils/filterToggleReducer';
import itemViewReducer from './itemViewReducer';
import formRequirmentsReducer from './formRequirementsReducer';
import itemsReducer from './General/itemsReducer';
import coursesReducer from './General/coursesReducer';
import citiesReducer from './General/citiesReducer';
import schoolReducer from './SchoolManagerReducers/schoolReducer';
import invitationReducer from './General/invitationReducer';
import laboratoriesReducer from './SchoolManagerReducers/laboratoriesReducer';
import laboratoryManagersReducer from './SchoolManagerReducers/laboratoryManagersReducer';
import scienceTeachersReducer from './SchoolManagerReducers/scienceTeachersReducer';
import labItemsReducer from './LaboratoryManagerReducers/labItemsReducer';
import experimentsReducer from './General/experimentsReducer';
import evaluateQuestionsReducer from './ScienceTeacher/evaluateQuestionsReducer';
import supervisorAccountsReducer from './SystemAdministrator/supervisorAccountsReducer';
import visitReducer from './EducationalSupervisor/visitReducer';
import dashboardReducer from './General/dashboardReducer';
import laboratoryQuestionsReducer from './EducationalSupervisor/laboratoryQuestionsReducer';
import laboratoryManagerQuestionsReducer from './EducationalSupervisor/laboratoryManagerQuestionsReducer';
import allAdminQuestionReducer from './SystemAdministrator/allAdminQuestionReducer';
import defectsReducer from './defectsReducer';
import profileReducer from './General/profileReducer';

export default combineReducers({
    auth: authReducer,
    alert: alertReducer,
    register: registerReducer,
    referer: refererReducer,
    navToggle: navToggleReducer,
    isLoading: loadingReducer,
    profile: profileReducer,
    tableList: semesterReducer,
    itemView: itemViewReducer,
    filterToggle: filterToggleReducer,
    formRequirements: formRequirmentsReducer,
    items: itemsReducer,
    courses: coursesReducer,
    cities: citiesReducer,
    school: schoolReducer,
    invites: invitationReducer,
    laboratories: laboratoriesReducer,
    laboratoryManagers: laboratoryManagersReducer,
    labItems: labItemsReducer,
    scienceTeachers: scienceTeachersReducer,
    experiments: experimentsReducer,
    scienceTeacherQuestions: evaluateQuestionsReducer,
    educationalSupervisorsAccounts: supervisorAccountsReducer,
    educationalSupervisorCurrentVisit: visitReducer,
    dashboard: dashboardReducer,
    laboratoryQuestions: laboratoryQuestionsReducer,
    laboratoryManagerQuestions: laboratoryManagerQuestionsReducer,
    adminQuestions: allAdminQuestionReducer,
    defects: defectsReducer
});
