import { userConstants } from '../../actions/types';

const laboratoryManagerQuestionsReducer = (state={}, action) => {
    switch(action.type) {
        case 'FETCH_LABORATORY_MANAGER_EVALUATE_QUESTIONS':
            return { ...action.payload };
        case userConstants.LOGOUT:
            return {};
        default:
            return state;
    }
}

export default laboratoryManagerQuestionsReducer;